<template>
    <div id="side-slipping">
        <i class="fa fa-bars" @click="showInfo"></i>
        <header class="info" v-show="isShow" :class="{show:isShow}">
            <img class="head-bg" src="./../assets/img/headBg.jpg" alt="head-bg"/>
            <div class="head-img"></div>
            <span>{{$store.state.motto}}</span>
            <div class="head-name">{{$store.state.owner}}</div>
            <div class="link">
                <el-tooltip class="item" effect="dark" content="https://github.com/xll032" placement="bottom-start">
                    <a href="https://github.com/xll032" target="_blank"><span class="fa fa-github"></span></a>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="https://weibo.com" placement="bottom">
                    <span class="fa fa-weibo"></span>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="https://weixin.com" placement="bottom">
                    <span class="fa fa-wechat"></span>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="https://qq.com" placement="bottom-end">
                    <span class="fa fa-qq"></span>
                </el-tooltip>
            </div>
            <div class="type">
                <header><span class="fa fa-tags"></span> 技术分类</header>
                <el-tag v-for="tag in $store.state.tags" :type="tag.type" :key="tag.name" @click.native="searchForType(tag.name)">{{tag.name}}</el-tag>
            </div>
        </header>
        <section class="mask" :class="{show:isShow}" @click="showInfo"></section>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                isShow:false
            }
        },
        methods: {
            showInfo(){
                const that = this;
                that.isShow = !that.isShow;
                if(that.isShow){
                    document.getElementById('my-blog').style.right = '82%';
                }else{
                    document.getElementById('my-blog').style.right = '0';
                }

            }
        }
    }
</script>
<style>
    #side-slipping .fa-bars{
        line-height:50px;
        position:absolute;
        color:#fff;
        right:20px;
        display:none;
        font-size:22px;
    }
    #side-slipping .mask{
        position:fixed;
        top:0;
        right:0;
        width:100%;
        height:100%;
        background:rgba(0,0,0,0.4);
        display:none;
    }
    #side-slipping .mask.show{
        display:block;
    }
    #side-slipping header.info{
        width:82%;
        position: absolute;
        z-index:100;
        top:0;
        right:0px;
        background:#fff;
        overflow: hidden;
    }
    #side-slipping header.info.show{
        right:-82%;
    }
    #side-slipping header.info .head-bg{
        height:130px;
        background: #4783aa;
        position: absolute;
        z-index:0;
    }
    #side-slipping .round-img{
        cursor:pointer;
        width:100%;
        height:100%;
    }
    #side-slipping .round-title{
        cursor:pointer;
        position:absolute;
        margin:0 auto;
        left:0;
        right:0;
        text-align:center;
        color:#eff2f7;
        bottom:30px;
        text-shadow: 1px 1px 1px #333;
    }
    #side-slipping .round-title：hover{
        text-decoration:underline;
    }
    #side-slipping header.info .head-img{
        width:90px;
        height:90px;
        margin:80px auto 20px auto;
        -webkit-border-radius:50%;
        -moz-border-radius:50%;
        border-radius:50%;
        background:url('/static/img/head.png') no-repeat;
        background-size:100% 100%;
        position: relative;
        z-index:1;
        -webkit-box-shadow:0 0 6px 0 #999;
        -moz-box-shadow: 0 0 6px 0 #999;
        box-shadow: 0 0 6px 0 #999;
    }
    #side-slipping header.info .head-name{
        text-align:center;
    }
    #side-slipping .link{
        text-align:center;
    }
    #side-slipping .link .fa{
        cursor: pointer;
        padding:6px;
        color:#D3DCE6;
        font-size:24px;
        -webkit-transition: color 0.3s ease-in;
        -moz-transition: color 0.3s ease-in;
        -ms-transition: color 0.3s ease-in;
        -o-transition: color 0.3s ease-in;
        transition: color 0.3s ease-in;
    }
    @media screen and ( max-width: 768px ){
        #side-slipping .fa-bars{
            display:block;
        }
    }
</style>


