<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">问题的发生</p>
            <p>
                之前封装了一个元素进入可视区（或拉到底部）加载内容（进入可视区，自定义回调）的 <span class="tip">jQuery</span> 插件。
            </p>
            <p>
                结果有个同事表示获取出来的值不对，没有到底部就触发了函数，并且会一直触发。但是在别的页面上都是正确的。
            </p>
            <p class="minititle">寻找原因</p>
            <p>
                判断高度的条件是这样的：
            </p>
            <p>
                <span class="tip">$('#obj').offset().top + $('#obj').height() &lt; $(window).height() + $(document).scrollTop()</span>
            </p>
            <p>
                正常来讲，这句判断不会出问题。而在他的页面上始终返回 <span class="tip">true</span>。进一步测试，发现问题就出在了 <span class="tip">$(window).height()</span> 上。
            </p>
            <p>
                在这个页面上得到的 <span class="tip">$(window).height()</span> 的值始终等于 <span class="tip">$(document).height()</span> 的值。
            </p>
            <p class="minititle">问题的解决</p>
            <p>
                既然插件在别的页面是好的，也证明了这段代码只是在这个页面上出的问题，也并非浏览器的问题。最后就把问题锁定在了 <span class="tip">HTML</span> 页面上。
            </p>
            <p>
                果不其然，代码中没有写 <span class="tip">&lt;!DOCTYPE html&gt;</span>。这句话是申明页面为 <span class="tip">H5</span>，如果不加这句话，浏览器就不知道这个页面的文档类型。导致出现上面的错误。
            </p>
            <p>
                所以平时还是要细心一点。如果同样出现了这样的问题，不妨看看是不是这个地方出了问题。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


