<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、使用简介</p>
            <p>
                <span class="code">document.execCommand('param')</span>
            </p>
            <pre class="code"><span class="com">// 相当于单击文件中的打开按钮</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">''</span><span class="word">);</span><br><br><span class="com">// 将当前页面另存为</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'SaveAs'</span><span class="word">);</span><br><br><span class="com">// 剪贴选中的文字到剪贴板</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'Cut'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="key">null</span><span class="word">);</span><br><br><span class="com">// 删除选中的文字</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'Delete'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="key">null</span><span class="word">);</span><br><br><span class="com">// 改变选中区域的字体</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'FontSize'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="param">sFontName</span><span class="word">);</span><br><br><span class="com">// 改变选中区域的字体大小</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'FontSize'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="param">sSize</span><span class="key">|</span><span class="param">iSize</span><span class="word">);</span><br><br><span class="com">// 设置前景颜色</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'ForeColor'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="param">sColor</span><span class="word">);</span><br><br><span class="com">// 使绝对定位的对象可直接拖动</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'2D-Position'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="str">'true'</span><span class="word">);</span><br><br><span class="com">// 使对象定位变成绝对定位</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'AbsolutePosition'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="str">'true'</span><span class="word">);</span><br><br><span class="com">// 设置前景颜色</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'BackColor'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="param">sColor</span><span class="word">);</span><br><br><span class="com">// 使选中区域的文字加粗</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'Bold'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="key">null</span><span class="word">);</span><br><br><span class="com">// 复制选中的文字到剪贴板</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'Copy'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="key">null</span><span class="word">);</span><br><br><span class="com">// 设置指定锚点为书签</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'CreateBookmark'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="param">sAnchorName</span><span class="word">);</span><br><br><span class="com">// 将选中文本变成超连接,若第二个参数为true,会出现参数设置对话框</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'CreateLink'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="param">sLinkURL</span><span class="word">);</span><br><br><span class="com">// 设置当前块的标签名</span><br><span class="spe">document</span><span class="word">.</span><span class="method">execCommand</span><span class="word">(</span><span class="str">'FormatBlock'</span><span class="word">,</span><span class="str">'false'</span><span class="word">,</span><span class="param">sTagName</span><span class="word">);</span></pre>
            <p class="minititle">二、可供使用的参数</p>
            <p>
                <span class="tip">FireFox</span> 对其支持不是很好。
            </p>
            <el-table :data="table_90" stripe>
                <el-table-column prop="name" label="属性"></el-table-column>
                <el-table-column prop="intro" label="说明"></el-table-column>
            </el-table>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


