<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                直接上代码：
            </p>
            <pre class="code"><span class="spe">onLoad</span><span class="word">:</span><span class="key">function</span><span class="word">(options)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 页面初始化函数，options 中为页面跳转带来的参数</span><br><span class="flower">}</span><span class="word">,</span><br><span class="spe">onReady</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 页面渲染完成函数</span><br><span class="flower">}</span><span class="word">,</span><br><span class="spe">onShow</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 页面显示函数</span><br><span class="flower">}</span><span class="word">,</span><br><span class="spe">onHide</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 页面隐藏函数</span><br><span class="flower">}</span><span class="word">,</span><br><span class="spe">onUnload</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 页面关闭函数</span><br><span class="flower">}</span><br></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


