<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">安装 Visual SVN Tortoise SVN</p>
            <p>
                地址：<a target="_blank" href="https://www.visualsvn.com/visualsvn/download/tortoisesvn/">https://www.visualsvn.com/visualsvn/download/tortoisesvn/</a>
            </p>
            <p class="minititle">初次使用</p>
            <p>
                <span class="tip">Visual SVN</span> 新建项目（<span class="tip">repositories</span>） 创建好以后边上会有一个地址
            </p>
            <p>
                本地建一个目录，导入这个地址。然后就可以提交了。  <span class="tip">Visual SVN</span> 可以创建 <span class="tip">Users</span> 并给他们用 <span class="tip">Groups</span> 分组。
            </p>
            <p>
                完成这些以后就可以提交了，每次提交版本号会加 1。
            </p>
            <p class="minititle">trunk branch tag 使用</p>
            <p class="minititle">简要介绍：</p>
            <p>
                trunk（主干|主线） tags（标记） branches（分支）
            </p>
            <p>
                trunk：用来做主方向开发，新功能开发应放在主线中，当模板开发完成后，需要修改，就用 branch
            </p>
            <p>
                branches：分支开发和主线开发是可以同时进行的，也就是并行开发，分支通常用于修复 bug 使用
            </p>
            <p>
                tag：用于标记某个可用版本，可以标记已经上线发布的版本，也可以标记正在测试的版本，通常是只读的。
            </p>
            <p class="minititle">使用的流程：</p>
            <p>
                1、新功能开发，直接在 <span class="tip">trunk</span> 上写；
            </p>
            <p>
                2、修复 <span class="tip">bug</span> 创建 分支/标记
            </p>
            <p>
                3、标记是用于记录当时的版本库内容，分支用于写 <span class="tip">bug</span> 修复的代码；
            </p>
            <p>
                4、分支代码修复好以后，再合并到主干上去
            </p>
            <p>
                例如，某一个版本（1.0）已开发完成，而发现了此版本有一个 <span class="tip">bug</span> 急需修复，同时又有新的需求在开发。开发过程中的代码进行 <span class="tip">bug</span> 修复可能带来新的问题，所以修复 <span class="tip">bug</span> 应该在刚开发完成的版本的基础上进行 <span class="tip">fix</span>，即之前的 1.0 版本。
            </p>
            <p>
                这样有以下两个明显的好处：
            </p>
            <p>
                1、<span class="tip">bug</span> 修复后可以立即上线，不会因为新需求未完成或者测试新需求，而延迟上线时间
            </p>
            <p>
                2、<span class="tip">bug</span> 修复是在原来上线的版本进行的，引起新 <span class="tip">bug</span> 的风险小，如果在新需求基础上修复 <span class="tip">bug</span>，新功能可能引发新 <span class="tip">bug</span>
            </p>
            <p>
                至于日常使用的话，就不在此介绍了，多用用就熟悉了。现在使用 <span class="tip">git</span> 越来越多了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


