<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                该问题仅在 <span class="tip">iPhone</span> （<span class="tip">iPad</span> 应该也会）上的浏览器才会出现，在 PC 端无法检查到该问题。对于此问题解决方法也很简单。添加下列标签，就可以完美解决该问题。
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">meta</span> <span class="attr">name=</span><span class="str">"format-detection"</span> <span class="attr">content=</span><span class="str">"telephone=no"</span><span class="arrow">/&gt;</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


