<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                前言中介绍了使用 <span class="tip">webpack</span> 和普通页面之间的区别。下面就引入 <span class="tip">fa</span> 图标库的例子做下简单的引入介绍。
            </p>
            <p>
                安装就不细说了 执行 <span class="code">npm install xxx（包名）</span> 即可。
            </p>
            <p>
                <span class="tip">fa</span> 图标库直接下载引入会提示 <span class="error">dependency was not found</span>。这时候根据提示把依赖安装好就行了。
            </p>
            <p>
                <span class="tip">fa</span> 图标库的依赖是 <span class="tip">font-awesome-webpack</span> 和  <span class="tip">less</span>。
            </p>
            <p>
                引入的方式跟引入 <span class="tip">elementUI</span> 一样（前面几篇有介绍）。在需要使用的地方写上：
            </p>
            <pre class="code"><span class="key">import </span><span class="spe">fa </span><span class="key">from </span><span class="str">'font-awesome-webpack'</span><br><span class="spe">Vue</span><span class="word">.</span><span class="method">use</span><span class="word">(</span><span class="spe">fa</span><span class="word">)</span><span class="word">;</span></pre>
            <p>
                引入其他的库的方法也类似。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


