<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">1、普通的打开新窗口</p>
            <p>
               要进行普通的跳转非常容易，可以直接使用 <span class="code">a</span> 标签，也可以给标签加点击事件 <span class="code">window.open('url')</span>。
            </p>
            <p class="minititle">2、ajax 回调后的跳转</p>
            <p>
                <span class="code">ajax</span> 回调后的跳转实际上就是用 <span class="tip">js</span> 控制打开新窗口。浏览器对于打开新窗口有一个安全机制，防止请求后跳转到一个不安全的页面，所以回调后会阻止跳转，这个行为是浏览器默认的。要解除这个安全机制需要用户设置“不对 <span class="tip">js</span> 进行安全拦截”。显然用户是不会这么做的。
            </p>
            <p>
                那么，有什么方案可以解决这个问题，答案是肯定的。
            </p>
            <p>
                比如说，在回调中使用定时器，时间到了再打开新窗口，理论上讲时间设置比较合理的话，用户体验应该是不错的。而如果网速慢的话，就尴尬了。不过不必担心刚才的都是废话，因为这个操作同样会被浏览器阻止掉。（要么来个 <span class="code">while(true)</span>，好吧，<span class="tip">js</span> 是单线程的）
            </p>
            <p>
                废话说完了，现在来说说真正有用的方法。（不知道有没有其他的好方法，有的话请告知，多谢了！）
            </p>
            <p class="minititle">方法一：预先打开窗口</p>
            <p>
                做法是：将一个 <span class="code">window.open()</span> 存入一个变量，先打开一个空页面，回调后将其改为指定页面。这样可以避免回调后被阻止。比较神奇的是，这个方法居然可以控制到别的页面上的地址。先上代码：（注：这里用的是 <span class="tip">jQuery</span> 的 <span class="code">ajax</span>，用哪种方法，不影响最后的结果）
            </p>
            <pre class="code"><span class="key">var</span> <span class="globe">newLink</span> <span class="key">=</span> <span class="key">window</span><span class="word">.</span><span class="method">open</span><span class="word">(</span><span class="str">'about:blank'</span><span class="word">);</span><br><span class="spe">$</span><span class="word">.</span><span class="method">ajax</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* ... */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">success</span><span class="word">:</span><span class="key">function</span><span class="word">(data)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="globe">newLink</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">href</span> <span class="key">=</span> <span class="str">'ajax传入的url'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                这个方法有一个显而易见的缺点，在回调之前会先打开一个新的空窗口，不管网速有多快，用户势必会看到一个空窗口，然后才会展现出数据。如果网速比较慢就会更加糟糕了，比如用户直接关了这个窗口。
            </p>
            <p class="minititle">方法二：ajax 同步</p>
            <p>
                将 <span class="code">ajax</span> 请求改为同步，也可以进行正常跳转。也不会出现方法一的空白页。代码如下：
            </p>
            <pre class="code"><span class="spe">$</span><span class="word">.</span><span class="method">ajax</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* ... */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">async</span><span class="word">:</span><span class="key">false</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">success</span><span class="word">:</span><span class="key">function</span><span class="word">(data)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">window</span><span class="word">.</span><span class="method">open</span><span class="word">(</span><span class="str">'ajax传入的url'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                这个方法也存在缺点：将 <span class="code">ajax</span> 请求改为同步就失去了 <span class="code">ajax</span> 设计的初衷，同时失去了异步的优势。而如果用户网速在请求的时候不流畅，<span class="tip">js</span> 的运行就会出现问题。
            </p>
            <p>
                综上，以上两种方法各有缺点，需要根据实际情况权衡，做出抉择。
            </p>
            <p class="tip">提示：有一点需要说明一下，<span class="tip">vue</span> 推荐使用的 <span class="tip">axios</span> 不支持设置 <span class="tip">axios</span>。</p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


