<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、安装、配置 Apache</p>
            <p>
                首先在官网下载 <span class="tip">Apache</span>。下载地址：<a target="_blank" href="http://httpd.apache.org/download.cgi ">http://httpd.apache.org/download.cgi </a>。
            </p>
            <p>
                <span class="tip">Windows</span> 系统 要选择 <span class="tip">File for Microsoft Windows</span>，然后选择 <span class="tip">ApacheHaus</span>。找到 <span class="tip">Download Location</span>，两个图标选择一个。我点了第一个，第二个肯定是国外的，第一个不知道是什么。
            </p>
            <p>
                下载完成后，解压到一个目录下。如：<span class="tip">D:/b/Apache24</span>。此路径后面还会用到，对应好自己的路径，不要弄错了。
            </p>
            <p>
               接下来在 <span class="tip">cmd</span> 中进入到目录 <span class="tip">D:/b/Apache24/bin</span> 后，输入如下命令：
            </p>
            <pre class="cmd">httpd.exe -k install -n "apache24"</pre>
            <p>
                一般来说，是会报错的。不过不要担心，这是正常现象。基本上这些错误都是由于 <span class="tip">httpd.conf</span> 配置路径不对引起的。<span class="tip">cmd</span> 的提示很清楚，根据提示基本就能解决了。大致上的解决方案如下：
            </p>
            <p>
                找到 <span class="tip">Apache24/conf/httpd.conf</span>，把里面所有的 <span class="code">${SRVROOT}</span> 改成 <span class="code">D:/b/Apache24</span>（刚才的路径）。然后就可以了。
            </p>
            <p>
                这时候在浏览器中打开 <span class="tip">localhost/index</span> 应该就能看到 <span class="tip">Apache</span> 的界面。接下来进到 <span class="tip">D:/b/Apache24/htdocs</span>（这个位置在 <span class="tip">httpd.conf</span> 中也是可以改的，这里不介绍了，自己找找看吧）。刚才的 <span class="tip">index.html</span>就在里面。在这里随便建一个页面，然后在中打开试试看（文件名要写全，否则没用）。
            </p>
            <p>
                再建一个 <span class="tip">info.php</span>，在里面输入 <span class="code">&lt;?php phpinfo();</span>。打开它，会发现输出了文本内容。这是因为还没有安装和配置 <span class="tip">php</span>。这个页面可以用来测试后面的 <span class="tip">php</span> 安装配置是否成功。
            </p>
            <p>
                <span class="tip">Apache</span> 的安装和配置到此就结束了。接下来，介绍 <span class="tip">php</span> 的安装和配置。
            </p>
            <p class="tip">
                注：可以使用 可执行文件 <span class="tip">Apache24/bin/ApacheMonitor.exe</span> 来打开 <span class="tip">Apache</span>，也可以用 <span class="tip">cmd</span> 输入如下代码打开（如果出错，可以看到错误信息）：
            </p>
            <pre class="cmd">httpd -k start</pre>
            <p class="minititle">二、安装、配置php</p>
            <p>
                <span class="tip">php</span> 官方下载地址（需要什么版本自己选择吧）：<a target="_blank" href="http://php.net/downloads.php">http://php.net/downloads.php</a>
            </p>
            <p>
                <span class="tip">windows</span> 系统选择 <span class="tip">Windows downloads</span>，要下载 <span class="tip">thread safe</span> 版，不要下载 <span class="tip">non thread safe</span>。
            </p>
            <p>
                下载好之后，解压到之前的 <span class="tip">Apache</span> 目录下（我的路径是 <span class="tip">D:/b/</span>）。
            </p>
            <p>
                找到 <span class="tip">D:/b/php/php.ini-production</span>，在原路径上复制一份，命名为 <span class="tip">php.ini</span> 。然后打开它修改配置。
            </p>
            <p>
                建议使用带有行数的编辑器打开，方便修改。
            </p>
            <p>
                修改修改的内容如下(除了736、738，其他都是去掉分号就可以。注：“<span class="tip">;</span>”在 <span class="tip">ini</span> 文件中是注释的意思)：
            </p>
            <pre class="cmd">;736行： ; extension_dir = "./"  改为<br>extension_dir = "D:/b/Apache&php"<br><br>;737行：  ; On windows:  改为<br>On windows:<br><br>;738行：; extension_dir = "ext"  改为 <br>extension_dir = "D:/b/php/ext"<br><br>;893行：;extension=php_curl.dll  改为 <br>extension=php_curl.dll<br><br>;896行：;extension=php_gd2.dll  改为 <br>extension=php_gd2.dll<br><br>;903行：;extension=php_mbstring.dll  改为<br>extension=php_mbstring.dll<br><br>;905行：;extension=php_mysqli.dll  改为 <br>extension=php_mysqli.dll<br><br>;909行：;extension=php_pdo_mysql.dll 改为 <br>extension=php_pdo_mysql.dll</pre>
            <p>
                配置完 <span class="tip">php</span> 之后，就差最后一步了：给 <span class="tip">Apache</span> 配置一个 <span class="tip">php</span> 关联。修改的文件还是之前的 <span class="tip">D:/b/Apache24/conf/httpd.conf</span>。修改内容如下：
            </p>
            <p>
                在  <span class="code">&lt;IfModule unixd_module&gt;</span> 前面，<span class="code">LoadModule</span> 的最后（183行左右的位置）加上这两行
            </p>
            <pre class="cmd">LoadModule php7_module "D:/b/php/php7apache2_4.dll"<br>PHPIniDir "D:/b/php/"</pre>
            <p> 
                最后在此配置文件的最后加上：
            </p>
            <pre class="cmd"># php7 support<br>LoadModule php7_module D:/b/php/php7apache2_4.dll<br>AddType application/x-httpd-php .php .html .htm<br># configure the path to php.iniPHPIniDir "D:/b/php"</pre>
            <p> 
                这时，在重启一下 <span class="tip"> Apache</span> 应该就可以了。如果报错的话，有两个办法查看错误。应该是报这个错误：
            </p>
            <p class="error">
                The requested operation has failed!
            </p>
            <p> 
                方法一（推荐）：使用之前所说的 <span class="tip">cmd</span> 输入命令，会明确说明错误，根据提示操作即可。
            </p>
            <pre class="cmd">httpd -k start</pre>
            <p> 
                方法二：用计算机日志查看错误：<span class="tip">计算机 => 右键 => 管理 => 系统工具 => 事件查看器 => Windows日志 => 应用程序</span>。这是从其他的地方学来的，感觉不是很好用，有兴趣可以研究研究（所以有时候图形界面也并不直观）。
            </p>
            <p> 
                重启 <span class="tip">Apache</span> 的报错问题解决了的话，基本上就没有问题了。这时，打开之前的 <span class="tip"> info.php</span>，如果看到版本信息就说明成功了。
            </p>
            <p> 
                接下来就可以愉快的玩耍了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


