<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">问题描述</p>
            <p>
                使用 <span class="code">fixed</span> 定位后的元素在唤起键盘或选择时间组件（<span class="code">input type="date"</span>） 会受到弹出层的影响而影响布局和用户体验。
            </p>
            <p>
                这种情况的表现在不同的手机及浏览器上不同的，比如 <span class="code">input</span> 框比键盘高出一小截，输入框直接被键盘遮住等。
            </p>
            <p>
                对于上面的问题大家肯定能想到一些方法，比如键盘或时间组件弹起时改变 <span class="code">input</span> 位置；使用 <span class="code">window.resize</span> 事件等。
            </p>
            <p>
                但是现实很残忍。
            </p>
            <p>
                1、<span class="tip">js</span> 没有键盘弹起/收起的事件，也就是无法获取这个事件的发生；
            </p>
            <p>
                2、<span class="tip">iOS</span> 系统键盘弹起/收起不会触发 <span class="code">window.resize</span> 事件；
            </p>
            <p>
                3、<span class="tip">Android 4.4</span> 以下（这样的手机不多了），键盘唤起时，不仅会触发 <span class="code">window.resize</span> 事件，同事也会触发 <span class="code">scroll</span> 事件（如果要使用滑动后失去焦点的需求，应该使用 <span class="code">touchMove</span>，不要使用 <span class="code">scroll</span> 事件）。
            </p>
            <p>
                4、<span class="tip">iOS</span> 之所以会遮挡输入框，是因为第三方输入法的 <span class="tip">tool bar</span> 或者键盘本身也被视作浏览器的可视区（即 <span class="code">window.innerHeight</span>）
            </p>
            <p class="minititle">解决方案</p>
            <p>
                根据上面的情况，有一些解决方案，但是基本都存在一定的兼容问题。下面介绍一些方案：
            </p>
            <p>
                1、不让页面整体滚动，绝对布局滚动内容，局部滚动；
            </p>
            <p>
                2、键盘完全弹出时，判断键盘是否在可视区域（即屏幕除去键盘占用的区域）；通过 <span class="tip">js</span> 调整输入框的位置；键盘收起后，调整到页面底部；
            </p>
            <p>
                下面是一个还凑合的解决方案：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"scroll-wrapper"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"bottom-input"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">input </span><span class="attr">type=</span><span class="str">"text" </span><span class="attr">placeholder=</span><span class="str">"content"</span><span class="arrow">/&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">style</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">.scroll-wrapper </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">position</span><span class="word">:</span><span class="str"> absolute</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">left</span><span class="word">:</span><span class="str"> 0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">right</span><span class="word">:</span><span class="str"> 0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">top</span><span class="word">:</span><span class="str"> 0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">.bottom-input </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">position</span><span class="word">:</span><span class="str"> absolute</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">bottom</span><span class="word">:</span><span class="str"> 0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">left</span><span class="word">:</span><span class="str"> 0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">right</span><span class="word">:</span><span class="str"> 0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">style</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">const </span><span class="param">interval</span><span class="key"> = </span><span class="method">setInterval</span><span class="word">(</span><span class="key">function</span><span class="word">() </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">document</span><span class="word">.</span><span class="spe">body</span><span class="word">.</span><span class="spe">scrollTop</span><span class="key"> = </span><span class="spe">document</span><span class="word">.</span><span class="spe">body</span><span class="word">.</span><span class="spe">scrollHeight</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,100);</span></pre>
            <p>
                上面的这个方法在 <span class="tip">iOS</span> 会出现连续闪烁的情况，即便效果还可以，给每一个 <span class="code">input</span> 加一个获得焦点事件，并且使用计时器是比较消耗资源的。
            </p>
            <p>
                因此，淘宝、腾讯等公司的移动端浏览器上基本看不到 <span class="code">fixed</span> 定位的 <span class="code">input</span>。
            </p>
            <p>
                没错，解决这个问题的最佳方案，就是移动端不用 <span class="code">fixed</span> 属性。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


