<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                亲手尝试过以后对跨域理解加深了许多。端口号不同也是跨域的。会报如下错误：
            </p>
            <p class="error">
                XMLHttpRequest cannot load http://localhost/test.php. No 'Access-Control-Allow-Origin' header is present on the requested resource. Origin 'http://localhost:8080' is therefore not allowed access.
            </p>
            <p>
                协议、端口、域名有一点不同都是属于跨域的。
            </p>
            <p>
                解决跨域的办法不少。这里介绍一种（现在只会这种最简单判断，以后学成了再加方法。据说这种方法安全性不高，所以自己玩玩可以，线上的话，看看有没有更好的办法）：
            </p>
            <p>
                在要请求的 <span class="tip">php</span> 加上 <span class="code">header('Access-Control-Allow-Origin:http://localhost:8080');</span> 即可。下面是一个简陋的无数据库的不安全的假登录的示例：
            </p>
            <pre class="code"><span class="flower">&lt;?php</span><br><span class="method">header</span><span class="word">(</span><span class="str">'Access-Control-Allow-Origin:http://localhost:8080'</span><span class="word">);</span><br><span class="rect">$account</span><span class="key"> = </span><span class="rect">$_GET[</span><span class="str">'account'</span><span class="rect">]</span><span class="word">;</span><br><span class="rect">$password</span><span class="key"> = </span><span class="rect">$_GET[</span><span class="str">'password'</span><span class="rect">]</span><span class="word">;</span><br><span class="key">if</span><span class="word">(</span><span class="rect">$account</span><span class="key"> == </span><span class="str">'admin'</span><span class="key"> && </span><span class="rect">password</span><span class="key"> == </span><span class="str">'123456'</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">echo json_encode</span><span class="word">(</span><span class="method">array</span><span class="word">(</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'msg'&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;=>&nbsp;&nbsp;'登陆成功'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'state'&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;=>&nbsp;&nbsp;1</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'authority'&nbsp;&nbsp;=>&nbsp;&nbsp;</span><span class="method">array</span><span class="word">(</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'read'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'write'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">)</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">),</span><span class="flower">JSON_UNESCAPED_UNICODE</span><span class="word">);</span><br><span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">echo json_encode</span><span class="word">(</span><span class="method">array</span><span class="word">(</span><span class="str">'msg'=>'账号或密码不正确'</span><span class="word">,</span><span class="str">'state'=>0</span><span class="word">),</span><span class="flower">JSON_UNESCAPED_UNICODE</span><span class="word">);</span><br><span class="flower">}</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


