<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">在 HTML 中使用</p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">img</span><span class="attr"> src=</span><span class="str">"./../assets/img_demo.png"</span><span class="arrow"> /&gt;</span></pre>
            <p>
                上面的写法会经过 <span class="tip">webpack</span> 的转化，所以可以引出来。
            </p>
            <p class="minititle">在 JS 中使用</p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">img </span><span class="attr"> :src=</span><span class="str">"demoImg"</span><span class="arrow"> /&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="key">export</span><span class="spe"> default</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">data</span><span class="word"> () </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">demoImg</span><span class="word"> : </span><span class="str">"./../assets/img_demo.png"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span></pre>
            <p>
                <span class="tip">JS</span> 不会被 <span class="tip">webpack</span> 转化，会将地址按原本的样子放入 <span class="code">src</span> 里。所以，要使用这两种方式才能引到图片：
            </p>
            <p>
                一、使用 <span class="tip">import</span> 引入
            </p>
            <pre class="code"><span class="key">import </span><span class="spe">demoImg</span><span class="key"> from</span><span class="str">'./../assets/img_demo.png'</span></pre>
            <p>
                二、使用最外层的路径（在打包出来的 <span class="tip">static</span> 文件夹里须有此文件）
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">img </span><span class="attr"> :src=</span><span class="str">"demoImg"</span><span class="arrow"> /&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="key">export</span><span class="spe"> default</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">data</span><span class="word"> () </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">demoImg</span><span class="word"> : </span><span class="str">"/static/img/img_demo.png"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span></pre>
            <p class="minititle">在 CSS 中使用</p>
            <p>
                在 <span class="tip">CSS</span> 中使用和在 <span class="tip">HTML</span> 中使用基本一样，会经过 <span class="tip">webpack</span> 的转化。但是在实际使用的时候，作为 <span class="code">background-image</span> 遇到了一些问题。
            </p>
            <p>
                打包出来以后的 <span class="tip">CSS</span> 文件会单独放到一个 <span class="tip">css</span> 目录中，而图片会被单独放到一个 <span class="tip">img</span> 目录中。<span class="tip">img</span> 和 <span class="tip">css</span> 属于平级的目录。<span class="tip">CSS</span> 代码本身会被转化为 <span class="code">background-image:"/static/img/demo_img.png"</span>。此时这个路径的图片是找不到的。需要手动改为 <span class="code">background-image:"../../static/img/demo_img.png"</span> 或 <span class="code">background-image:"../img/demo_img.png"</span>。
            </p>
            <p>
                这个手动修改如果量大是比较麻烦的，目前还没有想到好的解决方法。哪位朋友的知道的话，希望能指点一二。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


