<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>上面介绍的也差不多了，就直接上代码吧。</p>
            <p class="minititle">原生 js 方法</p>
            <pre class="code"><span class="com">/* 看起来比 jQuery 简单一些 */</span><br><span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'obj'</span><span class="word">)</span><span class="word">.</span><span class="spe">offsetTop</span> <span class="key">&lt;</span> <span class="spe">document</span><span class="word">.</span><span class="spe">body</span><span class="word">.</span><span class="spe">scrollTop</span><span class="word"> <span class="key">+</span> <span class="spe">window</span><span class="word">.</span><span class="spe">screen</span><span class="word">.</span><span class="spe">height</span>;</span></pre>
            <p class="minititle">jQuery 方法</p>
            <pre class="code"><span class="com">/* 定义方法 */</span><span class="spe">jQuery</span><span class="word">.</span><span class="spe">fn</span><span class="word">.</span><span class="method">visibleCheck</span> <span class="key">=</span> <span class="key">function</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 元素在页面中距离顶端的高度  ?   当前屏幕滚动的高度 + 屏幕本身的高度 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return</span> <span class="spe">$</span><span class="word">(</span><span class="key">this</span><span class="word">).</span><span class="method">offset</span><span class="word">().</span><span class="spe">top</span> <span class="key">&lt;</span> <span class="word">(</span> <span class="spe">$</span><span class="word">(</span><span class="spe">document</span><span class="word">).</span><span class="method">scrollTop</span><span class="word">()</span> <span class="key">+</span> <span class="spe">$</span><span class="word">(</span><span class="spe">window</span><span class="word">).</span><span class="method">height</span><span class="word">() );</span><br><span class="flower">}</span><br><span class="com">/* 调用上面的方法 */</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'obj'</span><span class="word">).</span><span class="method">visibleCheck</span><span class="word">();</span><span class="com"> // 在可视区内返回 true ，否则返回 false</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


