<template>
    <div id="my-blog">
        <router-view></router-view>
    </div>
</template>
<script>
    import Vue from 'vue'
    import Element from 'element-ui'
    import fa from 'font-awesome-webpack'
    import 'element-ui/lib/theme-default/index.css'
    import VueResource from 'vue-resource'
    import code from './assets/css/code.css'
    //import statistics from './assets/js/busuanzi.pure.min.js'

    Vue.use(Element);
    Vue.use(VueResource);
    Vue.use(fa);
    Vue.use(code);
    export default{
        name: 'app',
    }
</script>
<style>
    html,body,#app{margin:0;padding:0;background: #f9fafc;}
    #app {
        margin: 0 auto;
        font-family: "Helvetica Neue","PingFang SC",Arial,sans-serif;
        color:#1f2d3d;
    }
    #my-blog{
        position:relative;
    }
</style>


