<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                加上<span class="code">text-overflow</span> 属性，默认值是<span class="code">clip</span> 是切掉多余的部分，<span class="code">ellipsis</span> 是多余的部分缩成省略号。同时加上 <span class="code">overflow:hidden</span> 进行配合。此外，元素要加上 <span class="code">display:block</span> 或 <span class="code">inline-block</span> 才有用（对象元素是块级的）。例如：
            </p>
            <pre class="code"><span class="word">.</span><span class="key">element</span><span class="flower">{</span><br>&nbsp;<span class="fun">overflow</span><span class="word">:</span><span class="str">hidden</span><span class="word">;</span><br>&nbsp;<span class="fun">white-space</span><span class="word">:</span><span class="str">nowrap</span><span class="word">;</span><br>&nbsp;<span class="fun">width</span><span class="word">:</span><span class="str">50%</span><span class="word">;</span><br>&nbsp;<span class="fun">-ms-text-overflow</span><span class="word">:</span><span class="str">ellipsis</span><span class="word">;</span><br>&nbsp;<span class="fun">text-overflow</span><span class="word">:</span><span class="str">ellipsis</span><span class="word">;</span><br>&nbsp;<span class="fun">display</span><span class="word">:</span><span class="str">inline-block</span><span class="word">;</span><br><span class="flower">}</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


