<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">1、引入 js 和 cs</p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">script</span> <span class="attr">src=</span><span class="str">"sweetalert.min.js"</span><span class="arrow">&gt;&lt;</span><span class="arrow">/</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">link</span> <span class="attr">rel=</span><span class="str">"stylesheet"</span> <span class="attr">href=</span><span class="str">"sweetalert.css"</span><span class="arrow">/></span></pre>
            <p class="minititle">2、简单使用</p>
            <pre class="code"><span class="method">swal</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">title</span><span class="word">:</span><span class="str">'标题'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">text</span><span class="word">:</span><span class="str">'文本'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">type</span><span class="word">:</span><span class="str">'warning'</span><span class="word">,</span><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;/* 可用的 type 有：warning、success、info、input、error */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">showCancelButton</span><span class="word">:</span><span class="key">true</span><span class="word">,</span><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;/* 是否显示取消按钮，默认为 false */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">closeOnConfirm</span><span class="word">:</span><span class="key">false</span><span class="word">,</span><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;/* 点击确定关闭，默认是 true */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">confirmButtonText</span><span class="word">:</span><span class="str">'确认'</span><span class="word">,</span><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;/* 确定按钮文本内容，默认是 ok */</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">confirmButtonColor</span><span class="word">:</span><span class="str">'#ec6c62'</span><span class="word">,</span><span class="com">/* 确定按钮的颜色，默认是一种有点蓝的颜色 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">animation</span><span class="word">:</span><span class="str">'slide-from-top'</span><span class="word">,</span><span class="com">&nbsp;&nbsp;/* 模态框出现的方式 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">imageUR</span><span class="word">:</span><span class="str">'../url.png'</span><span class="word">,</span><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;/* 图片内容地址 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">html</span><span class="word">:</span><span class="str">'直接写 HTML 内容'</span><span class="word">,</span><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;/* 直接写 HTML 可以显示在模态框内 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">allowOutsideClick</span><span class="word">:</span><span class="key">false</span><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;/* 是否支持外部点击关闭，默认是 true */</span><br><span class="flower">}</span><span class="word">,</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 回调函数 */</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p class="minititle">3、新版 2.0 写法改变</p>
            <p>
                在新版的 <span class="tip">Sweet Alert 2.0</span> 对回调函数做了改变，将确定和取消的按钮统一做了回调的处理。写法如下：
            </p>
            <pre class="code"><span class="method">swal</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">title</span><span class="word">:</span><span class="str">'标题'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">showCancelButton</span><span class="word">:</span><span class="key">true</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 其他参数略 */</span><br><span class="flower">}</span><span class="word">).</span><span class="method">then</span><span class="word">(</span><span class="key">function</span><span class="word">(isConfirm)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(isConfirm</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 点击确定按钮后的回调操作 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 点击取消按钮后的操作 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span><span class=""></span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>
<style>
    .color{
        width:10%;
        min-width:30px;
        height:110px;
        float:left;
    }
    .color>div{
        width:30px;
        height:30px;
        margin:0 auto;
    }
    .color1{
        color:#19caad;
    }
    .color1>div{
        background:#19caad;
    }
</style>


