<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">1、a 标签的链接及锚点</p>
            <p>
                大家都知道给 <span class="code">a</span> 标签增加链接非常容易，在 <span class="code">href</span> 属性里写上链接即可。<span class="code">&lt;a href="https://xll032.github.io">首页&lt;/a></span>。如果要打开新的页面只需再加上 <span class="code">target="_blank"</span>。
            </p>
            <p>
                如果不希望它有链接的效果，链接名称写成这样就可以了： <span class="code">href="javascript:void(0)"</span>。由此可见，这里面也可以写一些表达式。例如：<span class="code">href="javascript:history.go(-1)"</span>，返回上一页。
            </p>
            <p>
                <span class="code">a</span> 标签还可以在页面中写成锚点。比如返回某处：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">id=</span><span class="str">"top"</span><span class="arrow">></span><span class="word">我是头部内容</span><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="com">&lt;！-- 中间省略很多内容 --></span><br><span class="arrow">&lt;</span><span class="tag">a</span> <span class="attr">href=</span><span class="str">"#top"</span><span class="arrow">></span><span class="word">去头部</span><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">></span></pre>
            <p class="minititle">2、a 标签的样式设置</p>
            <p>
                <span class="code">a</span> 标签的样式设置是有一定规则的，随便设置顺序是会让样式失效的。设置的规则是：<span class="tip">lvha</span>，示例如下：
            </p>
            <pre class="code"><span class="key">a:link</span><span class="flower">{</span><span class="fun">text-decoration</span><span class="word">:</span><span class="str">none</span><span class="word">;</span><span class="fun">color</span><span class="word">:</span><span class="key">#20a0ff</span><span class="word">;</span><span class="flower">}</span><br><span class="key">a:visited</span><span class="flower">{</span><span class="fun">text-decoration</span><span class="word">:</span><span class="str">none</span><span class="word">;</span><span class="fun">color</span><span class="word">:</span><span class="key">#1d8ce0</span><span class="word">;</span><span class="flower">}</span><br><span class="key">a:hover</span><span class="flower">{</span><span class="fun">text-decoration</span><span class="word">:</span><span class="str">underline</span><span class="word">;</span><span class="fun">color</span><span class="word">:</span><span class="key">#58b7ff</span><span class="word">;</span><span class="flower">}</span><br><span class="key">a:active</span><span class="flower">{</span><span class="fun">text-decoration</span><span class="word">:</span><span class="str">none</span><span class="word">;</span><span class="fun">color</span><span class="word">:</span><span class="key">#1d8ce0</span><span class="word">;</span><span class="flower">}</span><br></pre>
            <p class="minititle">3、a 标签中奇怪的用法</p>
            <p>
                接下来说一个奇怪的现象。先简单举个例子，再来说明（可以点击一下试试效果： <a target="_blank" href="/#/home"><a target="_blank" href="/#/blog">Blog</a></a>），代码如下：
            </a>
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">a</span> <span class="attr">href=</span><span class="str">"/home"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">a</span> <span class="attr">href=</span><span class="str">"/blog"</span><span class="arrow">&gt;</span><span class="word">Blog</span><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">&gt;</span></pre>
            <p>
               一般来说，很少有人会去这么写吧。一个是感觉这种写法非常的奇怪，一个是一般不会有这种奇怪的需求。点击以后生效的里面的链接。在 <span class="tip">Chrome</span> 下会解析成这样（注：新版的浏览器已经不会变化结构了）：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">a</span> <span class="attr">href=</span><span class="str">"/home"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">a</span> <span class="attr">href=</span><span class="str">"/blog"</span><span class="arrow">&gt;</span><span class="word">Blog</span><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">&gt;</span></pre>
            <p>
                不过其实结构只要在 <span class="code">a</span> 标签内写上文字内容还是可以用的，不过会很奇怪，具体的效果自己试试看就知道了。（点此试效果：<a target="_blank" href="/#/home">Home<a target="_blank" href="/#/blog">Blog</a></a>）代码如下：
            </p>
             <pre class="code"><span class="arrow">&lt;</span><span class="tag">a</span> <span class="attr">href=</span><span class="str">"/home"</span><span class="arrow">&gt;</span><span class="word">Home</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">a</span> <span class="attr">href=</span><span class="str">"/blog"</span><span class="arrow">&gt;</span><span class="word">Blog</span><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">&gt;</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


