<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                <span class="tip">IE8</span> 不支持 <span class="code">indexOf()</span> 这个方法，如果非要用这样的方法的话，那就自己封装一个函数。用 <span class="code">for</span> 循环去匹配，然后获取对应的下标即可。
            </p>
            <p>
                <span class="code">for...in</span> 这个方法最好不要在数组中使用，因为在 <span class="code">for...in</span> 中数组会被作为对象处理。对普通对象迭代的时候，会出现一些不想要的属性，同时对象原型中的属性也会显示出来。
            </p>
            <pre class="code"><span class="key">var</span> <span class="param">obj</span> <span class="key">=</span> <span class="flower">{</span> <span class="word">...</span> <span class="flower">}</span><span class="word">;</span><br><span class="key">for</span> <span class="word">(</span><span class="key">var</span> <span class="param">prop</span> <span class="key">in</span> <span class="param">obj</span><span class="word">)</span> <span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param">obj</span><span class="word">.</span><span class="method">hasOwnProperty</span><span class="word">(</span><span class="param">prop</span><span class="word">))</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var</span> <span class="param">item</span> <span class="key">=</span> <span class="param">obj</span><span class="word">.</span><span class="param">prop</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">...</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span></pre>
            <p>
                上面代码中的 <span class="code">obj.prop</span> 是不能用的，也就是说 <span class="code">item</span> 不会被赋值，在 <span class="code">for...in</span> 中应该使用 <span class="code">obj[prop]</span>。总而言之，<span class="code">for...in</span> 应该用于对象，而不是数组。
            </p>
            <p>
                现在对 <span class="tip">IE</span> 的兼容越来越少了，连微软自己都要放弃了。不过，这种兼容还是得看用户群如何了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


