<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">学习 ES6 的原因</p>
            <p>
                最近要使用 <span class="tip">vue</span> 开发一个项目。很多插件都需要自己去封装，如分页、 tab 切换等。于是去找了相关的 <span class="tip">UI</span> 框架。<span class="tip">elementUI</span>，还有一个 <span class="tip">iview</span> 都不错。个人更喜欢用 <span class="tip">elementUI</span> 一点。
            </p>
            <p>
                <span class="tip">elementUI</span> 的官方文档是用 <span class="tip">ES6</span> 写的，转换成 <span class="tip">ES5</span> 再开发则会有诸多不便。加上 <span class="tip">ES6</span> 普及已经是大势所趋。抓紧学习有利无弊。于是，就同时踏上了 学习 <span class="tip">ES6</span>  的道路。
            </p>
            <p class="minititle">推荐</p>
            <p>
                本来想传上来一些小笔记，现在回头看，觉得里面的内容比较浅，基本从下面这本书里得到的理解。直接看这本书效果应该差不多。
            </p>
            <p>
                这里推荐一下：<a target="_blank" href="http://es6.ruanyifeng.com/">《ECMAScript 6 入门》 - 阮一峰</a>。挺适合入门看的。我最近也在看这本书。
            </p>
            <p class="minititle">ES6 转 ES5</p>
            <p>
                <span class="tip">ES6</span> 虽然已经出来很久，并且也在开发中大量使用了。但实际上，各大浏览器对 <span class="tip">ES6</span> 并没有做到完全支持。所以使用的时候还需要转换成 <span class="tip">ES5</span>。
            </p>
            <p>
                如果使用了 <span class="tip">webpack</span> 就不需要了，<span class="tip">build</span> 的时候自己会转换过来。
            </p>
            <p>
                下面介绍的转换工具仍然是 <span class="tip">Grunt</span>。没有使用过的话，可以看之前的一篇安装使用的介绍：<a target="_blank" href="/#/blog/blog_33">《Grunt 的安装、配置以及使用》</a>
            </p>
            <p>
                下面的内容是在已经可以使用 <span class="tip">Grunt</span> 的基础上写的。在配置文件 <span class="tip">Gruntfile.js</span> 中添加代码：
            </p>
            <pre class="code"><span class="spe">grunt</span><span class="word">.</span><span class="spe">initConfig</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// ES6 转换</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">babel</span><span class="word">: </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">options</span><span class="word">: </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">sourceMap</span><span class="word">: </span><span class="key">false</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">presets</span><span class="word">: </span><span class="rect">[</span><span class="str">'D:/Grunt/node_modules/babel-preset-es2015'</span><span class="rect">]</span><span class="com"> // 写自己安装这个组件的地址</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">dist</span><span class="word">: </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">files</span><span class="word">:</span><span class="rect">[</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">expand</span><span class="word">: </span><span class="key">true</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">cwd</span><span class="word">: </span><span class="str">'D:/source/'</span><span class="word">,</span><span class="com"> // 带转换源文件目录</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">src</span><span class="word">: </span><span class="str">'**/*.js'</span><span class="word">,</span><span class="com"> // 源文件名称（ * 表示所有）</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">dest</span><span class="word">: </span><span class="str">'D:/source/output/'</span><span class="com"> // 放置输出文件的目录</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span><br><span class="spe">grunt</span><span class="word">.</span><span class="method">loadNpmTasks</span><span class="word">(</span><span class="str">'grunt-babel'</span><span class="word">);</span><br><span class="spe">grunt</span><span class="word">.</span><span class="method">registerTask</span><span class="word">(</span><span class="str">'toes5'</span><span class="word">,</span><span class="rect">[</span><span class="str">'babel'</span><span class="rect">]</span><span class="word">);</span></pre>
            <p>
                只是这样还不能使用，还要安装这些组件。（这些组件有多，也不知道是不是下了多余的了）
            </p>
            <pre class="cmd">npm install grunt-babel --save-dev</pre>
            <p>
                安装这个以后，会提示找不到 <span class="tip">babel-preset-es2015</span>，那就把这个组件也安装了。
            </p>
            <pre class="cmd">npm install babel-preset-es2015</pre>
            <p>
                这样一来只要路径对了，就可以正常使用了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


