<template>
    <main id="blogList">
        <section class="list-left">
            <article v-for="item in $store.state.currentBlogData" @click="$parent.$parent.toDetails(item.type,item.url)">
                <h4 class="title">
                    {{item.title}}
                </h4>
                <p class="time">
                    {{item.time}}
                    <el-tag v-for="tag in item.tags" :type="tag.type" :key="tag.type">{{tag.name}}</el-tag>
                    {{distance(item.time)}}天前
                </p>
                <p v-html="returnBrief(item.brief)" class="brief">
                    {{item.brief}}
                </p>
            </article>
            <footer class="text-center">
                <el-pagination
                        @size-change="changeSize"
                        @current-change="changePage"
                        :current-page.sync="currentPage"
                        :page-sizes="[6, 10, 20]"
                        :page-size="$store.state.pageSize"
                        layout="total, prev, pager, next"
                        :total="$store.state.totalBlogLength">
                </el-pagination>
            </footer>
        </section>
        <section class="list-right">
            <div class="chosen">
                <div class="classify">
                    <p class="title">分类</p>
                    标签可供筛选
                </div>
                <div class="latest">
                    <p class="title">最新博客</p>
                    显示最新的10篇博客的标题，分类，时间，标题限制长度
                    <p class="content">new Date() 兼容问题 2016-9-13 javascript</p>
                </div>
            </div>
        </section>
    </main>
</template>
<script>
    export default {
        data: function (){
            return {
                currentPage: 1
            }
        },
        methods: {
            toDetails(type,path){
                // 跳转至详情页
                if( type === 0 ) this.$router.push( '/blog/blog_' + path );
                else if( type === 1 ) this.$router.push( '/demo/demo_' + path );
            },
            distance(time){
                // 计算距今的时间（天数）
                let now = new Date();
                let [ year, month, date ] = [ time.substring(0,4), time.substring(5,7), time.substring(8,10) ];
                let before = new Date( year, month - 1, date );
                return ( ( now - before ) / 1000 / 60 / 60 / 24 ) >> 0;
            },
            returnBrief(val){
                // 过滤标签后固定显示最多75字简介
                const _val = val.replace(/<[^>]+>/g,"");
                return _val.substring( 0,75 ) + ( _val.length <= 75 ? '' : '...' );
            },
            changePage(val){
                this.currentPage = val;
                this.getData();
            },
            changeSize(val){
                this.pageSize = val;
                this.getData();
            },
            getData(){
                // 根据 pageSize 和 currentPage 获得当前显示的数据
                const that = this;
                const params = {
                    type: 0, // 0 blog , 1 demo
                    currentPage: that.currentPage
                };
                that.$store.commit( 'pageChange', params );          
            }
        },
        mounted(){
            if(location.hash ==='#/blog') document.title = '个人博客 by 龙 - blogList';
        }
    }
</script>
<style>
    #blogList .list-right .chosen{
        margin:10px;
    }
    #blogList .list-left,#blogList .list-right{
        float:left;
        background: #fff;
        overflow:hidden;
        -webkit-box-shadow: 0 0 10px 0 #eee;
        -moz-box-shadow: 0 0 10px 0 #eee;
        box-shadow: 0 0 10px 0 #eee;
    }
    #blogList .list-left{
        min-height:600px;
        width:64%;
        margin-right:2%;
    }
    #blogList .list-right{
        width:30%;
    }
    #blogList .list-right .chosen .title{
        color:#333;
        font-size:16px;
    }
    #blogList .list-right .chosen .content{
        color:#999;
        font-size:14px;
    }
    #blogList .el-pager li:hover{
        color: #23aa23;
    }
    #blogList .el-pagination__sizes .el-input .el-input__inner:hover{
        border-color: #23aa23;
    }
    #blogList .el-select-dropdown__item:hover{
        background-color: #23aa23;
    }
    #blogList .el-select-dropdown__item.selected.hover{
        background-color: #23aa23;
    }
    #blogList .el-pager li.active{
        border-color: #23aa23;
        background-color: #23aa23;
        color:#fff;
    }
    #blogList .el-tag{
        margin:0 2px;
    }
    #blogList article{
        cursor: pointer;
        padding:6px;
        height:150px;
        overflow:hidden;
       /* -webkit-box-shadow: 0 0 10px 1px #ddd;
        -moz-box-shadow: 0 0 10px 1px #ddd;
        box-shadow: 0 0 10px 1px rgba(240,255,240,1);*/
        font-size:14px;
        transition: all 1s ease-in-out;
        -webkit-transition: all 0.4s ease-in-out;
        -moz-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
    }
    #blogList article p{
        margin:5px 0;
        font-size:1em;
    }
    #blogList article .title{
        border-left:8px solid #23aa23;
        margin:10px 0;
        padding-left:4px;
        color:#475669;
        font-size:1.2em;
    }
    #blogList article .time{
        padding-left:10px;
        color:#99A9BF;
        font-size:0.9em;
    }
    #blogList article .brief{
        padding-left:10px;
        color:#8492A6;
        font-size:1em;
    }
    #blogList footer{
        margin-bottom:10px;
    }
    @media screen and ( min-width: 750px ) {
        #blogList article:hover{
            font-size:16px;
        }
    }
    @media screen and ( max-width: 768px ){
        #blogList .list-left{
            width:100%;
        }
        #blogList .list-right{
            display:none;
        }
    }
</style>


