<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、http 和 https 简单介绍</p>
            <p>
                1、<span class="tip">HTTP </span><span class="tip">（HyperText Transfer Protocol）</span>是一种详细规定了浏览器和万维网服务器之间互相通信的规则，通过因特网传送万维网文档的数据传送协议
            </p>
            <p>
                2、<span class="tip">HTTPS</span><span class="tip">（HyperText Transfer Protocol over Secure Soceket Layer）</span>，是以安全为目标的 <span class="tip">HTTP</span> 通道，简单讲是 <span class="tip">HTTP</span> 的安全版
            </p>
            <p>
                即在 <span class="tip">HTTP</span> 下加入 <span class="tip">SSL</span> 层，<span class="tip">HTTPS</span> 的安全基础是 <span class="tip">SSL</span>，因此加密的详细内容就需要 <span class="tip">SSL</span>。
            </p>
            <p>
                <span class="tip">HTTPS</span> 在 <span class="tip">HTTP</span> 和 <span class="tip">TCP</span> 之间有一个加密/身份验证层。现在广泛用于万维网上安全敏感的通讯，例如交易支付。
            </p>
            <p class="minititle">二、HTTP 和 HTTPS 之间的区别</p>
            <p>
                （1）<span class="tip">HTTPS</span> 协议需要到 <span class="tip">CA</span> 申请证书，一般免费证书很少，需要交费；
            </p>
            <p>
                （2）<span class="tip">HTTP</span> 是超文本传输协议，信息是明文传输，<span class="tip">HTTPS</span> 则是具有安全性的 <span class="tip">SSL</span> 加密传输协议；    
            </p>
            <p>
                （3）<span class="tip">HTTP</span> 和 <span class="tip">HTTPS</span> 使用的是完全不同的连接方式用的端口也不一样，前者是 <span class="tip">80</span>，后者是 <span class="tip">443</span>；
            </p>
            <p>
                （4）<span class="tip">HTTP</span> 的连接很简单,是无状态的 <span class="tip">HTTPS</span> 协议是由 <span class="tip">SSL</span> + <span class="tip">HTTP</span> 协议构建的可进行加密传输、身份认证的网络协议；
            </p>
            <p>
                5）要比 <span class="tip">HTTP</span> 协议安全的 <span class="tip">HTTPS</span> 协议所解决的问题：
            </p>
            <p>
                1. 信任主机的问题。 采用 <span class="tip">HTTPS</span> 的 <span class="tip">server</span> 必须从 <span class="tip">CA</span> 申请一个用于证明服务器用途类型的证书。改证书只有用于对应的 <span class="tip">server</span> 的时候,客户度才信任次主机。所以目前所有的银行系统网站，关键部分应用都是 <span class="tip">HTTPS</span> 的。客户通过信任该证书,从而信任了该主机。其实这样做效率很低,但是银行更侧重安全。这一点对我们没有任何意义，我们的 <span class="tip">server</span> ，采用的证书不管自己 <span class="tip">issue</span> 还是从公众的地方 <span class="tip">issue</span>， 客户端都是自己人，所以我们也就肯定信任该 <span class="tip">server</span>。
            </p>
            <p>
                2. 通讯过程中的数据的泄密和被窜改：
            </p>
            <p>
                A. 一般意义上的 <span class="tip">HTTPS</span>，就是 <span class="tip">server</span> 有一个证书：
            </p>
                a) 主要目的是保证 <span class="tip">server</span> 就是他声称的 <span class="tip">HTTPS</span>。这个跟第一点一样；
            <p>
            <p>
                b) 服务端和客户端之间的所有通讯，都是加密的；
            </p>
            <p>
                i. 具体讲,是客户端产生一个对称的密钥,通过 <span class="tip">server</span> 的证书来交换密钥. 一般意义上的握手过程；
            </p>
            <p>
                ii. 加下来所有的信息往来就都是加密的。第三方即使截获也没有任何意义。因为他没有密钥， 当然窜改也就没有什么意义了；
            </p>
            <p>
                B. 少许对客户端有要求的情况下，会要求客户端也必须有一个证书：
            </p>
            <p>
                a) 这里客户端证书，类似表示个人信息的时候，除了用户名/密码，还有一个 <span class="tip">ca</span> 认证过的身份。应为个人证书一般来说上别人无法模拟的，所有这样能够更深的确认自己的身份；
            </p>
            <p>
                b) 目前少数个人银行的专业版是这种做法,具体证书可能是拿U盘作为一个备份的载体。<span class="tip">HTTPS</span> 一定是繁琐的；
            </p>
            <p>
                c) 本来简单的 <span class="tip">HTTP</span> 协议，一个 <span class="tip">get</span> 一个 <span class="tip">response</span>。由于 <span class="tip">HTTPS</span> 要还密钥和确认加密算法的需要。单握手就需要 6、7 个往返：
            </p>
            <p>
                i. 任何应用中，过多的 <span class="tip">round trip</span> 肯定影响性能；
            </p>
            <p>
                d) 接下来才是具体的 <span class="tip">HTTP</span>协议每一次响应或者请求，都要求客户端和服务端对会话的内容做加密/解密：
            </p>
            <p>
                i. 尽管对称加密/解密效率比较高,可是仍然要消耗过多的 <span class="tip">CPU</span>，为此有专门的 <span class="tip">SSL</span> 芯片. 如果 <span class="tip">CPU</span> 性能比较低的话,肯定会降低性能,从而不能 <span class="tip">server</span> 更多的请求；
            </p>
            <p>
                ii. 加密后数据量的影响。所以会出现那么多的安全认证提示
            </p>
        
                
                
                    
            
                
                    
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


