<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                1、限宽不限高
            </p>
            <p>
                <span class="code">width</span> 设为定值或百分比，不设高度，图片自动按比例缩放为指定宽度（百分比可以自适应）。        
            </p>
            <pre class="code"><span class="tag">img</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">width</span><span class="word">:</span><span class="str">100%</span><span class="word">;</span><span class="com">// 100px</span><br><span class="flower">}</span></pre>
            <p>
                2、限高不限宽
            </p>
            <p>
                <span class="code">height</span> 设为定值百分比，不设宽度，图片自动按比例缩放为指定高度（百分比同样可以自适应大小）。但要注意，这里的 <span class="code">height </span>设置了百分比高度后，必须对父级设置了定值的高度才能生效。
            </p>
            <pre class="code"><span class="tag">img</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">height</span><span class="word">:</span><span class="str">100px</span><span class="word">;</span><span class="com">// 100%，父级必须设置定值的高度，百分比才生效    </span><br><span class="flower">}</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


