<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                这些方法其实都比较简单，下面罗列了这些方法和说明。
            </p>
            <el-table :data="table_45" stripe>
                <el-table-column prop="name" label="属性名" width="120">
                </el-table-column>
                <el-table-column prop="intro" label="说明" min-width="">
                </el-table-column>
            </el-table>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


