<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">安装</p>
            <p>
                去官网安装 <span class="tip">node.js</span> ，链接地址：<a target="_blank" href="https://nodejs.org/en/download/">nodejs.org/en/download/</a>。下载好以后安装后就能使用了，最新的 <span class="tip">node.js</span> 会一并把 <span class="tip">npm</span> 安装好。
            </p>
            <p class="minititle">查看版本</p>
            <p>
                在 <span class="tip">cmd</span> 中使用 <span class="code">node -v</span> 可以查看 <span class="tip">node.js</span> 版本。如果显示了版本说明安装成功。
            </p>
           <p>
               安装非常的简单，后续写一些更有用的吧。
           </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


