<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
             <p>
                原生的方法如下：
            </p>
            <pre class="code"><span class="spe">window</span><span class="word">.</span><span class="method">print</span><span class="word">();</span></pre>
            <p>
                这个方法在 <span class="tip">Chrome</span>、<span class="tip">360</span>、<span class="tip">UC</span>、<span class="tip">edge</span> 下预览，<span class="tip">FireFox</span>、<span class="tip">IE</span> 下没有预览（其他浏览器没有试过）。
            </p>
            <p>
                打印的插件可以去 <span class="tip">github</span> 上搜索看看。不过我觉得浏览器上做这个效果并不是很理想。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


