<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
               <span class="tip">echarts</span> 的官方文档写的很详细，比较推荐去官网学习。这里提供一个官网链接：<a target="_blank" href="http://echarts.baidu.com/">http://echarts.baidu.com/</a>
            </p>
            <p>
                这里简单的举一个饼图的例子。这里用的是 <span class="tip">echarts@3.6.2</span> 版本。使用不同版本产生兼容问题，请以官网文档为准。
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">id=</span><span class="str">"pieChart"</span><span class="arrow">></span><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">></span><br><span class="key">var </span><span class="param">pieChart</span><span class="key"> = </span><span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'pieChart'</span><span class="word">);</span><br><span class="param">pieChart</span><span class="word">.</span><span class="fun">showLoading</span><span class="word">();</span><br><span class="param">pieChart</span><span class="word">.</span><span class="fun">setOption</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">title</span><span class="str">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">text</span><span class="str">:</span><span class="str">'2016年四餐消费数据统计表'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">subtext</span><span class="str">:</span><span class="str">'瞎扯的数据'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">x</span><span class="str">:</span><span class="str">'center'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">tooltip</span><span class="str">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">trigger</span><span class="str">:</span><span class="str">'item'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">format</span><span class="str">:</span><span class="str">'</span><span class="word">{a}&lt;</span><span class="tag">br</span><span class="word">/>{b}:{c}({d}%)</span><span class="str">:</span><span class="str">'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">x</span><span class="str">:</span><span class="str">'center'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">legend</span><span class="str">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">orient</span><span class="str">:</span><span class="str">'vertical'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">left</span><span class="str">:</span><span class="str">'left'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="str">:</span><span class="rect">[</span><span class="str">'早餐'</span><span class="word">,</span><span class="str">'午餐'</span><span class="word">,</span><span class="str">'晚餐'</span><span class="word">,</span><span class="str">'夜宵'</span><span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">series</span><span class="str">:</span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">name</span><span class="str">:</span><span class="str">'来源'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">type</span><span class="str">:</span><span class="str">'pie'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">radius</span><span class="str">:</span><span class="str">'55%'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">center</span><span class="str">:</span><span class="rect">[</span><span class="str">'50%'</span><span class="word">,</span><span class="str">'60%'</span><span class="rect">]</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="str">:</span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">2165</span><span class="word">,</span><span class="spe">name</span><span class="str">:'早餐'</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">3892</span><span class="word">,</span><span class="spe">name</span><span class="str">:'午餐'</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">4120</span><span class="word">,</span><span class="spe">name</span><span class="str">:'晚餐'</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">1246</span><span class="word">,</span><span class="spe">name</span><span class="str">:'夜宵'</span><span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">]</span><br><span class="flower">}</span><span class="word">);</span><br><span class="param">pieChart</span><span class="word">.</span><span class="fun">hideLoading</span><span class="word">();</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">></span></pre>
            <p>
            另外不要忘记引入 <span class="tip">echarts</span>，写代码的时候最好是把里面的数据另外存入变量中，操作会更灵活，看起来也会舒服一点，封装一个方法调用用起来会更好一些。
            </p>
            <p>
            如果是用 <span class="tip">vue</span> 的话，有一个 <span class="tip">v-charts</span> 是基于 <span class="tip">echarts</span> 封装的，据说比较轻量，下面的效果是直接用的 <span class="tip">echarts</span>，封装了一个 <span class="code">methods</span>，名为 <span class="code">drawPie(id)</span>，然后在 <span class="code">mounted</span> 的时候，调用函数 <span class="code">this.drawPie('pieChart')</span>
            </p>
            <p>上面的代码运行效果如下：</p>
            <div id="pieChart"></div>
        </section>
    </div>
</template>
<script>
    import echarts from 'echarts'
    export default{
        data(){
             const arr = this.$store.state.initialBlogData;
        return {
            thisBlog: arr[ arr.length - 1 - this.order() ],
                chart: null,
                head: ['早餐','午餐','晚餐','夜宵'],
                headData: [ 
                    {value:2165,name:'早餐'},
                    {value:3892,name:'午餐'},
                    {value:4120,name:'晚餐'},
                    {value:1246,name:'夜宵'}
                ]
            }
        },
        methods:{
            order(){
                return ( window.location.href.substring(window.location.href.indexOf('_')+1) - 1 );
            },
            drawPie(id) {
                // 绘图方法
                this.chart = echarts.init(document.getElementById(id));
                // 皮肤添加同一般使用方式
                this.chart.showLoading();
                // 返回到data中
                let that = this;
                // set
                this.chart.setOption({
                    title : {
                        text: '2016年四餐消费数据统计表',
                        subtext: '瞎扯的数据',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: that.head
                    },
                    series : [
                        {
                            name: '来源',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:that.headData,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
                this.chart.hideLoading()
            }
        },
        mounted(){
            this.drawPie('pieChart');
            document.documentElement.scrollTop = document.body.scrollTop =0
        }
    }
</script>
<style>
    #pieChart{
        width:500px;
        height:500px;
    }
</style>


