<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">引入 js 和 css</p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">link</span> <span class="attr">rel=</span><span class="str">"path/to/swiper.min.css"</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span> <span class="attr">src=</span><span class="str">"path/to/swiper.jquery.min.js"</span><span class="arrow">&gt;</span><br></pre>
            <p>
                如果使用了 <span class="tip">jQuery</span> 或者 <span class="code">zepto</span> 可以使用更轻量的 <span class="code">swiper.jquery.min.js</span>。
            </p>
            <p class="minititle">简单的使用介绍</p>
            <p>
                直接上一个简单的示例代码，也可以去官网上查看详细说明。
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-container"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-wrapper"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-slide"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">slide content 111</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-slide"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">slide content 222</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-slide"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">slide content 333</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">&lt;!-- 分页器 --&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-pagination"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">&lt;!-- 导航按钮 --&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-button-prev"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-button-next"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">&lt;!-- 滚动条 --&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span> <span class="attr">class=</span><span class="str">"swiper-scrollbar"</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">></span><br><span class="key">var</span> <span class="param">mySwiper</span> <span class="key">=</span> <span class="key">new</span> <span class="fun">Swiper</span><span class="word">('.</span><span class="key">swiper-container</span><span class="word">',</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">width</span><span class="word">:</span><span class="word">100,</span><span class="com">// 宽度</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">height</span><span class="word">:</span><span class="word">100,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">width</span><span class="word">:</span><span class="word">100,</span><span class="com">// 高度</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">direction</span><span class="word">:</span><span class="str">'vertical'</span><span class="word">,</span><span class="com">// 滑动的方向：vertical 垂直；horizontal 水平</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">loop</span><span class="word">:</span><span class="key">true</span><span class="word">,</span><span class="com">// 是否开启自动循环</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">pagination</span><span class="word">:</span><span class="str">'.swiper-pagination'</span><span class="word">,</span><span class="com">// 分页器</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">nextButton</span><span class="word">:</span><span class="str">'.swiper-button-next'</span><span class="word">,</span><span class="com">// 显示下一页（与上一页配对）</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">prevButton</span><span class="word">:</span><span class="str">'.swiper-button-prev'</span><span class="word">,</span><span class="com">// 显示上一页（与下一页配对）</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">scrollbar</span><span class="word">:</span><span class="str">'.swiper-scrollbar'</span><span class="word">,</span><span class="com">// 滚动条</span><br><span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">></span></pre>
           <p>
               另外，如果不使用 <span class="code">Source Map</span>功能，把 <span class="tip">js</span> 压缩文件最后一行 <span class="code">//# sourceMappingURL=maps/swiper.min.js.map</span> 删掉。以免在某些浏览器出现 500 报错。要使用的话，需要加入 <span class="code">map</span> 文件。
           </p>
           <p>
               最后附上 <span class="tip">Swiper</span> 官方链接：<a target="_blank" href="http://www.swiper.com.cn">www.swiper.com.cn</a>。
           </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


