<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
               1、<span class="code">parent()</span> 方法若不填写参数，返回元素的上一级；若填写了参数，返回符合条件的上一级，当上一级不满足条件时，则返回空值。见下例：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"parent"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"demo"</span><span class="arrow">>&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'.demo'</span><span class="word">).</span><span class="method">parent</span><span class="word">();</span><span class="com">// 返回 .parent jQuery 对象</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'.demo'</span><span class="word">).</span><span class="method">parent</span><span class="word">(</span><span class="str">'.parent'</span><span class="word">);</span><span class="com">// 结果同上</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'.demo'</span><span class="word">).</span><span class="method">parent</span><span class="word">(</span><span class="str">'.p'</span><span class="word">);</span><span class="com">// .p 不存在，返回空值</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">></span></pre>
            <p>
                2、<span class="code">parents()</span> 方法若不填写参数，则返回所有父级元素；若填写元素，则从所有父级元素中找符合条件的元素，当所有父级都不合条件时，返回空值。要注意的是，找父级元素是一级一级向上找的，符合条件的会按顺序一个个 <span class="code">push</span> 到一个数组里。
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"grandpa"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"father"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"father"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">class=</span><span class="str">"demo"</span><span class="arrow">>&lt;/</span><span class="tag">div</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'.demo'</span><span class="word">).</span><span class="method">parents</span><span class="word">();</span><br><span class="com">/* 返回元素依次为 .father,.father .grandpa body html jQuery 对象的数组（两个 .father 的顺序也是依次向上的） */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'.demo'</span><span class="word">).</span><span class="method">parents</span><span class="word">(</span><span class="str">'.father'</span><span class="word">);</span><br><span class="com">/* 返回元素依次为 .father .father jQuery对象的数组 */ </span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'.demo'</span><span class="word">).</span><span class="method">parents</span><span class="word">(</span><span class="str">'.grandpa'</span><span class="word">);</span><br><span class="com">/* 返回 .grandpa jQuery对象 */ </span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'.demo'</span><span class="word">).</span><span class="method">parent</span><span class="word">(</span><span class="str">'.p'</span><span class="word">);</span><span class="com">// .p 不存在，返回空值</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">></span></pre>
            <p>
                掌握了这个方法，对获取父级元素进行 <span class="tip">DOM</span> 操作也就变得非常轻松了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


