<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                只需要为 <span class="tip">IE</span> 专门增加一个组件即可，即 <span class="tip">babel-polyfill </span>。
            </p>
            <p>
                安装组件很容易，就不多说了。
            </p>
            <pre class="cmd">npm install babel-polyfill</pre>
            <p>
                然后在 <span class="tip">main.js</span> 中引入：
            </p>
            <pre class="code"><span class="key">import </span><span class="str">'babel-polyfill'</span></pre>
            <p>
                但不知为何， <span class="tip">axios</span> 还是不支持 <span class="tip">IE9</span> 及以下的浏览器（<span class="tip">axios</span> 文档中写了支持 <span class="tip">IE9</span>），没有找到解决办法。所以最后还是放弃了。只支持到 <span class="tip">IE10</span>。
            </p>
            <p>
                如果有哪位大神知道为什么 <span class="tip">IE9</span> 兼容不了，请赐教。愿闻其详。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


