<template>
    <main>
        <section>
            <article v-for="item in $store.state.currentDemoData" @click="$parent.$parent.toDetails(item.type,item.url)">
                <h4 class="title">
                    {{item.title}}
                </h4>
                <p class="time">
                    {{item.time}}
                    <el-tag v-for="tags in item.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
                    {{distance(item.time)}}天前
                </p>
                <p class="brief">
                    {{item.brief}}
                </p>
            </article>   
        </section>
         <footer class="text-center">
            <el-pagination
              @size-change="changeSize"
              @current-change="changePage"
              :current-page.sync="currentPage"
              :page-sizes="[6, 10, 20]"
              :page-size="$store.state.pageSize"
              layout="total, prev, pager, next"
              :total="$store.state.totalDemoLength">
            </el-pagination>
        </footer>
    </main>
</template>
<script>
    export default {
        data: function (){
            return {
                currentPage: 1
            }
        },
        methods: {
            distance(time){
                // 计算距今的时间（天数）
                let now = new Date();
                let [ year, month, date ] = [ time.substring(0,4), time.substring(5,7), time.substring(8,10) ];
                let before = new Date( year, month, date );
                return ( ( now - before ) / 1000 / 60 / 60 / 24 ) >> 0;
            },
            returnBrief(val){
                // 过滤标签后固定显示最多75字简介
                const _val = val.replace(/<[^>]+>/g,"");
                return _val.substring( 0,75 ) + ( _val.length <= 75 ? '' : '...' );
            },
            changePage(val){
                this.currentPage = val;
                this.getData();
            },
            changeSize(val){
                this.pageSize = val;
                this.getData();
            },
            getData(){
                // 根据 pageSize 和 currentPage 获得当前显示的数据
                const that = this;
                const params = {
                    type: 0, // 0 blog , 1 demo
                    currentPage: that.currentPage
                }
                that.$store.commit( 'pageChange', params );              
            }
        }
    }
</script>
<style>
    article{
        cursor: pointer;
        padding:6px;
        height:150px;
       /* -webkit-box-shadow: 0 0 10px 1px #ddd;
        -moz-box-shadow: 0 0 10px 1px #ddd;
        box-shadow: 0 0 10px 1px rgba(240,255,240,1);*/
        font-size:14px;
        transition: all 1s ease-in-out;
        -webkit-transition: all 0.4s ease-in-out;
        -moz-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
    }
    article:hover{
        font-size:16px;
    }
    article p{
        margin:5px 0;
        font-size:1em;
    }
    article .title{
        border-left:8px solid #23aa23;
        margin:10px 0;
        padding-left:4px;
        color:#475669;
        font-size:1.4em;
    }
    article .time{
        padding-left:10px;
        color:#99A9BF;
        font-size:0.9em;
    }
    article .brief{
        padding-left:10px;
        color:#8492A6;
        font-size:1em;
    }
</style>


