<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、$.extend()</p>
            <p>
                <span class="code">$.extend()</span> 方法的作用非常多，下面就来一一介绍。
            </p>
            <p class="minititle">（1）重载原型，实现静态方法</p>
            <pre class="code"><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">demo</span><span class="word">:</span><span class="key">function</span><span class="word">(name)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="word">name</span><span class="key"> ? </span><span class="word">name</span><span class="key"> : </span><span class="str">'test'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br><span class="spe">$</span><span class="word">.</span><span class="method">demo</span><span class="word">();</span><span class="com">// 无参调用，输出 test</span><br><span class="spe">$</span><span class="word">.</span><span class="method">demo</span><span class="word">('hello');</span><span class="com">// 带参调用，输出 hello</span></pre>
            <p class="minititle">（2）合并对象</p>
            <pre class="code"><span class="key">let </span><span class="param">res</span><span class="key"> = </span><span class="flower">{ </span><span class="spe">a</span><span class="word">:1, </span><span class="spe">b</span><span class="word">:2</span><span class="flower"> }</span><span class="word">;</span><br><span class="com">// 可合并多个对象，会改变 res，res 变为 {a:1,b:2,c:3,d:4}</span><br><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="param">res</span><span class="word">,</span><span class="flower">{</span><span class="spe">c</span><span class="word">:3</span><span class="flower">}</span><span class="word">,</span><span class="flower">{</span><span class="spe">d</span><span class="word">:4</span><span class="flower">}</span><span class="word">);</span><br><span class="com">// 如果不想改动原数组，可以新建一个对象存放数据</span><br><span class="key">let </span><span class="param">Res</span><span class="key"> = </span><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="flower">{}</span><span class="word">,</span><span class="param">res</span><span class="word">,</span><span class="flower">{</span><span class="spe">c</span><span class="word">:3</span><span class="flower">}</span><span class="word">,</span><span class="flower">{</span><span class="spe">d</span><span class="word">:4</span><span class="flower">}</span><span class="word">);</span></pre>
            <p class="minititle">（3）重载</p>
            <p>
                即重新加载内容，常用于更新值，有点类似于（2）所描述的。当然也可以用来重载函数等。
            </p>
            <p class="minititle">（4）拷贝</p>
            <p>
                拷贝分为普通拷贝和深度拷贝两种。<span class="code">$.extend()</span> 方法第一个参数传 <span class="code">true</span> 即为深度拷贝，传 <span class="code">false</span> 则为普通拷贝。
            </p>
            <p>
                深度拷贝会将所有的内容完整拷贝下来，而普通拷贝则在匹配到一个符合条件的值后将其他的值剔除。具体的见下面例子中的展示：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">obj1</span><span class="key"> = </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">name</span><span class="word">:</span><span class="str">'John'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">age</span><span class="word">:</span><span class="word">23</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">address</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">province</span><span class="word">:</span><span class="str">'浙江'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">city</span><span class="word">:</span><span class="str">'杭州'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">;</span><br><span class="key">var </span><span class="param">obj2</span><span class="key"> = </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">gender</span><span class="word">:</span><span class="str">'male'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">address</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">city</span><span class="word">:</span><span class="str">'宁波'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">;</span><br><span class="key">var </span><span class="param">res1</span><span class="key"> = </span><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="key">true</span><span class="word">,</span><span class="param">obj1</span><span class="word">,</span><span class="param">obj2</span><span class="word">);</span><br><span class="key">var </span><span class="param">res2</span><span class="key"> = </span><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="key">false</span><span class="word">,</span><span class="param">obj1</span><span class="word">,</span><span class="param">obj2</span><span class="word">);</span><br><span class="com">/*  结果：</span><br><span class="com">res1 = {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">name:'John',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">age:23,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">gender:'male',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">address:{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">province:'浙江',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">city:'宁波'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">}</span><br><span class="com">}</span><br><span class="com">res2 = {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">name:'John',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">age:23,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">gender:'male',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">address:{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">city:'宁波'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">}</span><br><span class="com">}</span><br><span class="com">*/</span></pre>
            <p class="minititle">（5）$.fn.extend()</p>
            <p>
                <span class="code">$.fn.extend()</span> 是为 <span class="tip">jQuery</span> 添加一个静态方法，实例化的方法可以通过一个 <span class="tip">jQuery</span> 对象去调用，见下例：
            </p>
            <pre class="code"><span class="spe">$</span><span class="word">.</span><span class="spe">fn</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">myMethod</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">alert</span><span class="word">(123);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'.selector'</span><span class="word">).</span><span class="method">myMethod</span><span class="word">();</span></pre>
            <p>
                另外提一点，<span class="code">$.fn.extend()</span> 在封装 <span class="tip">jQuery</span> 插件比较常用。而为了防止 <span class="code">$</span> 符号在框架间产生冲突，一般不会直接使用它，而直接使用 <span class="code">jQuery</span>。
            </p>
            <p>
                为了减少反复写 <span class="code">jQuery</span>，可以使用下面的方式封装一下，把 <span class="code">$</span> 作为形参来使用。如：
            </p>
            <pre class="code"><span class="word">(</span><span class="key">function</span><span class="word">($)</span><span class="flower">{</span><span class="com"> /* 内容1 */ </span><span class="flower">}</span><span class="word">)(jQuery);</span><br><span class="word">(</span><span class="key">function</span><span class="word">($)</span><span class="flower">{</span><span class="com"> /* 内容2 */ </span><span class="flower">}</span><span class="word">(jQuery));</span></pre>
            <p>
                这两种写法，还疑惑了一把。第一种用括号把匿名函数包起来然后调用，传入实参 <span class="code">jQuery</span>；
            </p>
            <p>
                第二种的括号的包在最外层的。这种写法，看起来最外层的括号并没有用，而实际上去掉会报错。
            </p>
            <p class="error">Uncaught SyntaxError: Unexpected token (</p>
            <p>
                想想就知道了，<span class="code">function</span> 后面不加函数名是错误的写法。加上了括号正是使匿名函数合法化。不管是第一种还是第二种，函数体都在括号内部，所以效果是一样的。
            </p>
            <p>
                PS：这个解释是我自己想出来的，不知道原理是不是真的如此，如果有人了解的话，望告知。万分感谢。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


