<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                将 <span class="tip">/#/</span> 去除掉的方式非常简单。
            </p>
            <pre class="code"><span class="key">const </span><span class="param">router</span><span class="key"> = new </span><span class="word">VueRouter(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">mode</span><span class="word">:</span><span class="str">'history'</span><span class="word">,</span><span class="com"> // 加上这句就 ok 了</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">routes</span><span class="word">:</span><span class="param">routeConfig</span><br><span class="flower">}</span></pre>
            <p>
                但有一点一定要注意。使用 <span class="tip">history</span> 模式以后，虽然可以去掉 <span class="tip">/#/</span>，但是在服务器环境下刷新就会找不到地址。因为会按照传统的路径模式去找文件（单页应用采用 <span class="tip">js</span> 注入的方式当然没有这些路径的文件）。
            </p>
            <p>
                因此，如果只是认为 <span class="tip">/#/</span> 不好看的话，不要使用 <span class="tip">history</span> 模式。点击跳转虽然没有问题，但是总会有用户通过 <span class="tip">url</span> 地址进入页面的情况发生。这是页面就找不到了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


