<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                设置 <span class="code">textarea</span> 有下面两种方法。第二种效果更好一些，本质就是禁止用户改变 <span class="code">textarea</span> 的宽度。废话不多说，直接上代码：
            </p>
            <pre class="code"><span class="com">/* 1、下面的属性将会完全禁止缩放，用户体验不好 */</span><br><span class="key">textarea</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">resize</span><span class="word">:</span><span class="key">none</span><span class="word">;</span><br><span class="flower">}</span><br><span class="com">/* 2、利用最小宽度禁止横向缩放，纵向对布局一般无影响 */</span><br><span class="key">textarea</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">max-width</span><span class="word">:</span><span class="key">100%</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">width</span><span class="word">:</span><span class="key">1500px</span><span class="word">;</span><span class="com">/* 最小宽度定死后，设置一个很大的宽度，就不能横向缩放了 */</span><br><span class="flower">}</span><br></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


