<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
               <span class="code">remove()</span> 方法虽然会将元素从 <span class="tip">DOM</span> 树中删除，但是实际上并不会完全删除这个元素。这个元素仍然被存在某一个元素中，要如何取得这个元素，下面举个简单的例子来说明。
            </p>
            <pre class="code"><span class="key">var</span> <span class="param">el</span> <span class="key">=</span> <span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="word">.</span><span class="key">removeEle</span><span class="str">'</span><span class="word">).</span><span class="method">remove</span><span class="word">();</span><span class="com">// 移除</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="word">.</span><span class="key">parent</span><span class="str">'</span><span class="word">).</span><span class="method">append</span><span class="word">(</span><span class="param">el</span><span class="word">);</span><span class="com">// 重新添加</span></pre>
            <p>
                也就是说 <span class="code">remove()</span> 方法的返回值就是元素本身，通过一个变量保存这个返回值，就可以再需要的时候再次使用了。
            </p>
            <p>
                有一点是需要注意的，<span class="code">remove()</span> 和 <span class="code">append()</span> 都是向 <span class="tip">DOM</span> 树中添加或删除元素，这是需要经过渲染的。操作很多的话，开销还是比较大。刚开始学 <span class="tip">jQuery</span> 的时候用这两个方法写过一个 <span class="tip">2048</span> 小游戏，运行到数字比较多的时候能明显感觉不流畅。 
            </p>
            <p>
                因此，<span class="code">remove()</span> 方法最好用在元素基本上不会再用的时候。<span class="tip">2048</span> 小游戏还是用 <span class="code">canvas</span> 性能好一些。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


