<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
               实际上，使用媒体查询是一个不错的方案，但是由于屏幕的尺寸变化非常大，有时候相差一点就可能需要重新定制一个样式，造成大量重复相似的 <span class="tip">css</span> 。这时候就到了除了 <span class="code">px</span> 以外的其他属性发挥作用了。
            </p>
           <p>
               首先先简单介绍一下这 6 种属性
           </p>
           <p class="minititle">1、px（pixel）</p>
           <p>
               这个属性就不多说了，就是传统的像素；
           </p>
           <p class="minititle">2、em</p>
           <p>
               表示相对父节点的大小。如父节点为 <span class="code">16px</span>，而某元素宽度为 <span class="code">0.5rem</span>，那么这个元素的实际宽度就是 <span class="code">8px</span>；
           </p>
           <p class="minititle">3、rem</p>
           <p>
               表示相对根节点的大小。如根节点为 <span class="code">16px</span>，而某元素宽度为 <span class="code">0.5rem</span>，那么这个元素的实际宽度就是 <span class="code">8px</span>。根节点一般是 最外层设置过 px 大小的节点（<span class="code">font-size</span> 属性）；
           </p>
           <p class="minititle">4、vw</p>
           <p>
               表示相对可视区宽度的大小。如可视区宽度为 <span class="code">750px</span>，而某元素宽度为 <span class="code">10vw</span>，那么这个元素的实际宽度就是 <span class="code">75px</span>（计算方式为每一份的 <span class="code">vw</span> 对应了可视区宽度的 <span class="tip">1%</span>，即可视区宽度被分为 <span class="tip">100</span> 份）；
           </p>
           <p class="minititle">5、vh</p>
           <p>
               表示相对可视区高度的大小。如可视区高度为 <span class="code">1080px</span>，而某元素的高度为 <span class="code">100vh</span>，那么这个元素的实际宽度就是 <span class="code">1080px</span>（计算方式同 <span class="code">vw</span>，不赘述）；
           </p>
           <p class="minititle">6、vm（vmin）</p>
           <p>
               表示相对可视区宽度和高度中最小的那一个值的大小。如可视区为 <span class="tip">1366 * 1080</span>，那么就取 <span class="code">1080px</span> 进行分割。
           </p>
           <p>
               介绍完这些之后，对它们就可以做一个总结了：
           </p>
           <p>
               <span class="code">em</span> 只针对父级元素，如果使用得当也还可以，个人感觉效果既然和 <span class="code">rem</span> 差不多，还是要看具体的需求再决定用哪一个好。<span class="code">rem</span> 多用于手机端，写一段 <span class="tip">js</span> 去判断屏幕宽度计算出一个合适的大小值赋给 <span class="tip">html</span> 节点，这样一套 <span class="tip">css</span> ，有一个设计图的标准的话，就可以很容易适应所有手机屏幕。
           </p>
           <p>
               <span class="code">vh</span>、<span class="code">vm</span>、<span class="code">vw</span> 也是多用于手机端，PC 端很多浏览器并不支持该属性。具体使用不做介绍了，没有真的用过这些，不敢乱说。
           </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


