<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                使用 <span class="tip">animate</span> 函数的方法就不介绍了。下面直接上改用 <span class="tip">css3</span> 后的核心代码：
            </p>
            <pre class="code"><span class="com">// 设置 css3 样式 </span><br><span class="key">function </span><span class="fun">setCss</span><span class="word">(dis, time)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'transform'</span><span class="word">:</span><span class="str">'translate3d('</span><span class="key"> + </span><span class="word">dis</span><span class="key"> + </span><span class="str">'px,0,0)'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'transition'</span><span class="word">:</span><span class="str">'all '</span><span class="key"> + </span><span class="word">time</span><span class="key"> + </span><span class="str">'ms'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'-webkit-transform'</span><span class="word">:</span><span class="str">'translate3d('</span><span class="key"> + </span><span class="word">dis</span><span class="key"> + </span><span class="str">'px,0,0)'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'-webkit-transition'</span><span class="word">:</span><span class="str">'all '</span><span class="key"> + </span><span class="word"> time </span><span class="key"> + </span><span class="str">'ms'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'-moz-transform'</span><span class="word">:</span><span class="str">'translate3d('</span><span class="key"> + </span><span class="word">dis</span><span class="key"> + </span><span class="str">'px,0,0)'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'-moz-transition'</span><span class="word">:</span><span class="str">'all '</span><span class="key"> + </span><span class="word">time</span><span class="key"> + </span><span class="str">'ms'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'-ms-transform'</span><span class="word">:</span><span class="str">'translate3d('</span><span class="key"> + </span><span class="word">dis</span><span class="key"> + </span><span class="str">'px,0,0)'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">'-ms-transition'</span><span class="word">:</span><span class="str">'all '</span><span class="key"> + </span><span class="word">time</span><span class="key"> + </span><span class="str">'ms'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">;</span><br><span class="flower">}</span><br><span class="com">// 内部元素</span><br><span class="key">var </span><span class="globe">$li</span><span class="key"> = </span><span class="spe">$</span><span class="word">(</span><span class="str">'.li'</span><span class="word">);</span><br><br><span class="com">// 菜单是否完全展开，防止用户重复点击</span><br><span class="key">var </span><span class="globe">menuShow</span><span class="key"> = </span><span class="key">true</span><span class="word">;</span><br><br><span class="spe">$</span><span class="word">(</span><span class="str">'#menu'</span><span class="word">).</span><span class="method">off</span><span class="word">(</span><span class="str">'click'</span><span class="word">).</span><span class="method">on</span><span class="word">(</span><span class="str">'click'</span><span class="word">,</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">dis</span><span class="key"> = </span><span class="key">-</span><span class="spe">$</span><span class="word">(</span><span class="spe">window</span><span class="word">).</span><span class="method">width</span><span class="word">()</span><span class="key"> + </span><span class="word">15 ,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">time</span><span class="key"> = </span><span class="word">500 ;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="globe">menuShow</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="globe">menuShow</span><span class="key"> = false</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( </span><span class="key">!</span><span class="globe">$li</span><span class="word">.</span><span class="method">data</span><span class="word">(</span><span class="str">'menu-show'</span><span class="word">) )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* $li.show()<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.stop()<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.animate({right:-15},500,function(){<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;menuShow = true;<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;})<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.data('menu-show',true);<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;PS：这是原来 animate 方法<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 设置 css 方法</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="globe">$li</span><span class="word">.</span><span class="method">show</span><span class="word">()<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.</span><span class="method">data</span><span class="word">(</span><span class="str">'menu-show'</span><span class="word">,</span><span class="key">true</span><span class="word">)</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">.</span><span class="method">css</span><span class="word">(</span><span class="fun">setCss</span><span class="word">(dis, time) );</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 完全展开后恢复可点击状态</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">setTimeout</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="globe">menuShow</span><span class="key"> = </span><span class="key">true</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,time);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">//这里原来也有一个收起的 animate 不写了 </span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="globe">$li</span><span class="word">.</span><span class="method">show</span><span class="word">()<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.</span><span class="method">css</span><span class="word">(</span><span class="fun">setCss</span><span class="word">(0, time))</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">.</span><span class="method">data</span><span class="word">(</span><span class="str">'menu-show'</span><span class="word">,</span><span class="key">false</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">setTimeout</span><span class="word">(</span><span class="fun">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="globe">menuShow</span><span class="key"> = </span><span class="key">true</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><span class="word">time);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                这里说明一下没有使用 <span class="tip">Add class</span> 和  <span class="tip">Remove class</span> 的原因：在代码中可以通过设置 <span class="code">setCss(dist,time)</span> 自定义展开的距离（<span class="tip">dist</span>）和展开时间（<span class="tip">time</span>）。如此一来，写纯 <span class="tip">css</span> 的效果就差了。
            </p>
            <p>
                如果有更加简洁方便的方法，欢迎交流。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


