<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先要准备一个符合条件的二维码（这个去网上随便搞一个能测试功能就好了）。
            </p>
            <p>
                下面就是使用扫码功能的简单示例：
            </p>
            <p class="minititle">wxml:</p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">view </span><span class="attr">bindtap=</span><span class="str">"scanCode"</span><span class="arrow">&gt;</span><span class="word">点我扫码</span><span class="arrow">&lt;/</span><span class="tage">view</span><span class="arrow">&gt;</span></pre>
            <p class="minititle">js（多处使用可以放到 app.js 里）:</p>
            <pre class="code"><span class="spe">Page</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">scanCode</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">wx</span><span class="word">.</span><span class="method">scanCode</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">success</span><span class="word">:</span><span class="key">function</span><span class="word">(res)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// res 包含了二维码链接地址等信息</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">_url</span><span class="key"> = </span><span class="word">res.</span><span class="spe">result</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">_url</span><span class="word"> = </span><span class="param">_url</span><span class="word">.</span><span class="method">substring</span><span class="word">(</span><span class="param">_url</span><span class="word">.</span><span class="method">substring</span><span class="word">(</span><span class="str">'?'</span><span class="word">);</span><span class="com">// 取参数</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">wx</span><span class="word">.</span><span class="method">navigatorTo</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">url</span><span class="word">:</span><span class="str">'跳转地址'</span><span class="key"> + </span><span class="param">_url</span><span class="com">// 带上之前得到的参数</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">fail</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">complete</span><span class="word">:</span><span class="key">function</span><span class="word">()</span><span class="flower">{}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">)</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span><br><span class="com">// 在跳转到的页面中写 onLoad 方法，可以读到带过来的参数</span><br><span class="com">/* onLoad:function(options){ console.log(options); } */</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


