<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                <span class="code">navigator</span> 有这个属性可以直接判断：
            </p>
            <pre class="code"><span class="key">const</span><span class="param"> cookieEnable</span><span class="key"> = </span><span class="spe">navigator</span><span class="word">.</span><span class="spe">cookieEnabled</span><span class="word">;</span><span class="com"> // true：开启；false：关闭</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


