<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                 实现起来其实很简单，直接上代码吧。
            </p>
            <pre class="code"><span class="fun">function </span><span class="fun">formatNum</span><span class="word">(num)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(num</span><span class="key"> === </span><span class="key">null</span><span class="word">) </span><span class="key">return </span><span class="str">'0'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(num</span><span class="key"> === </span><span class="str">''</span><span class="word">) </span><span class="key">return </span><span class="str">'0'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">let </span><span class="param">_num</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">typeof </span><span class="word">num</span><span class="key">=== </span><span class="str">''string</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">_num</span><span class="key"> = </span><span class="method">parseInt</span><span class="word">(num);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">_num</span><span class="key"> = </span><span class="word">num;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(_num</span><span class="key"> &lt; </span><span class="word">0)</span><span class="key"> return </span><span class="word">0;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(_num</span><span class="key"> &lt; </span><span class="word">9999)</span><span class="key"> return </span><span class="word">_num;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(_num</span><span class="key"> &lt; </span><span class="word">99999999)</span><span class="key"> return </span><span class="spe">Math</span><span class="word">.</span><span class="method">round</span><span class="word">(_num</span><span class="key">/</span><span class="word">10000</span><span class="key">*</span><span class="word">100)</span><span class="key">/</span><span class="word">100</span><span class="key">+</span><span class="str">'万'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(_num</span><span class="key"> &lt; </span><span class="word">999999999999)</span><span class="key"> return </span><span class="spe">Math</span><span class="word">.</span><span class="method">round</span><span class="word">(_num</span><span class="key">/</span><span class="word">100000000</span><span class="key">*</span><span class="word">100)</span><span class="key">/</span><span class="word">100</span><span class="key">+</span><span class="str">'亿'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(_num</span><span class="key"> &lt; </span><span class="word">9999999999999999)</span><span class="key"> return </span><span class="spe">Math</span><span class="word">.</span><span class="method">round</span><span class="word">(_num</span><span class="key">/</span><span class="word">1000000000000</span><span class="key">*</span><span class="word">100)</span><span class="key">/</span><span class="word">100</span><span class="key">+</span><span class="str">'万亿'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(_num</span><span class="key"> &lt; </span><span class="word">99999999999999999999)</span><span class="key"> return </span><span class="spe">Math</span><span class="word">.</span><span class="method">round</span><span class="word">(_num</span><span class="key">/</span><span class="word">10000000000000000</span><span class="key">*</span><span class="word">100)</span><span class="key">/</span><span class="word">100</span><span class="key">+</span><span class="str">'亿亿'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">else return </span><span class="str">'太多了，不玩了'</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
                可以在输入框内试一试效果：
            </p>
            <p>
                <el-input v-model="val_76" size="small" type="number"></el-input>
            </p>
            <p>
                输入的值为：{{input_76(val_76)}}
            </p>
            <p>
                原理：上面的函数将原数据化为 <span class="tip">1.xxxxx</span>的形式，然后乘一百后四舍五入，相当于留下了之后的小数点后两位，然后再除以一百得到和原来一样的数据，并产生了小数点后两点，加上单位即可。
            </p>
            <p>
                若要保留小数点后 <span class="tip">n</span> 位，则先乘 <span class="tip">10 ^ n</span> 后，再除以 <span class="tip">10 ^ n</span> 即可。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


