<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                <span class="tip">$http</span> 有下面两种写法（以 <span class="tip">get</span> 为例，跟 <span class="tip">axios</span> 的写法很类似）：
            </p>
            <pre class="code"><span class="com">/* 写法一 */</span><br><span class="method">$http</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">url</span><span class="word">:</span><span class="str">'请求地址'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">method</span><span class="word">:</span><span class="str">'get'</span><span class="word">,</span><span class="com"> // post</span><br><span class="flower">}</span><span class="word">);</span><br><span class="com">/* 写法二 */</span><br><span class="method">$http</span><span class="word">.</span><span class="method">get</span><span class="word">(</span><span class="str">'请求地址'</span><span class="word">,</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">params</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">a</span><span class="word">:</span><span class="str">'a1'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">b</span><span class="word">:</span><span class="str">'key'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">).</span><span class="method">success</span><span class="word">(</span><span class="key">function</span><span class="word">(</span><span class="word">data, status, config, headers)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 成功的回调</span><br><span class="flower">}</span><span class="word">).</span><span class="method">error</span><span class="word">(</span><span class="key">function</span><span class="word">(</span><span class="word">data)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 错误的回调</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                <span class="tip">post</span> 同样可以这么写。跟着的方法也可以分开写，定义一个变量先存下 <span class="code">$http</span> 对象即可。
            </p>
            <p>
                现在使用上面的写法应该会提示：
            </p>
            <p class="error">
                $http( ... ).success is not a function
            </p>
            <p>
                这是因为，新版的 <span class="tip">Angular</span> 取消了上面写法，改成了 <span class="code">then</span> 和 <span class="code">catch</span>（分别对应了 <span class="code">success</span> 和 <span class="code">error</span>）
            </p>
            <p>
                此外，回调函数也可以写成这样：
            </p>
            <pre class="code"><span class="com">// promise 为保存的请求对象</span><br><span class="param">promise</span><span class="word">.</span><span class="method">then</span><span class="word">(</span><span class="key">function</span><span class="word">( res )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">( res );</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* res 对象中包含了下面这些内容：</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">data:Object,status:200,config:Object,statusText:'OK' */</span><br><span class="flower">}</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


