<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、获取 style 内的属性</p>
            <pre class="code"><span class="com">// 列出所有的 css 表内的属性</span><br><span class="spe">console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="spe">document</span><span class="word">.</span><span class="spe">getElementById</span><span class="word">(</span><span class="str">'demo'</span><span class="word">).</span><span class="spe">style</span><span class="word">);</span></pre>
            <p>
                上例只能获取 <span class="tip">style</span> 中存在的属性
            </p>
            <p class="minititle">二、获取计算后属性</p>
             <pre class="code"><span class="key">var </span><span class="param">$demo</span><span class="key"> = </span><span class="spe">document</span><span class="word">.</span><span class="spe">getElementById</span><span class="word">(</span><span class="str">'demo'</span><span class="word">);</span><br><span class="com">// 列出所有计算后的样式</span><br><span class="spe">console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="spe">window</span><span class="word">.</span><span class="spe">getComputedStyle</span><span class="word">(</span><span class="param">$demo</span><span class="word">));</span></pre>
             <p>如果使用 <span class="tip">jQuery</span> 获取某一项属性就比较简单：</p>
             <pre class="code"><span class="spe">console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="spe">$</span><span class="word">(</span><span class="str">'#demo'</span><span class="word">).</span><span class="method">css</span><span class="word">(</span><span class="str">'color'</span><span class="word">));</span><span class="com"> // 返回最终 color</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


