<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">示例</p>
            <p>下面的代码中将对该问题进行详细说明。</p>
            <pre class="code"><span class="key">var </span><span class="param">time </span><span class="key">= </span><span class="str">'2016-09-13 09:51:57'</span><span class="word">;</span><br><span class="key">var</span> <span class="param">a</span> <span class="key">= new </span><span class="method">Date</span><span class="word">(</span><span class="param"><span class="word">time</span>);</span></pre>
            <p>在现代浏览器中，a 会返回的值是 <span class="code">Tue Sep 13 2016 00:00:00 GMT+0800</span>。而在 IE 中此方法会返回 <span class="tip">NaN</span>，如果不考虑兼容 <span class="tip">IE</span> 的话，没有必要修改写法。事实上，这样的问题，并非只在 <span class="tip">IE</span> 上发生。在 <span class="tip">iOS</span>  系统中，也会返回相同的结果（<span class="tip">NaN</span>）。</p>
            <p class="subtitle">正文</p>
            <p>解决这个问题的方法很简单。将传入的格式做一个修改即可，形式为：<span class="code">new Date(yy,MM,dd,hh,mm,ss)</span>。这样的格式支持 <span class="tip">IE</span>、<span class="tip">iOS</span> 以及主流浏览器，可以完美解决问题。</p>
            <p>如果判断浏览器，是否对时间字符串选择性做处理也可以，应该可以减少一些在现代浏览器上的运算开销。不过个人觉得没有必要这么做，统一传入后面提到的格式即可。示例如下：</p>
            <pre class="code"><span class="key">var </span><span class="param">time </span><span class="key">= </span><span class="str">'2016-09-13 09:51:57'</span><span class="word">;</span><br><span class="key">var </span><span class="param">yy </span><span class="key">= </span><span class="param">time</span><span class="word">.</span><span class="method">substring</span><span class="word">(0,4),</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">MM </span><span class="key">= </span><span class="param">time</span><span class="word">.</span><span class="method">substring</span><span class="word">(5,7)</span><span class="key"> - </span><span class="word">1,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">dd </span><span class="key">= </span><span class="param">time</span><span class="word">.</span><span class="method">substring</span><span class="word">(8,10),</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">hh </span><span class="key">= </span><span class="param">time</span><span class="word">.</span><span class="method">substring</span><span class="word">(11,13),</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">mm </span><span class="key">= </span><span class="param">time</span><span class="word">.</span><span class="method">substring</span><span class="word">(14,16),</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">ss </span><span class="key">= </span><span class="param">time</span><span class="word">.</span><span class="method">substring</span><span class="word">(17,19);</span><br><span class="key">var </span><span class="param">a </span><span class="key">= </span><span class="key">new </span><span class="method">Date</span><span class="word">(</span><span class="param">yy</span><span class="word">,</span><span class="param">MM</span><span class="word">,</span><span class="param">dd</span><span class="word">,</span><span class="param">hh</span><span class="word">,</span><span class="param">mm</span><span class="word">,</span><span class="param">ss</span>);</span></pre>
            <p class="tip">注意：时间月份（MM）不能直接传入，需要先<span class="code">-1</span>。</p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


