<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先，建一个名为 <span class="tip">demo.js</span> 的文件，把 <span class="tip">xxx.vue</span> 里面所有的 <span class="tip">js</span> 内容复制进去。 <span class="tip">xxx.vue</span> 里面 <span class="tip">js</span> 部分这样写：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="key">import</span><span class="spe"> demo</span><span class="key"> from </span><span class="str">'./demo'</span><span class="word">;</span><span class="com">// js 后缀可以省略，写自己的 js 路径</span><br><span class="key">export</span><span class="spe"> default </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">...demo</span><br><span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span></pre>
            <p>
                注意：<span class="code">...</span> 是必须写的。这是 <span class="tip">ES6</span> 的展开运算符，用于展开函数、数组、对象等。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


