<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">select 相关值</p>
            <p>
                1、当前选中的 <span class="code">option</span> 的 <span class="code">text</span> 值；
            </p>
            <p>
                2、当前选中的 <span class="code">option</span> 的 <span class="code">value</span> 值；
            </p>
            <p>
                3、所有 <span class="code">option</span> 的 <span class="code">text</span> 值。
            </p>
            <p>
                下面就直接上代码了。
            </p>
            <p class="minititle">原生 js 写法</p>
            <pre class="code"><span class="com">/* 取得选中值的下标（selectIndex属性） */</span><br><span class="key">var</span> <span class="param">that</span> <span class="key">=</span> <span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'sel'</span><span class="word">);</span><br><span class="key">var</span> <span class="param">index</span> <span class="key">=</span> <span class="param">that</span><span class="word">.</span><span class="spe">selectIndex</span><span class="word">;</span><br><span class="com">/* 利用 index 获取当前的 text 和 value */</span><br><span class="param">that</span><span class="word">.</span><span class="spe">options</span><span class="rect">[</span><span class="param">index</span><span class="rect">]</span><span class="word">.</span><span class="spe">innerText</span><span class="word">;</span><br><span class="param">that</span><span class="word">.</span><span class="spe">options</span><span class="rect">[</span><span class="param">index</span><span class="rect">]</span><span class="word">.</span><span class="spe">value</span><span class="word">;</span></pre>
            <p class="minititle">jQuery 写法</p>
            <pre class="code"><span class="com">/* 注意：option:selected 是 jQuery 的方法 */</span><br><span class="com">/* 1、获得 select 当前选中 option 的 text 值： */</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="key">#sel option:selected</span><span class="str">'</span><span class="word">).</span><span class="method">text</span><span class="word">();</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="key">#sel</span><span class="str">'</span><span class="word">).</span><span class="method">find</span><span class="word">(</span><span class="str">'</span><span class="key">option:selected</span><span class="str">'</span><span class="word">).</span><span class="method">text</span><span class="word">();</span><br><span class="com">/* 2、获得 Select 当前选中 option 的 value 值： */</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="key">#sel option:selected</span><span class="str">'</span><span class="word">).</span><span class="method">val</span><span class="word">();</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="key">#sel</span><span class="str">'</span><span class="word">).</span><span class="method">find</span><span class="word">(</span><span class="str">'</span><span class="key">option:selected</span><span class="str">'</span><span class="word">).</span><span class="method">val</span><span class="word">();</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="key">#sel</span><span class="str">'</span><span class="word">).</span><span class="method">val</span><span class="word">();</span><br><span class="com">/* 3、获得 Select 所有 option 的 text 值：（这个一般没有用） */</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="key">#sel</span><span class="str">'</span><span class="word">).</span><span class="method">text</span><span class="word">();</span></pre>
            <p class="minititle">Vue 和 AngularJS 写法</p>
            <p>
                这两种就比较省心，不写代码了。用 <span class="code">v-model</span> 和 <span class="code">ng-model</span> 绑定一下就好了。当然，<span class="tip">Vue</span> 要在 <span class="code">data</span> 里写上数据 ， <span class="tip">AngularJS</span> 要在 <span class="code">$scope</span> 里写上数据 。 
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


