<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                例如这么一个链接：<span class="tip">https://www.baidu.com/s?wd=你好</span>，把它输入到 IE 浏览器上打开会变成这样：<span class="tip">https://www.baidu.com/s?wd=%E4%BD%A0%E5%A5%BD</span>。而把这个带有 16 进制编码的链接输入到现代浏览器（如 <span class="tip">Chrome</span>）则会自动转回到中文。
            </p>
            <p>
                这样看来似乎浏览器会自己处理自己无法识别的内容，但在 <span class="tip">js</span> 中去做跳转，例如：<span class="tip">https://www.baidu.com/s?wd=你好</span>，在 <span class="tip">IE</span> 浏览器（还是要说一下，是旧版的 <span class="tip">IE</span>，现在应该很少需要做这个处理了）中就会出问题了。它会无法识别。
            </p>
            <p>
                转码的代码很简单：
            </p>
            <pre class="code"><span class="method">encode</span><span class="word">(</span><span class="str">'url'</span><span class="word">);</span><br><span class="com"> // 有时可能需要两次转码。</span><br><span class="method">encode</span><span class="word">(</span><span class="method">encode</span><span class="word">(</span><span class="str">'url'</span><span class="word">));</span></span></pre>
            <p>
                由于现代浏览器也能识别这样的 16 进制编码，所以转码过去以后，并不需要转回来。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


