<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle"></p>
            <p class="minititle">一、安装、构建 Vue webpack 项目</p>
            <p>
                在 <span class="tip">Vue</span> 的官网也有这个教程。在此之前还是需要先安装 <span class="tip">node.js</span>。
            </p>
            <p>
                1、安装 <span class="tip">Vue</span>（不输入版本号就是最新的，也可以指定版本，指定版本不容易出兼容的问题）：
            </p>
            <pre class="cmd">npm install vue@2.1.6</pre>
            <p>
                2、全局安装 <span class="tip">vue-cli</span>：
            </p>
            <pre class="cmd">npm install --global vue-cli</pre>
            <p>
                3、创建一个新项目（名为 <span class="code">my-project</span>）：
            </p>
            <pre class="cmd">npm init webpack my-project</pre>
            <p>
                4、进入项目：
            </p>
            <pre class="cmd">cd my-project</pre>
            <p>
                5、安装依赖：
            </p>
            <pre class="cmd">npm install</pre>
            <p>
                6、接下来运行：
            </p>
            <pre class="cmd">npm run dev</pre>
            <p>
                如果看到了 <span class="tip">Welcome to Your Vue.js App</span> 的页面说明创建成功（项目生成在执行命令的当前目录下）。
            </p>
            <p>
                在项目中新建一个 <span class="tip">page</span> 文件夹，在里面放入项目的页面。具体怎么放比较好可以自己研究看看。后面会介绍到 <span class="tip">Vue Router</span>（路由）。
            </p>
            <p class="minititle">二、安装 element ui</p>
            <p>
                执行如下命令：
            </p>
            <pre class="cmd">npm i element-ui@1.0.9</pre>
            <p>
                安装完成后，在 <span class="tip">my-project/src/APP.vue</span> 下的 <span class="tip">script</span> 中引入 <span class="tip">element ui</span>（加入如下代码）：
            </p>
            <pre class="code"><span class="key">import </span><span class="spe">elementUI </span><span class="key">from</span><span class="str"> 'element-ui'</span><br><span class="key">import </span><span class="str">'element-ui/lib/theme-default/index.css'</span><br><span class="spe">Vue</span><span class="word">.</span><span class="method">use</span><span class="word">(</span><span class="spe">elementUI</span><span class="word">)</span></pre>
            <p>
                这样在别的页面中就可以使用 <span class="tip">element ui</span> 了。
            </p>
            <p>
                使用 <span class="tip">Vue</span> 写单页应用的时候，路由是必不可少的。下面就要引入 <span class="tip">Vue Router</span>。
            </p>
            <p class="minititle">三、Vue Router</p>
            <p>
                1、安装 <span class="tip">Vue Router</span>：
            </p>
            <pre class="cmd">npm install vue-router --save-dev</pre>
            <p>
                2、引入 <span class="tip">Vue Router</span>
            </p>
            <p>
                在 <span class="tip">my-project/src/main.js </span>中引入 <span class="tip">Vue Router</span>（加入如下代码）：
            </p>
            <pre class="code"><span class="key">import </span><span class="spe">Vue </span><span class="key">from</span><span class="str"> 'vue'</span><br><span class="key">import </span><span class="spe">App </span><span class="key">from</span><span class="str"> './App'</span><br><span class="key">import </span><span class="spe">VueRouter </span><span class="key">from</span><span class="str"> 'vue-router'</span><br><span class="key">import </span><span class="spe">routeConfig </span><span class="key">from</span><span class="str"> './router-config'</span><br><span class="com">// 加载路由中间件</span><br><span class="spe">Vue</span><span class="word">.</span><span class="method">use</span><span class="word">(</span><span class="spe">VueRouter</span><span class="word">)</span><br><span class="com">// 定义路由</span><br><span class="key">const</span><span class="param"> router</span><span class="key"> = </span><span class="key">new </span><span class="spe">VueRouter</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">routes</span><span class="word">:</span><span class="spe">routeConfig</span><br><span class="flower">}</span><span class="word">)</span><br><span class="key">new </span><span class="spe">Vue</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">router</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">store</span><span class="word">,</span><span class="com">// 后面引入Vuex用到这个</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">el</span><span class="word">:</span><span class="str">'#app'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">render</span><span class="word">:</span><span class="spe">h</span><span class="key"> => </span><span class="method">h</span><span class="word">(</span><span class="param">app</span><span class="word">)</span><br><span class="flower">}</span><span class="word">)</span></pre>
            <p>
                接下来要把上面写到的 <span class="code">router-config.js</span> 加进来。在 <span class="tip">src</span> 目录下 新建这个文件，在里面进行配置：
            </p>
            <pre class="code"><span class="com">// 引入组件</span><br><span class="key">import </span><span class="spe">activePublic</span><span class="key"> from </span><span class="str">'./page/activePublic/index.vue'</span><br><span class="key">export</span><span class="spe"> default </span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 配置路由，当路径为 '/activePublic',使用组件 activePublic</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">path</span><span class="word">:</span><span class="str">'/activePublic'</span><span class="word">,</span><span class="spe">component</span><span class="word">:</span><span class="spe">activePublic</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="rect">]</span></pre>
            <p class="minititle">四、Vuex</p>
            <p>
                <span class="tip">Vuex</span> 是 <span class="tip">Vue</span> 的状态管理。不会和 <span class="code">data</span> 混在一起，看起来很清爽，而且数据也可以实时更新。采用集中管理应用的所有组件的状态，以相应的规则保证每一种状态以一种可预测的方式发生变化。可以配合一些工具，方便测试。
            </p>
            <p>
                1、安装 <span class="tip">vuex</span>：
            </p>
            <pre class="cmd">npm install vuex --save </pre>
            <p>
                同样在 <span class="tip">my-project/src/main.js</span> 引入：
            </p>
            <pre class="code"><span class="key">import</span><span class="spe"> Vue</span><span class="key"> from </span><span class="str">'vue'</span><br><span class="key">import</span><span class="spe"> Vuex</span><span class="key"> from </span><span class="str">'vuex'</span><br><span class="spe">Vue</span><span class="word">.</span><span class="method">use</span><span class="word">(</span><span class="spe">Vuex</span><span class="word">)</span><br><span class="param">store</span><span class="key"> = </span><span class="key">new </span><span class="spe">Vuex</span><span class="word">.</span><span class="method">Store</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">state</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 状态值</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">count</span><span class="word">:</span><span class="word">1</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">getters</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// store 的计算属性，这些方法也可以被其他方法调用，属于全局函数</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">mutations</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 改变 store.state 的值的唯一方法</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">increment</span><span class="word">(state)</span><span class="flower">{</span><span class="word">state.</span><span class="spe">count</span><span class="key"> ++</span><span class="word"></span><span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                注册好的 <span class="code">mutation</span>、<span class="code">handler</span> 方法不能直接调用，需要使用 <span class="code">store.commit('increment')</span>这样的方式去调用（<span class="code">handler</span> 没怎么用过，感觉不怎么用得到）。
            </p>
            <p class="minititle">五、一些 Vue 的插件</p>
            <p>
                编辑器： vue-quil-editor：<a target="_blank" href="https://github.com/surmon-china/vue-quill-editor">https://github.com/surmon-china/vue-quill-editor</a>；
            </p>
            <p>
                markdown Vue-SimpleMDE：<a target="_blank" href="https://github.com/F-loat/vue-simplemde">https://github.com/F-loat/vue-simplemde</a>
            </p>
            <p>
                图表：echarts
            </p>
            <p>
                支持裁剪的上传图片插件：Vue-Core-Image-Upload：<a target="_blank" href="https://github.com/Vanthink-UED/vue-core-image-upload">https://github.com/Vanthink-UED/vue-core-image-upload</a>
            </p>
            <p class="minititle">六、Vue 调试工具</p>
            <p>
                介绍一下一款不错的 <span class="tip">Vue</span> 调试工具（<span class="tip">Chrome</span> 插件）：<span class="tip">Vue Devtools</span>。
            </p>
            <p>
                首先，在 <span class="tip">Chrome</span> 更多工具-扩展程序-获取更多-搜索并安装。
            </p>
            <p>
                安装好之后，可能会不能用，如果是这样的话，在路径 <span class="tip">C:\Users\Administrator\AppData\Local\Google\Chrome\User Data\Default\Extensions\nhdogjmejiglipccpnnnanhbledajbpd\3.0.6_0</span>（一般是这个路径）。找到 <span class="tip">mainifest.json</span> 文件，把 <span class="tip">background</span> 下的 <span class="tip">persistent</span> 值改为 <span class="tip">true</span>。
            </p>
            <p>
                使用方法，<span class="tip">F12</span> 打开控制台，可以看到有一个 <span class="tip">Vue</span> 的选项卡。
            </p>
            <p>
                如果检测到页面是 <span class="tip">Vue</span> 的话，右上角的图标也会变为彩色，否则是不可用的。
            </p>
            <p>
                值得一提的是，这个插件挺占用内存的。可以把数据记录的小红点去掉，需要的时候再打开，这样会好很多。不过还是挺占资源的。
            </p>
            <p>
                不过这个东西真的挺好用。组件的结构，组件中的数据，<span class="tip">Vuex</span> 状态、事件都一目了然。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


