<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">Linux 小白笔记</p>
            <el-table :data="table_98" stripe>
                <el-table-column label="命令" prop="name"></el-table-column>
                <el-table-column label="说明" prop="intro"></el-table-column>
            </el-table>
            <p>
                聚沙成塔，积少成多。加油！这些命令还带有很多参数，不列举了。以后再完善，现在有点懵。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


