<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">1、普通选择器 mode="selector"</p>
            <el-table :data="table_66_1" stripe>
                <el-table-column label="属性名" prop="name"></el-table-column>
                <el-table-column label="类型" prop="type"></el-table-column>
                <el-table-column label="默认值" prop="def"></el-table-column>
                <el-table-column label="说明" prop="intro"></el-table-column>
            </el-table>
            <p class="minititle">2、时间选择器 mode="time"</p>
            <el-table :data="table_66_2" stripe>
                <el-table-column label="属性名" prop="name"></el-table-column>
                <el-table-column label="类型" prop="type"></el-table-column>
                <el-table-column label="默认值" prop="def"></el-table-column>
                <el-table-column label="说明" prop="intro"></el-table-column>
            </el-table>
            <p class="minititle">3、日期选择器 mode="date"</p>
            <el-table :data="table_66_3" stripe>
                <el-table-column label="属性名" prop="name"></el-table-column>
                <el-table-column label="类型" prop="type"></el-table-column>
                <el-table-column label="默认值" prop="def"></el-table-column>
                <el-table-column label="说明" prop="intro"></el-table-column>
            </el-table>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


