<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                下面的列表列出了它们的类型和值：
            </p>
            <el-table
                    :data="table_37"
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="name"
                        label="对象"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="type"
                        label="类型"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="value"
                        label="值">
                </el-table-column>
            </el-table>
            <p class="minititle">说明</p>
            <p>
                1、假值之间相等，空值之间相等；
            </p>
            <p>
                2、假值是一个有效的对象，可以用 <span class="code">toString()</span> 等类型相关的方法，而空值不行。空值使用这种方法会抛出异常；
            </p>
            <p>
                3、空值不能使用 <span class="code">toString()</span> 方法，但是可以使用 <span class="code">String</span> 方法构造（构造后就变成了相应的字符串）。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


