<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                使用方法很简单，直接展示一个简单的例子：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">input </span><span class="attr">type=</span><span class="str">"file" </span><span class="attr">accept=</span><span class="str">"MIME TYPE" </span><span class="attr">name=</span><span class="str">"fileUp"</span><span class="arrow">/></span></pre>
            <p>
                <span class="code">MIME TYPE</span> 是填入的上传的类型
            </p>
            <p>
                下面是所有支持的文件类型的链接，有需要可以去看看：<a href="http://www.iana.org/assignments/media-types/media-types.xhtml" target="_blank">http://www.iana.org/assignments/media-types/media-types.xhtml</a>
            </p>
            <p>
                此外，要提一点，这个方法不支持 <span class="tip">IE9</span> 以前的浏览器，而这个属性只是为选择添加了分类，并非真的不能上传其他类型的文件。做简单的处理可以，要完全限制还是要用 <span class="tip">js</span> 去控制。
            </p>
            <p>
                <span class="tip">W3School</span> 上提示说：请避免使用该属性。应该在服务器端验证文件上传。
            </p>
            <p>
                以后研究过真正限制文件类型上传的功能后，会在此加上相应的传送门。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


