<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <el-table :data="table_62">
                <el-table-column prop="name" label="事件名称" min-width="54">
                </el-table-column>
                <el-table-column prop="type" label="事件类型" min-width="34">
                </el-table-column>
                <el-table-column prop="intro" label="描述">
                </el-table-column>
            </el-table>
            <p>
                如果对于新版的 <span class="tip">jQuery</span> 不适用，请以相应的文档为准。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


