<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、安装 Grunt</p>
            <p>
                <span class="tip">Grunt</span> 是基于 <span class="tip">node.js</span> 使用的，<span class="tip">Grunt@0.4.x</span> 必须配合 <span class="tip">node.js@0.8.0</span> 或更高的版本使用。如果已经安装过 <span class="tip">node.js</span> 的话，就可以开始安装 <span class="tip">Grunt</span> 了。如果之前安装过不可用或版本过旧的 <span class="tip">Grunt</span> 的话，可以先将其卸载。
            </p>
            <p>
                卸载 <span class="tip">Grunt</span> 的命令如下：
            </p>
            <pre class="cmd"> npm uninstall -g grunt</pre>
            <p>
                在介绍安装 <span class="tip">Grunt</span> 之前，要先简单介绍一下如何使用 <span class="tip">cmd</span>。打开 <span class="tip">cmd</span> 的方法是 <span class="code">Win+R</span>（Windows 系统），输入 <span class="code">cmd</span> 后进入 <span class="tip">command</span> 界面。执行 <span class="tip">Grunt</span> 安装命令的当前目录会作为其文件的存放处，所以安装的时候要先进入自己希望安装的目录。进入某一个磁盘只需输入盘符回车即可，如：<span class="tip">d:</span>；进入某一目录方法：<span class="code">cd dirName1/dirName2</span>；返回上一级方法：<span class="code">cd ..</span>
            </p>
            <p>
                下面介绍安装的命令：
            </p>
            <pre class="cmd"> npm install -g grunt-cli</pre>
            <p>
                <span class="code">-g</span> 代表的是全局安装，即命令可以在安装目录外使用。<span class="tip">Grunt</span> 有两个版本，服务器版本（Grunt）、客户端版本（Grunt-cli）。安装好了 <span class="tip">Grunt</span> 并不代表安装好了 <span class="tip">Grunt</span>。
            </p>
            <p>
                <span class="tip">Grunt-cli</span> 执行的任务就是调用 <span class="tip">Gruntfile</span> 在同一目录中的 <span class="tip">Grunt</span>，由于目录的限制，使得 <span class="tip">Grunt</span> 可以同时安装多个版本。
            </p>
            <p>
                <span class="tip">Grunt</span> 使用模块化结构，安装需要的相应模块后才可以正常使用。除此之外，还需要完成 <span class="tip">Grunt</span> 的配置才可以真正开始使用。
            </p>
            <p class="minititle">二、配置 Grunt</p>
            <p>
                首先在安装的根目录下创建项目文件夹。可使用命令 <span class="code">mkdir projectName</span> 创建，也可以手动新建。进到新建的 <span class="tip">projectName</span> 下，创建 <span class="tip">package.json</span> 和 <span class="tip">Gruntfile.js</span>。
            </p>
            <p>
                下面介绍一下这两个文件的命名、作用以及如何创建：
            </p>
            <p>
                 <span class="tip">package.json</span>被 <span class="tip">npm</span> 用于存储项目的元数据，以便将此项目发布为 <span class="tip">npm</span> 模块;
            </p>
            <p>
                 <span class="tip">Gruntflie.js</span>（或命名为 <span class="tip">Gruntfile.coffee</span> 用来配置或定义任务（<span class="tip">task</span>）并加载 <span class="tip">Grunt</span> 插件。
            </p>
            <p class="minititle">创建 package.json</p>
            <p>
                可以使用命令创建，也可手动创建。使用的命令如下（根据提示填写即可）：
            </p>
            <pre class="cmd"> npm init</pre>
            <p>
                下面是一个创建好的示例
            </p>
            <pre class="code"><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">name</span><span class="word">:</span> <span class="str">"GruntDemo"</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 模块名称：只能包含小写字母数字和中划线，如果为空则使用项目文件夹名称代替 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">version</span><span class="word">:</span> <span class="str">"0.0.0"</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 版本号 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">description</span><span class="word">:</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 描述：会在npm搜索列表中显示 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">entry point</span><span class="word">:</span> <span class="str">"index.js"</span>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 模块入口文件 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">test command<span class="word">:</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 测试脚本 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">git repository</span><span class="word">:</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* git仓库地址 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">keywords</span><span class="word">:</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 关键字：用于npm搜索，多个关键字用空格分开 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;author:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 作者 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">license</span><span class="word">:</span> <span class="str">"BSD-2-Clause"&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 开源协议 */</span><br>}</span></span></pre>
            <p>
                更多的配置参数可以参照 <span class="tip">package.json</span> 官方文档：<a target="_blank" href="https://docs.npmjs.com/files/package.json">docs.npmjs.com/files/package.json</a>
            </p>
            <p class="minititle">Gruntfile.js 配置</p>
            <p>
                <span class="tip">Gruntfile.js</span> 主要有三块内容：任务配置代码；插件加载代码；任务注册代码。具体的划分及作用见下面示例（下面只是一个简单的例子）：
            </p>
            <pre class="code"><span class="spe">module</span><span class="word">.</span><span class="spe">exports</span> <span class="key">=</span> <span class="key">function</span><span class="word">(grunt)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 配置任务参数（任务配置代码，写在 grunt.initConfig() 中） */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">grunt</span><span class="word">.</span><span class="spe">initConfig</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">pkg</span><span class="word">:</span><span class="spe">grunt</span><span class="word">.</span><span class="spe">file</span><span class="word">.</span><span class="spe">readJSON</span><span class="word">(</span><span class="str">'package.json'</span><span class="word">), </span><span class="com">/* 读取 package.json 文件，并获取信息，方便后面的任务应用，提高灵活性 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">cssmin</span><span class="word">:</span><span class="flower">{ </span><span class="com">/* 这个变量名是要使用的模块，名字是固定的，对应的参数也固定。使用不同的模块需要去查看文档上的说明 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">combine</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">files</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">'css/demo.css'</span><span class="word">:</span><span class="rect">[</span><span class="str">'css/*.css'</span><span class="rect">]</span> <span class="com">/* 合并指定文件 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">minify</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">options</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">keepSpecialComments</span><span class="word">:</span><span class="word">0,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">banner</span><span class="word">:</span><span class="str">'/* minified css file */'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">files</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">'css/master.min.css'</span><span class="word">:</span><span class="rect">[</span><span class="str">'css/master.css'</span><span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 插件加载（加载 cssmin 模块）*/</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">grunt</span><span class="word">.</span><span class="method">loadNpmTasks</span><span class="word">(</span><span class="str">'grunt-contrib-cssmin'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 注册任务。自定义任务：通过定义 default 任务，可以让 Grunt 默认执行一个或多个任务 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">grunt</span><span class="word">.</span><span class="method">registerTask</span><span class="word">(</span><span class="str">'default'</span><span class="word">,</span><span class="rect">[</span><span class="str">'mincss'</span><span class="rect">]</span><span class="word">);</span><br><span class="flower">}</span></pre>
            <p>
                配置好了这两个文件，加载模块代码中用到的相应模块也是必不可少的。接下来就是安装这些要用到的模块。
            </p>
            <p>（1）首先最新版的安装 <span class="tip">Grunt</span>（之前安装的是 <span class="tip">Grunt-cli</span>，现在这个就是对应之前的卸载的命令）：</p>
            <pre class="cmd"> npm install grunt --save-dev</pre>
            <p>（2）接着是安装所需的组件，上面用到了压缩 <span class="tip">css</span>，用到的是下面这个组件：</p>
            <pre class="cmd"> npm install grunt-contrib-mincss --save-dev</pre>
            <p>（3）如果要安装指定版本的 <span class="tip">Grunt</span> 可以使用如下命令：(<span class="tip">VERSION</span> 为想要的版本号)</p>
            <pre class="cmd"> npm install grunt@VERSION --save-dev</pre>
            <p>（4）常用的组件安装命令</p>
            <p>
                合并文件：<span class="code">npm install grunt-contrib-concat --save-dev</span>
            </p>
            <p>
                语法检查：<span class="code">npm install grunt-contrib-jshint --save-dev</span>
            </p>
            <p>
                scss 编译：<span class="code">npm install grunt-contrib-sass --save-dev</span>
            </p>
            <p>
                压缩 js：<span class="code">npm install grunt-contrib-uglify --save-dev</span>
            </p>
            <p>
                监听变化：<span class="code">npm install grunt-contrib-watch --save-dev</span>
            </p>
            <p>
                服务器：<span class="code">npm install grunt-contrib-connect --save-dev</span>
            </p>
            <p class="minititle">使用 Grunt</p>
            <p>
                安装、配置、加载组件都完成之后，当然是使用了。
            </p>
            <p>首先在 <span class="tip">cmd</span> 中进入到 <span class="tip">Grunt</span> 的安装目录下（建议目录设的浅一点，用起来方便），然后输入命令（<span class="code">mincss</span> 是我在 <span class="tip">Gruntfile.js</span> 中的注册任务名，如果不输入任务名会默认执行所有注册过的任务）：</p>
            <pre class="cmd"> grunt mincss</pre>
            <p>
                执行完之后会提示成功或者失败。上面的效果是把指定的 <span class="tip">css</span> 文件进行压缩。
            </p>
            <p>
                好了，简单的介绍就到这里了。更多的功能自己摸索摸索吧。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


