<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                一开始直接把 <span class="tip">API</span> 上的 <span class="code">el-popover</span> 组件直接放到 <span class="code">v-if</span> 里面去，发现并不能生效。
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">id=</span><span class="str">"app"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">v-for=</span><span class="str">"item in visibleArr"</span><span class="attr"> :key=</span><span class="str">"item.name"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-popover</span><span class="attr"> :ref=</span><span class="str">"item.name"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">el-popover</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-button </span><span class="attr">v-popover:</span><span class="word">item.name</span><span class="arrow">&gt;</span><span class="word">{{</span><span class="str">'点击'</span><span class="word">+item.name}}</span><span class="arrow">&lt;/</span><span class="tag">el-button</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="key">new</span><span class="spe"> Vue</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">visibleArr</span><span class="word">:</span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">name</span><span class="word">:</span><span class="str">'a'</span><span class="word">,</span><span class="spe">state</span><span class="word">:</span><span class="key">false</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">name</span><span class="word">:</span><span class="str">'b'</span><span class="word">,</span><span class="spe">state</span><span class="word">:</span><span class="key">false</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">name</span><span class="word">:</span><span class="str">'c'</span><span class="word">,</span><span class="spe">state</span><span class="word">:</span><span class="key">false</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">name</span><span class="word">:</span><span class="str">'d'</span><span class="word">,</span><span class="spe">state</span><span class="word">:</span><span class="key">false</span><span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span></pre>
            <p>
                这种写法看起来是应该可以的，每一个按钮都一一都有对应的名字和状态，实际上这样做，一个都没有用。也没有报错。
            </p>
            <p>
                仔细读了文档，发现传入值的方法还有一种 <span class="code">slot</span>，文档里藏得比较隐秘。直接贴代码看吧：
            </p>
         
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">el-table</span><span class="attr"> :data=</span><span class="str">"numberData"</span><span class="attr"> stripe</span><span class="attr"> style=</span><span class="str">"width:100%"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-table-column</span><span class="attr"> prop=</span><span class="str">"no"</span><span class="attr"> label=</span><span class="str">"编号"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">el-table-column</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-table-column</span><span class="attr"> prop=</span><span class="str">"name"</span><span class="attr"> label=</span><span class="str">"姓名"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">el-table-column</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-table-column</span><span class="attr"> prop=</span><span class="str">"age"</span><span class="attr"> label=</span><span class="str">"年龄"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">el-table-column</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-table-column</span><span class="attr"> label=</span><span class="str">"操作"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">template</span><span class="attr"> scope=</span><span class="str">"scope"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-popover</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">placement=</span><span class="str">"top"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">title=</span><span class="str">"警告"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">width=</span><span class="str">"200"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">v-model=</span><span class="str">"scope.row.state"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">p</span><span class="arrow">&gt;</span><span class="word">信息删除后将无法恢复！</span><span class="arrow">&lt;/</span><span class="tag">p</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span><span class="attr"> style=</span><span class="str">"text-align: right; margin: 0"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-button</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">size=</span><span class="str">"mini"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">type=</span><span class="str">"text"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">@click=</span><span class="str">"scope.row.state = false"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">取消</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">el-button</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-button</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">size=</span><span class="str">"mini"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">type=</span><span class="str">"primary"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">@click=</span><span class="str">"del(delName)"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">确定</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">el-button</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">el-button</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">size=</span><span class="str">"mini"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">slot=</span><span class="str">"reference"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">type=</span><span class="str">"danger"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="attr">@click=</span><span class="str">"chooseDel(scope.row.name)"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">删除</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">el-button</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">el-popover</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">el-table-column</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">el-table</span><span class="arrow">&gt;</span></pre>
            <p>
                直接复制的话记得把 <span class="code">@click</span> 去掉，或者加一下 <span class="code">methods</span>。最主要的就是属性：<span class="code">slot="reference"</span>。下面是就是这个示例：
            </p>
            <el-table :data="popover_93" stripe style="width:100%">
                <el-table-column prop="no" label="编号">
                </el-table-column>
                <el-table-column prop="name" label="姓名">
                </el-table-column>
                <el-table-column prop="age" label="年龄">
                </el-table-column>
                <el-table-column label="操作">
                    <template scope="scope">
                        <el-popover
                                placement="top"
                                title="警告"
                                width="200"
                                v-model="scope.row.state">
                            <p>信息删除后将无法恢复！</p>
                            <div style="text-align: right; margin: 0">
                                <el-button size="mini" type="text" @click="scope.row.state = false">
                                    取消
                                </el-button>
                                <el-button type="primary" size="mini" @click="del_93(name_93)">
                                    确定
                                </el-button>
                            </div>
                            <el-button type="danger" size="mini" slot="reference" @click="click_93(scope.row.name)">
                                删除
                            </el-button>
                        </el-popover>
                    </template>
                </el-table-column>
            </el-table>
           
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


