<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
           <p>
               获取的方法非常简单：
           </p>
           <pre class="code"><span class="key">var </span><span class="param">app</span><span class="key"> = </span><span class="method">getApp</span><span class="word">();</span></pre>
           <p>
               <span class="code">app</span> 中就包含 <span class="tip">app.js</span> 中的 <span class="code">App({ })</span> 全部内容。
           </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


