<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先就上一下它的地址：<a target="blank" href="https://github.com/SheetJS/js-xlsx">https://github.com/SheetJS/js-xlsx</a>。这里也有比较详尽的 <span class="tip">demo</span> 和 <span class="tip">API</span>，可以去看看。
            </p>
            <p>
                如果只是简单使用，看看我下面给出的示例就差不多了。
            </p>
            <p class="minititle">一、引入和安装</p>
            <p>
                直接引入：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">script</span><span class="attr"> src=</span><span class="str">"dist/xlsx.full.min.js"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span></pre>
            <p>
                安装 <span class="tip">node_modules</span>：
            </p>
            <pre class="cmd">npm install xlsx</pre>
            <p class="minititle">二、简单示例</p>
            <p>
                下面是一个简单的示例和效果演示：
            </p>
            <p class="minititle">导入</p>
            <p>
                源码（复制可以直接用，改改应该就能满足需求）：
            </p>
            <pre class="code"><span class="com">&lt;!DOCTYPE html&gt;</span><br><span class="arrow">&lt;</span><span class="tag">html</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">head</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">meta</span><span class="attr"> charset=</span><span class="str">"UTF-8"</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">title</span><span class="arrow">&gt;</span><span class="word">demo</span><span class="arrow">&lt;/</span><span class="tag">title</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">script</span><span class="attr"> src=</span><span class="str">"http://oss.sheetjs.com/js-xlsx/xlsx.full.min.js"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">head</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">body</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">input</span><span class="attr"> type=</span><span class="str">"file"</span><span class="attr"> onchange=</span><span class="str">"importExcel(this)"</span><span class="arrow">/&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">&lt;!-- 存放表格数据的容器 --&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span><span class="attr"> id="demo"</span><span class="arrow">&gt;&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="com">/*</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">FileReader共有4种读取方法：</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">1.readAsArrayBuffer(file)：将文件读取为ArrayBuffer。</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">2.readAsBinaryString(file)：将文件读取为二进制字符串</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">3.readAsDataURL(file)：将文件读取为Data URL</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">4.readAsText(file, [encoding])：将文件读取为文本，encoding 缺省值为 'UTF-8'</span><br><span class="com">*/</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">let </span><span class="param">wb</span><span class="word">;</span><span class="com"> // 读取后的数据</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">let </span><span class="param">rABS</span><span class="key"> = false</span><span class="word">;</span><span class="com"> // 是否将文件读取为二进制字符串</span><br><br><span class="com">// 导入函数</span><br><span class="key">function</span><span class="fun"> importExcel</span><span class="word">(obj)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">!</span><span class="word">obj.</span><span class="spe">files</span><span class="word">)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return false</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 可以控制传入文件大小，单位用的是 kb（下面的文件 size 单位是 b）</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">const </span><span class="param">IMPORTFILE_MAXSIZE</span><span class="key"> = </span><span class="word">1024 ;</span><br><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">const</span><span class="param"> suffix</span><span class="key"> = </span><span class="word">obj.</span><span class="spe">files</span><span class="rect">[</span><span class="word">0</span><span class="rect">]</span><span class="word">.</span><span class="spe">name</span><span class="word">.</span><span class="method">split</span><span class="word">(</span><span class="str">'.'</span><span class="word">)</span><span class="rect">[</span><span class="word">1</span><span class="rect">]</span><span class="word">;</span><span class="com"> // 后缀名</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param">suffix</span><span class="key"> !== </span><span class="str">'xls'</span><span class="key"> && </span><span class="param">suffix</span><span class="key"> !== </span><span class="str">'xlsx'</span><span class="word">)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">alert</span><span class="word">(</span><span class="str">'导入文件格式不正确'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return false</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(obj.</span><span class="spe">files</span><span class="rect">[</span><span class="word">0</span><span class="rect">]</span><span class="word">.</span><span class="spe">size</span><span class="key"> / </span><span class="word">1024 </span><span class="key"> &gt; </span><span class="param">IMPORTFILE_MAXSIZE</span><span class="word">)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">alert</span><span class="word">(</span><span class="str">'导入表格文件的大小不能大于 1M'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">const </span><span class="param">f</span><span class="key"> = </span><span class="word">obj.</span><span class="spe">files</span><span class="rect">[</span><span class="word">0</span><span class="rect">]</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">const </span><span class="param">reader</span><span class="key"> = new</span><span class="fun"> FileReader</span><span class="word">();</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">reader</span><span class="word">.</span><span class="spe">onload</span><span class="key"> = </span><span class="key">function</span><span class="word">(e) </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">const </span><span class="param">data</span><span class="key"> = </span><span class="word">e.</span><span class="spe">target</span><span class="word">.</span><span class="spe">result</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param">rABS</span><span class="word">)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">wb</span><span class="key"> = </span><span class="spe">XLSX</span><span class="word">.</span><span class="method">read</span><span class="word">(</span><span class="method">btoa</span><span class="word">(</span><span class="method">fixdata</span><span class="word">(</span><span class="param">data</span><span class="word">)),</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">type</span><span class="word">:</span><span class="str">'base64'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">wb</span><span class="key"> = </span><span class="spe">XLSX</span><span class="word">.</span><span class="method">read</span><span class="word">(</span><span class="param">data</span><span class="word">,</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">type</span><span class="word">:</span><span class="str">'binary'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// wb.SheetNames[0]是获取Sheets中第一个Sheet的名字</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// wb.Sheets[Sheet名]获取第一个Sheet的数据</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'demo'</span><span class="word">).</span><span class="spe">innerHTML</span><span class="key"> = </span><span class="spe">JSON</span><span class="word">.</span><span class="method">stringify</span><span class="word">( </span><span class="spe">XLSX</span><span class="word">.</span><span class="spe">utils</span><span class="word">.</span><span class="method">sheet_to_json</span><span class="word">(</span><span class="param">wb</span><span class="word">.</span><span class="spe">Sheets</span><span class="rect">[</span><span class="param">wb</span><span class="word">.</span><span class="spe">SheetNames</span><span class="rect">[</span><span class="word">0</span><span class="rect">]]</span><span class="word">) );</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param">rABS</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">reader</span><span class="word">.</span><span class="method">readAsArrayBuffer</span><span class="word">(</span><span class="param">f</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">reader</span><span class="word">.</span><span class="method">readAsBinaryString</span><span class="word">(</span><span class="param">f</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><span class="com">// 文件流转 BinaryString</span><br><span class="key">function</span><span class="fun"> fixdata</span><span class="word">(data)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">let </span><span class="param">str</span><span class="key"> = </span><span class="str">''</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">len</span><span class="key"> = </span><span class="word">0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">const </span><span class="param">size</span><span class="key"> = </span><span class="word">10240;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">for</span><span class="word">( ; </span><span class="param">len</span><span class="key"> &lt; </span><span class="param">data</span><span class="word">.</span><span class="spe">byteLength</span><span class="key"> / </span><span class="param">size</span><span class="word">; </span><span class="key">++ </span><span class="param">len</span><span class="word">)</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">str</span><span class="key"> += </span><span class="spe">String</span><span class="word">.</span><span class="spe">fromCharCode</span><span class="word">.</span><span class="method">apply</span><span class="word">(</span><span class="key">null</span><span class="word">, </span><span class="key">new </span><span class="method">Uint8Array</span><span class="word">(</span><span class="param">data</span><span class="word">.</span><span class="method">slice</span><span class="word">(</span><span class="param">len</span><span class="key"> * </span><span class="param">size</span><span class="word">, </span><span class="param">len</span><span class="key"> * </span><span class="param">size</span><span class="key"> + </span><span class="param">size</span><span class="word"> )));</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">str</span><span class="key"> += </span><span class="spe">String</span><span class="word">.</span><span class="spe">fromCharCode</span><span class="word">.</span><span class="method">apply</span><span class="method">(</span><span class="key">null</span><span class="word">, </span><span class="key">new </span><span class="method">Uint8Array</span><span class="word">(</span><span class="param">data</span><span class="word">.</span><span class="method">slice</span><span class="word">(</span><span class="param">len</span><span class="key"> * </span><span class="param">size</span><span class="word"> )));</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="param">str</span><span class="word">;</span><br><span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">body</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">html</span><span class="arrow">&gt;</span><br></pre>
