<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                小程序不能进行 <span class="tip">DOM</span> 操作，所有的样式变化均由 <span class="code">data</span> 来驱动。虽然使用起来感觉受到了重重限制，用习惯了以后感觉还行。
            </p>
            <p>
                下面是一个比较简单的小示例（点击按钮以后加一个背景色）：
            </p>
            <p class="minititle">wxml:</p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">view</span> <span class="attr">bindtap=</span><span class="str">"clickMe"</span> <span class="attr">class=</span><span class="str">"{{click}}"</span><span class="arrow">&gt;</span><span class="word">clickMe</span><span class="arrow">&lt;/</span><span class="tag">view</span><span class="arrow">&gt;</span></pre>
            <p class="minititle">wxss:</p>
            <pre class="code"><span class="key">.clicked</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">background</span><span class="word">:</span><span class="key">#999</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p class="minititle">js:</p>
            <pre class="code"><span class="spe">Page</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="word">:</span><span class="key">function</span><span class="word">(event)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">that</span><span class="key"> = </span><span class="param">this</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">that</span><span class="word">.</span><span class="method">setData</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">clicked</span><span class="word">:</span><span class="str">'clicked'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


