<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先介绍一下 <span class="code">data-*</span> 的用途。它可以用来传递字符串、数组、对象、函数等。
            </p>
            <p>
                保存下来的字符串可以作为全局变量读取状态，对象和数组可以用于保存更多的数据。
            </p>
            <p>
                而直接保存一个函数可以用于两个闭包函数之间公用同一个函数。将函数以 <span class="code">data-fun="myFun"</span> 的方式存入一个变量中，然后在另一个闭包函数中取出 <span class="code">data-fun</span> 就可以使用这个函数了。
            </p>
            <p>
                存储数据的代码如下：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">id=</span><span class="str">"demo" </span><span class="attr">data-test=</span><span class="str">"test"</span><span class="arrow">></span><span class="word">demoDiv</span><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">"#demo"</span><span class="word">).</span><span class="method">data</span><span class="word">(</span><span class="str">"test"</span><span class="word">,</span><span class="str">"name"</span><span class="word">);</span><span class="com"> // 设置为 name</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="spe">$</span><span class="word">(</span><span class="str">"#div1"</span><span class="word">).</span><span class="method">data</span><span class="word">(</span><span class="str">"test"</span><span class="word">);</span><span class="com"> // 打出 name</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">></span></pre>
            <p>
                执行上述代码后，检查 <span class="tip">html</span> 结构中会发现，仍然显示的是 <span class="code">data-test="test"</span>， 
            </p>
            <p>
                <span class="code">data()</span> 方法并没有真的改变 <span class="code">data-*</span> 的属性值，而是在缓存中设置了这个值，获取的值也是从缓存中读出来的。
            </p>
            <p>
                因此在页面的元素节点中看到的值没有发生变化，实际上用 <span class="tip">jQuery</span> 再获取得到的已经是新设置的值了（新增一个值也是一样，看不到相应的属性，但实际能获取到）。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


