<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                在 <span class="code">a</span> 包裹的情况下，点击 <span class="code">div</span> 中的任何内容都会触发跳转，会导致“编辑”的点击出问题。这时，就需要使用 <span class="code">preventDefault()</span> 来阻止浏览器的默认行为。如下例：
            </p>
            <a target="_blank" href="/#/home">
                <div class="edit">
                    <el-button type="primary" size="small" @click="count_21++">点击</el-button> 
                    <p>点击次数：{{count_21}}</p>               
                </div>
            </a>
            上例中点击 <span class="code">button</span>，在触发 <span class="code">button</span> 的点击事件的同时，会触发 <span class="code">a</span> 的跳转。其代码如下：
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">a</span><span class="attr"> class=</span><span class="str">"/home"</span><span class="attr"> target=</span><span class="str">"_blank"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span><span class="attr"> class=</span><span class="str">"edit"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">button</span><span class="attr"> onclick=</span><span class="str">"myClick"</span><span class="arrow">></span><span class="word">点击</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">style</span><span class="arrow">></span><br><span class="key">.edit</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">height</span><span class="word">:</span><span class="str">100px</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">border</span><span class="word">:</span><span class="str">1px solid </span><span class="key">#eee</span><span class="word">;</span><br><span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">style</span><span class="arrow">></span></pre>
            <p>
                而只需要在点击事件中增加一句 <span class="code">e.preventDefault()</span> 即可阻止 <span class="code">a</span> 的默认行为，从而触发点击事件，而不发生跳转。（<span class="code">e</span> 为传入的形参）。效果如下：
            </p>
            <a target="_blank" href="/#/home">
                <div class="edit">
                    <el-button type="primary" size="small" @click.prevent="count_21++">点击</el-button>
                    <p>点击次数：{{count_21}}</p>        
                </div>
            </a>
            <p>上面的两个例子可以点击做效果测试。另外本例中的代码实际上是用了 <span class="code">vue</span> 的写法：<span class="code">@click.prevent="count++"</span>用是这个写法，<span class="code">e.preventDefault()</span> 是原生 js 的写法。</p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>
<style>
    .edit{
        height:100px;
        border:1px solid #eee;
    }
    .edit .el-button{
        margin:15px;
    }
</style>


