<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                废话不多说，直接上个例子。
            </p>
            <pre class="code"><span class="spe">$</span><span class="word">(</span><span class="str">'</span><span class="key">#myElement</span><span class="str">'</span><span class="word">).</span><span class="method">click</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var</span> <span class="param">that</span> <span class="key">=</span> <span class="key">this</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">setTimeout</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="param">that</span><span class="word">).</span><span class="method">addClass</span><span class="word">(</span><span class="str">'aNewClass'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><span class="word">1000);</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                上例中，是一个按钮的点击事件，效果为 <span class="tip">1s</span> 后触发给该按钮新增一个类。这里在计时器中直接使用 <span class="code">this</span> 的话，指向的是计时器的函数，我们希望得到的效果是指向按钮。
            </p>
            <p>
                这就需要在点击事件最开始（计时器函数外）先定义一个变量预存 <span class="code">this</span> 变量。这也是常用的做法。在使用 <span class="tip">jQuery</span> 时，有一个 <span class="code">$.proxy()</span> 方法可供使用。还是直接上代码。
            </p>
            <pre class="code"><span class="spe">$</span><span class="word">(</span><span class="str">'#myElement'</span><span class="word">).</span><span class="method">click</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="method">setTimeout</span><span class="word">(</span><span class="spe">$</span><span class="word">.</span><span class="method">proxy</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="key">this</span><span class="word">).</span><span class="method">addClass</span><span class="word">(</span><span class="str">'aNewClass'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><span class="key">this</span><span class="word">),</span><span class="word">1000);</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                <span class="code">$.proxy(context,name)</span> 方法，第一个参数 <span class="code">context</span> 可以传入对象、函数等，第二个 <span class="code">name</span> 是参数操作的函数。在本例中，这个方法可以使得计时器中的 <span class="code">this</span> 参数也指向 <span class="code">$.proxy()</span> 传入的 <span class="code">this</span>，也就是按钮本身。
             </p>
            <p>
                不过我认为，其实掌握了第一种方法就可以了。这个方法通用于所有的 <span class="code">this</span> 指向的问题。在 <span class="tip">微信小程序</span>、<span class="tip">vue</span> 等地方都用的很多。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


