<template>
    <main>
        <section class="main-left">
            <!-- 轮播 -->
            <el-carousel
                    class="_roundImg"
                    :interval="5000"
                    :autoplay="true"
                    arrow="always"
                    :height="carouselHei + 'px'">
                    <el-carousel-item
                            v-for="item in bgImg"
                            :key="item"
                            :name="item.url">
                        <img class="round-img" :src="item.src" alt="item.url" @click="toBlogDetail(item.url+'')">
                        <h3 class="round-title" @click="toBlogDetail(item.url+'')">{{$store.state.initialBlogData[$store.state.initialBlogData.length - item.url].title}}</h3>
                    </el-carousel-item>
            </el-carousel>

            <!-- 最新内容 -->
            <p class="lastest-text">Latest 10</p>
            <article v-for="item in recentTen" @click="$parent.toDetails(item.type,item.url)">
                <h4 class="title">
                    {{item.title}}
                </h4>
                <p class="time">
                    {{item.time}}
                    <el-tag v-for="tag in item.tags" :type="tag.type" :key="tag.type">{{tag.name}}</el-tag>
                    {{distance(item.time)}}天前
                </p>
                <p v-html="returnBrief(item.brief)" class="brief">
                    {{item.brief}}
                </p>
            </article>
        </section>
        <!-- 个人信息 -->
        <section class="main-right">
            <header class="info">
                <img class="head-bg" src="./../../assets/img/headBg.jpg" alt="head-bg"/>
                <div class="motto">{{$store.state.motto}}</div>
                <div class="head-img"></div>
                <div class="head-name">{{$store.state.owner}}</div>
                <div class="link">
                    <el-tooltip class="item" effect="dark" content="https://github.com/xll032" placement="bottom-start">
                        <a href="https://github.com/xll032" target="_blank"><span class="fa fa-github"></span></a>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="https://weibo.com" placement="bottom">
                        <span class="fa fa-weibo"></span>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="https://weixin.com" placement="bottom">
                        <span class="fa fa-wechat"></span>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="https://qq.com" placement="bottom-end">
                        <span class="fa fa-qq"></span>
                    </el-tooltip>
                </div>
            </header>
            <div class="type">
                <header><span class="fa fa-tags"></span> 技术分类</header>
                <el-tag v-for="tag in $store.state.tags" :type="tag.type" :key="tag.name" @click.native="searchForType(tag.name)">{{tag.name}}</el-tag>
            </div>
        </section>
    </main>
</template>
<script>
    import img1 from '../../assets/img/1.jpg'
    import img2 from '../../assets/img/2.jpg'
    import img3 from '../../assets/img/3.jpg'
    import img4 from '../../assets/img/4.jpg'
    import img5 from '../../assets/img/5.jpg'

    export default {
        name: 'app',
        data(){
            const that = this;
            const _recent = [];
            this.$store.state.initialBlogData.forEach( (e,i) => {
                if( i <= 10 ) _recent.push(e);
                else return false;
            });
            return {
                carouselHei:420,
                bgImg:[
                    {url:'82',src:img1},
                    {url:'48',src:img2},
                    {url:'33',src:img3},
                    {url:'22',src:img4},
                    {url:'1',src:img5}
                ],
                recentTen:_recent
            }
        },
        methods: {
            toDetails(type,path){
                // 跳转至详情页
                if( type === 0 ) this.$router.push( '/blog/blog_' + path );
                else if( type === 1 ) this.$router.push( '/demo/demo_' + path );
            },
            distance(time){
                // 计算距今的时间（天数）
                let now = new Date();
                let [ year, month, date ] = [ time.substring(0,4), time.substring(5,7), time.substring(8,10) ];
                let before = new Date( year, month - 1, date );
                return ( ( now - before ) / 1000 / 60 / 60 / 24 ) >> 0;
            },
            returnBrief(val){
                // 过滤标签后固定显示最多75字简介
                const _val = val.replace(/<[^>]+>/g,"");
                return _val.substring( 0,75 ) + ( _val.length <= 75 ? '' : '...' );
            },
            searchForType(name){
                const that = this;
                that.$store.commit('searchForType',name);
                that.$router.push('/blog');
            },
            toBlogDetail(url){
                this.$router.push('/blog/blog_' + url);
            },
            setCarouselHei(){
                const round = document.getElementsByClassName('_roundImg')[0],
                      minHei = 150;
                let calcHei = '';
                if(round) calcHei = ( parseInt(window.getComputedStyle(round).width) / 2.5 ) >> 0;
                else return false;
                this.carouselHei = calcHei < minHei ? minHei : calcHei;
            }
        },
        mounted(){
            if(location.hash ==='#/home') document.title = '个人博客 by 龙';

            this.setCarouselHei();

            window.onresize = () => this.setCarouselHei();
        }
    }
</script>
<style>
    /*
        main color： #23aa23 -> 绿色
                     #32be32 -> 亮绿
                     #f9fafc -> 浅灰，背景色

                     #2da4ef  lightblue

    */
    main.container.overflow-hidden{
        margin: 0;
    }
    main.container .lastest-text{
        padding-left:4px;
        margin:20px 0 0 0;
        font-size:16px;
        color:#99a9bf;
    }
    main.container .el-tag{
        cursor:pointer;
    }
    main.container section.main-right header.info{
        width:100%;
        position: relative;
        overflow: hidden;
    }
    main.container section.main-right header.info .motto{
        position: absolute;
        z-index:2;
        margin:0 auto;
        left:0;
        right:0;
        text-align:center;
        line-height:100px;
        font-size:18px;
        color:#f9fafc;
        font-family:'华文行楷',sans-serif;
        text-shadow: 1px 1px 1px #333;
    }
    main.container section.main-right header.info .head-bg{
        height:130px;
        background: #4783aa;
        position: absolute;
        z-index:0;
    }
    main.container .round-img{
        cursor:pointer;
        width:100%;
        height:100%;
    }
    main.container .round-title{
        cursor:pointer;
        position:absolute;
        margin:0 auto;
        left:0;
        right:0;
        text-align:center;
        color:#eff2f7;
        bottom:30px;
        text-shadow: 1px 1px 1px #333;
    }
    main.container .round-title：hover{
        text-decoration:underline;
    }
    main.container section.main-right header.info .head-img{
        width:90px;
        height:90px;
        margin:80px auto 20px auto;
        -webkit-border-radius:50%;
        -moz-border-radius:50%;
        border-radius:50%;
        background:url('/static/img/head.png') no-repeat;
        background-size:100% 100%;
        position: relative;
        z-index:1;
        -webkit-box-shadow:0 0 6px 0 #999;
        -moz-box-shadow: 0 0 6px 0 #999;
        box-shadow: 0 0 6px 0 #999;
    }
    main.container section.main-right header.info .head-name{
        text-align:center;
    }
    main.container .link{
        text-align:center;
    }
    main.container .link .fa{
        cursor: pointer;
        padding:6px;
        color:#D3DCE6;
        font-size:24px;
        -webkit-transition: color 0.3s ease-in;
        -moz-transition: color 0.3s ease-in;
        -ms-transition: color 0.3s ease-in;
        -o-transition: color 0.3s ease-in;
        transition: color 0.3s ease-in;
    }
    main.container .link .fa:hover{
        color:#475669;
    }
    main.container .type{
        padding:20px;
        font-size:14px;
        color:#8492a6;
    }
    main.container .type .el-tag{
        margin:3px;
    }
    main.container .el-carousel__indicators{
        width:100%;
        text-align:center;
    }
    main.container article .brief {
        padding-left: 10px;
        color: #8492A6;
        font-size: 1em;
    }
    main.container .el-tag{
        margin:0 2px;
    }
    main.container article{
        cursor: pointer;
        padding:6px;
        height:150px;
        overflow:hidden;
        /* -webkit-box-shadow: 0 0 10px 1px #ddd;
         -moz-box-shadow: 0 0 10px 1px #ddd;
         box-shadow: 0 0 10px 1px rgba(240,255,240,1);*/
        font-size:14px;
        transition: all 1s ease-in-out;
        -webkit-transition: all 0.4s ease-in-out;
        -moz-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
    }
    main.container article p{
        margin:5px 0;
        font-size:1em;
    }
    main.container article .title{
        border-left:8px solid #23aa23;
        margin:10px 0;
        padding-left:4px;
        color:#475669;
        font-size:1.2em;
    }
    main.container article .time{
        padding-left:10px;
        color:#99A9BF;
        font-size:0.9em;
    }
    @media screen and ( min-width: 750px ) {
        main.container article:hover{
            font-size:16px;
        }
    }
</style>


