<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">1、不换行书写</p>
            <p>直接上代码和效果（后面的就不显示效果了，都一样）：</p>
            <el-button type="primary" size="small">按钮1</el-button><el-button type="success" size="small">按钮2</el-button><el-button type="danger" size="small">按钮3</el-button>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">1</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">2</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">3</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span></pre>
            <p>
                这个方法比较好，但是由于代码有时候会比较长，使得可读性大大降低。
            </p>
            <p class="minititle">2、标签特殊写法</p>
            <p>
               有四种比较特殊的写法可以规避换行符，同时做到换行的效果，方法在下列代码中展示：
            </p>
            <pre class="code"><span class="com">/* 1、结束标签换行 */</span><br><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">1</span><br><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">2</span><br><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">3</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><br><span class="com">/* 2、最后一个“&gt;”符号换行 */</span><br><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">1</span><span class="arrow">&lt;/</span><span class="tag">button</span><br><span class="arrow">&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">2</span><span class="arrow">&lt;/</span><span class="tag">button</span><br><span class="arrow">&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">3</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><br><span class="com">/* 3、利用注释换行 */</span><br><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">1</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="com">&lt;!--<br>--&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">2</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="com">&lt;!--<br>--&gt;</span><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">3</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span><br><span class="com">/* 4、不写闭合标签（H5中甚至最后一个都可以不写，但是在IE6/IE7下必须要加） */</span><br><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">1</span><br><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">2</span><br><span class="arrow">&lt;</span><span class="tag">button</span><span class="arrow">&gt;</span><span class="word">3</span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">&gt;</span></pre>
            <p>
                个人认为上面的几种方法是比较好的，可以根据自己的习惯（哪个看的顺眼一点）选择一种。
            </p>
            <p>
                下面介绍几种其他的方法，不过并不是很推荐。
            </p>
            <p class="minititle">3、margin 负值</p>
            <p>
                给 <span class="code">margin</span> 负值的大小与上下文的字体和文字大小都有关系，需要对不同情况做很多调试。这意味着要写很多样式去兼容，相比上面的方法，实在太复杂了。由于不推荐加上太麻烦，就不介绍了（要去网上查资料才知道怎么去设置了，曾经看到过这样的文章）。              
            </p>
            <p class="minititle">4、设置 font-size</p>
            <p>
                将 <span class="code">font-size</span> 设置为 <span class="code">0</span>。这个方法在 <span class="code">IE6/7</span> 上可能没用。而 旧版 <span class="tip">Chrome</span>（听说新版要取消了） 由于最小字体限制，也无法生效，不过可以做兼容。代码如下：
            </p>
            <pre class="code"><span class="key">.space</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">font-size</span><span class="word">:</span><span class="str">0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">-webkit-text-adjust</span><span class="word">:</span><span class="str">none</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
               不过还是不推荐。
            </p>
            <p class="minititle">5、使用 letter-spacing</p>
            <p>
                代码这里不上了，这个方法能兼容 <span class="tip">IE6/7</span> 但是在 <span class="tip">Opera</span> 下无法兼容。
            </p>
            <p class="minititle">6、使用 word-spacing</p>
            <p>
                也不上代码了，挺偏的方法。没有亲身试过。<span class="tip">Chrome</span> 下需要加上 <span class="code">display:table</span> 或者 <span class="code">display:inline-table</span> 做兼容。
            </p>
            <p>
                综上，首推 2 所介绍的方法。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>
<style>
    .el-button+.el-button{
        margin:0;
    }
</style>


