<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                把项目打包成线上项目的操作其实非常简单。执行如下命令即可：
            </p>
            <pre class="cmd">npm run build</pre>
            <p>
                1、打包成线上项目不是必须要 <span class="tip">node</span> 环境。开发时的 <span class="tip">node</span> 只是本地开发环境（如果直接用这个上线，别人就能看到源码了，而且代码多，效率低）。使用 <span class="tip">Apache</span> 或者其他服务器也可以。
            </p>
            <p>
                2、上线的文件：执行上述命令后，可以在项目目录下找到 <span class="tip">dist</span> 文件夹。里面有一个 <span class="tip">static</span> 文件夹和一个 <span class="tip">index.html</span> 文件。把它们放到线上服务器的根目录下。直接打开线上地址，就可以访问了。
            </p>
            <p>
                3、单页应用的优缺点就很明显了：首屏加载速度比较吃网速，网速慢就要很久；加载完之后就非常流畅了。如果是静态的，甚至可以脱机使用（比如我这个博客）。
            </p>
            <p>
                最后提一个我在 <span class="tip">build</span> 的时候，遇到的一个小问题（问题其实不小了）。打开以后，所有的 <span class="tip">js</span> 和 <span class="tip">css</span> 全部报错 <span class="tip">404</span>。解决方案不是很复杂。修改一下配置就好了（建议去学一下 <span class="tip">webpack</span>）。把 <span class="tip">config/index.js</span> 里的 <span class="code">build.assetsPublicPath</span> 值改为空（<span class="code">''</span>）。不过还是根据具体情况来做处理。
             </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


