<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">
              {{thisBlog.brief}}
            </p>
            <p class="subtitle">正文</p>
            <p>
               在 <span class="code">form</span> 表单中如果写了 <span class="code">button</span> 按下回车，或者点击 <span class="code">button</span> 在默认的情况下都会刷新页面。其实就是 <span class="code">submit</span> 事件。可以通过阻止浏览器默认行为来阻止刷新。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


