<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                一般在使用插件的时候，会使用到 <span class="tip">this</span> 的方法。如果在封装插件时，在回调函数中使用了 <span class="tip">this</span>，如下例。就会出现问题。
            </p>
            <pre class="code"><span class="word">(</span><span class="key">function</span><span class="word">($)</span><span class="flower">{</span><br><span class="spe">&nbsp;&nbsp;&nbsp;&nbsp;$</span><span class="word">.</span><span class="key">fn</span><span class="word">.</span><span class="method">plugin</span><span class="key"> = </span><span class="key">function</span><span class="word">(callback)</span><span class="flower">{</span><br><span class="word">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;callback();</span><br><span class="flower">&nbsp;&nbsp;&nbsp;&nbsp;}</span><br><span class="flower">}</span><span class="word">)(</span><span class="globe">jQuery</span><span class="word">);</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'#app'</span><span class="word">).</span><span class="method">plugin</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br><span class="spe">&nbsp;&nbsp;&nbsp;&nbsp;console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="key">this</span><span class="word">);</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                上面代码的 <span class="tip">this</span> 指向的是 <span class="tip">window</span> 对象，而我们希望得到的是 <span class="code">#app</span>。
            </p>
            <p>
                实际上，上例中 <span class="tip">this</span> 本就应该指向的是 <span class="tip">window</span> 对象（什么原因这里不介绍）。要使 <span class="tip">this</span> 指向我们所希望的 <span class="code">#app</span> 需要做一些特殊的处理。
            </p>
            <p>
                使用 <span class="code">call()</span> 方法修改指向就可以了。看下面的例子就明白了。
            </p>
            <pre class="code"><span class="word">(</span><span class="key">function</span><span class="word">($)</span><span class="flower">{</span><br><span class="spe">&nbsp;&nbsp;&nbsp;&nbsp;$</span><span class="word">.</span><span class="key">fn</span><span class="word">.</span><span class="method">plugin</span><span class="key"> = </span><span class="key">function</span><span class="word">(callback)</span><span class="flower">{</span><br><span class="key">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;var </span><span class="param">_self</span><span class="key"> = this</span><span class="word">;</span><br><span class="word">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;callback.</span><span class="method">call</span><span class="word">(</span><span class="param">_self</span><span class="word">);</span><br><span class="flower">&nbsp;&nbsp;&nbsp;&nbsp;}</span><br><span class="flower">}</span><span class="word">)(jQuery);</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'#app'</span><span class="word">).</span><span class="method">plugin</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br><span class="spe">&nbsp;&nbsp;&nbsp;&nbsp;console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="key">this</span><span class="word">);</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                这个方法还是比较实用，如果常用 <span class="tip">jQuery</span> 封装插件的话，需要熟悉这个方法。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


