<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                 去掉 <span class="tip">hash</span> 值想到的当然是改变地址了，比如 <span class="code">window.location.hash = '';</span>。
            </p>
            <p>
                这样的做法的确可以去掉<span class="tip">hash</span> 值，但是会留下 <span class="tip">#</span>。甚至直接替换也不能去掉。如： 
                <pre class="code"><span class="spe">window</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">hash</span><span class="key"> = </span><span class="str">'demo'</span><span class="word">;</span><span class="com"> // 地址栏最后变为 #demo</span><br><span class="spe">window</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">hash</span><span class="key"> = </span><span class="spe">window</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">hash</span><span class="word">.</span><span class="method">replace</span><span class="word">(</span><span class="str">'#demo'</span><span class="word">,</span><span class="str">''</span><span class="word">);</span><span class="com"> // 残留了 #</span></pre>
            </p>
            <p>
                用了上面的方法基本能看出，不管怎么清空 <span class="tip">hash</span> 值，地址栏上的 <span class="tip">#</span> 是不会消失了，除非刷新页面。
            </p>
            <p>
                如果希望在不刷新页面的情况下，将 <span class="tip">hash</span> 值包括 <span class="tip">#</span> 彻底去掉可以使用 <span class="tip">H5</span> 的一个新方法。
            </p>
            <p>
                <span class="code">history.pushState()</span> 和 <span class="code">history.replaceState()</span>。他们允许修改 <span class="tip">history</span> 实体，<span class="code">push</span> 用于添加，<span class="code">replace</span> 用于修改。参数：<span class="code">state,title,url</span> 去掉 <span class="tip">hash</span> 值以及 <span class="tip">#</span> 的方法具体如下：
            </p>
            <pre class="code"><span class="spe">history</span><span class="word">.</span><span class="method">replaceState</span><span class="word">(</span><span class="spe">null</span><span class="word">,</span><span class="str">''</span><span class="word">,</span><span class="spe">location</span><span class="word">.</span><span class="spe">pathname</span><span class="key">+</span><span class="spe">location</span><span class="word">.</span><span class="spe">search</span><span class="word">);</span></pre>
            <p>
                这个方法还可以用来无刷新的修改地址栏地址（仅修改地址栏，内容不会变化，不能跨域使用）。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


