<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                <span class="code">pre</span> 达到长度换行只需简单写两行 <span class="tip">css</span> 样式就可以了。
            </p>
            <pre class="code"><span class="key">pre</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">white-space</span><span class="word">:</span><span class="str">pre-wrap</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">word-wrap</span><span class="word">:</span><span class="str">break-word</span><span class="word">;</span><br><span class="flower">}</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


