<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                直接上一个使用 <span class="code">index()</span> 方法的例子：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div</span><span class="arrow">&gt;</span><span class="word">demo1</span><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><span class="com"> // =&gt; 下标 0</span><br><span class="arrow">&lt;</span><span class="tag">div</span><span class="arrow">&gt;</span><span class="word">demo2</span><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><span class="com"> // =&gt; 下标 1</span><br><span class="arrow">&lt;</span><span class="tag">div</span><span class="arrow">&gt;</span><span class="word">demo3</span><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><span class="com"> // =&gt; 下标 2</span></pre>
            <p>
                获取上面三个 <span class="code">div</span> 的下标，操作如下：
            </p>
            <pre class="code"><span class="spe">$</span><span class="word">(</span><span class="str">'body'</span><span class="word">)</span><span class="word">.</span><span class="method">on</span><span class="word">(</span><span class="str">'click'</span><span class="word">,</span><span class="str">'div'</span><span class="word">,</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var</span> <span class="param">_index</span> <span class="key">=</span> <span class="spe">$</span><span class="word">(</span><span class="str">'h4'</span><span class="word">).</span><span class="method">index</span><span class="word">(</span><span class="spe">$</span><span class="word">(</span><span class="key">this</span><span class="word">));</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="param">_index</span><span class="word">);</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                得到的 <span class="code">_index</span>，即为上述想要的效果。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


