<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                这里的内容比较短，具体的内容就放到这里说了。
            </p>
            <p>
                <span class="tip">jsonp</span> 是一种不安全的请求机制。在微信小程序中不支持这种格式的请求，因为如果要跨域请求，需要在小程序后台的域名配置中设置要请求的域名。
            </p>
            <p>
                填好之后，就可以直接向该域名发送请求了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


