<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先通过一个例子来说明一下这个问题：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">a</span><span class="key"> = </span><span class="word">1, </span><span class="param">b</span><span class="key"> = </span><span class="word">2;</span><br><span class="param">a</span><span class="key"> = </span><span class="param">b</span><span class="word">;</span><span class="com">// a = 2,b = 2</span><br><span class="param">b</span><span class="key"> = </span><span class="word">3;</span><span class="com">// a = 2,b = 3</span><br><span class="key">var </span><span class="param">arr1</span><span class="key"> = </span><span class="rect">[</span><span class="word">1, 2, 3</span><span class="rect">]</span><span class="word">;</span><br><span class="key">var </span><span class="param">arr2</span><span class="key"> = </span><span class="param">arr1</span><span class="word">;</span><br><span class="param">arr2</span><span class="rect">[</span><span class="word">1</span><span class="rect">]</span><span class="key"> = </span><span class="word">8;</span><span class="com"> // arr2 = [1,8,3]</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">arr1</span><span class="word">);</span><span class="com"> // arr1 = [1,8,3]</span></pre>
            <p>
                <span class="tip">Number</span>、<span class="tip">String</span>、<span class="tip">Boolean</span> 是原始类型对象。它们存储的都是实际的数据。 通俗讲，就是每一个都是一个不相干的值，指向都不同。即使使用 <span class="code">a = b</span>，变化的也只是值而已。
            </p>
            <p>
                而<span class="tip">Array</span>、<span class="tip">Object</span>、<span class="tip">Function</span>是合成类型对象（<span class="tip">Function</span> 可以直接赋值后修改，跟原始类型一样）。上例中只改了 <span class="code">arr2[1]</span>，但是<span class="code">arr1[1]</span> 也同时发生了改变。存储它们引用了一个内存地址，值是另外单独存放的。因为引用对象通常比较庞大，这是数据开销和内存开销优化的手段。所以，才发生了上面的现象。（这就有点像 <span class="tip">C语言</span> 的指针）。
            </p>
            <p>
                有时候我们会需要在保留原始数据的情况下，克隆一份新的数据做一些改动，然而，我们知道，直接用 <span class="code">arr1 = arr2</span> 的方式会使得两组数据一起发生变化，所以是行不通的。
            </p>
            <p>
                如果明白了这个原理的话，无论是克隆 <span class="tip">Array</span> 还是 <span class="tip">Object</span> 都很容易了。写法也很多样。
            </p>
            <p>
                大纲就是：新声明一个变量，通过循环的方式，把原 <span class="tip">Array</span> 或 <span class="tip">Object</span> 内的数据一个个添加进去。
            </p>
            <p>
                下面简单写一些克隆的方法，明白了原理的同学也可以自己写。
            </p>
            <p>
                简单的数组克隆：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">a</span><span class="key"> = </span><span class="rect">[</span><span class="word">1, 2,3</span><span class="rect">]</span><span class="word">;</span><br><span class="key">var </span><span class="param">b</span><span class="key"> = </span><span class="rect">[</span><span class="rect">]</span><span class="word">;</span><br><span class="com">// for 循环也可改为 ES6 forEach() 方法</span><br><span class="key">for</span><span class="word">(</span><span class="key">var </span><span class="param">i</span><span class="key"> = </span><span class="word">0; </span><span class="param">i</span><span class="key"> &lt; </span><span class="param">a</span><span class="word">.</span><span class="spe">length</span><span class="word">; </span><span class="param">i</span><span class="key">++ </span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">b</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="key"> = </span><span class="param">a</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="word">;</span><span class="com"> // 也改为 b.push(a[i]);</span><br><span class="flower">}</span><br><span class="param">b</span><span class="rect">[</span><span class="word">1</span><span class="rect">]</span><span class="key"> = </span><span class="word">8;</span><span class="com"> // b = [1,8,2]</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">a</span><span class="word">);</span><span class="com"> // a = [1,3,2]</span></pre>
            <p>
                简单的对象克隆：
            </p>
            <p>
                克隆的方法和上面的简单的数组克隆一样。
            </p>
            <p>
                在实际使用的过程中，数组和对象的结构往往是比较复杂的。简单的用上面的方法并不能完全克隆所有的数据。这里就不上代码了，最简洁的思路就是使用递归的方式遍历克隆所有数据，这样的方法具有通用性。
            </p>
            <p>
                如果使用 <span class="tip">jQuery</span> 的话，只需要使用 <span class="code">newObj = $.extend(true,{},oldObj)</span> 就可以深度克隆了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


