<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">方法一</p>
            <p>
                循环改变自身去重（下面的方法都是用 <span class="code">prototype</span> 写的，不想用把里面的拿出来就好了）。代码如下：
            </p>
            <pre class="code"><span class="spe">Array</span><span class="word">.</span><span class="spe">prototype</span><span class="word">.</span><span class="spe">unique_0</span><span class="key"> = </span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">for</span><span class="word">(</span><span class="key">var</span><span class="param"> i</span><span class="key"> = </span><span class="word">0; </span><span class="param">i</span><span class="key"> &lt; </span><span class="key">this</span><span class="word">.</span><span class="spe">length</span><span class="word">; </span><span class="param">i</span><span class="key">++</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">for</span><span class="word">(</span><span class="key">var</span><span class="param"> j</span><span class="key"> = </span><span class="param">i</span><span class="key"> + </span><span class="word">1; </span><span class="param">j</span><span class="key"> &lt; </span><span class="key">this</span><span class="word">.</span><span class="spe">length</span><span class="word">; </span><span class="param">j</span><span class="key">++</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="key"> === </span><span class="key">this</span><span class="rect">[</span><span class="key">j</span><span class="rect">]</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">this</span><span class="word">.</span><span class="method">splice</span><span class="word">(</span><span class="param">i</span><span class="word">,1);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">i</span><span class="key"> --</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return</span><span class="key"> this</span><span class="word">;</span><br><span class="flower">}</span><br><br><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,1,3,4,1,2,5,5</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">unique_0</span><span class="word">();</span></pre>
            <p class="minititle">方法二</p>
            <p>
                定义一个新的数组，把判断新数组中是否有旧数组的值，如果没有，就加进去。代码如下：
            </p>
            <pre class="code"><span class="spe">Array</span><span class="word">.</span><span class="spe">prototype</span><span class="word">.</span><span class="spe">unique_1</span><span class="key"> = </span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var</span><span class="param"> res</span><span class="key"> = </span><span class="rect">[</span><span class="key">this</span><span class="rect">[</span><span class="word">0</span><span class="rect">]]</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">for</span><span class="word">(</span><span class="key">var</span><span class="param"> i</span><span class="word"> = </span><span class="word">1; </span><span class="param">i</span><span class="key"> &lt; </span><span class="key">this</span><span class="word">.</span><span class="spe">length</span><span class="word">; </span><span class="param">i</span><span class="key">++</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">repeat</span><span class="key"> = false</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">for</span><span class="word">(</span><span class="key">var</span><span class="param"> j</span><span class="word"> = </span><span class="word">0; </span><span class="param">j</span><span class="key"> &lt; </span><span class="param">res</span><span class="word">.</span><span class="spe">length</span><span class="word">; </span><span class="param">j</span><span class="key">++</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="key"> === </span><span class="param">res</span><span class="rect">[</span><span class="param">j</span><span class="rect">]</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">repeat</span><span class="key"> = </span><span class="key">true</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">break</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">!</span><span class="param">repeat</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">res</span><span class="word">.</span><span class="method">push</span><span class="word">(</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="param">res</span><span class="word">;</span><br><span class="flower">}</span><br><br><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,1,3,4,1,2,5,5</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">unique_1</span><span class="word">();</span></pre>
            <p class="minititle">方法三</p>
            <p>
                先排序原数组再比较，效率较方法一高一些，但是也有局限性。这个方法会改变原数组的顺序，如果不希望改变顺序不能使用此方法。代码如下：
            </p>
            <pre class="code"><span class="spe">Array</span><span class="word">.</span><span class="spe">prototype</span><span class="word">.</span><span class="spe">unique_2</span><span class="key"> = </span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">this</span><span class="word">.</span><span class="method">sort</span><span class="word">();</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">res</span><span class="word"> = </span><span class="rect">[</span><span class="key">this</span><span class="rect">[</span><span class="word">0</span><span class="rect">]]</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">for</span><span class="word">(</span><span class="key">var </span><span class="param">i</span><span class="key"> = </span><span class="word">1; </span><span class="param">i</span><span class="key"> &lt; this</span><span class="word">.</span><span class="spe">length</span><span class="word">; </span><span class="param">i</span><span class="key">++</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="key"> !== </span><span class="param">res</span><span class="rect">[</span><span class="param">res</span><span class="word">.</span><span class="spe">length</span><span class="key"> - </span><span class="word">1</span><span class="rect">]</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">res</span><span class="word">.</span><span class="method">push</span><span class="word">(</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="param">res</span><span class="word">;</span><br><span class="flower">}</span><br><br><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,1,3,4,1,2,5,5</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">unique_2</span><span class="word">();</span></pre>
            <p class="minititle">方法四</p>
            <p>
                往对象里存数组，稍稍改变一下 <span class="code">json[this[i]]</span> 的判断条件和赋值，可以做到记录重复的次数。代码如下：
            </p>
            <pre class="code"><span class="spe">Array</span><span class="word">.</span><span class="spe">prototype</span><span class="word">.</span><span class="spe">unique_3</span><span class="key"> = </span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">res</span><span class="key"> = </span><span class="rect">[]</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">json</span><span class="key"> = </span><span class="flower">{}</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">for</span><span class="word">(</span><span class="key">var</span><span class="param"> i</span><span class="key"> = </span><span class="word">0; </span><span class="param">i</span><span class="key"> &lt; this</span><span class="word">.</span><span class="spe">length</span><span class="word">; </span><span class="param">i</span><span class="key">++</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">!</span><span class="param">json</span><span class="rect">[</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]]</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">res</span><span class="word">.</span><span class="method">push</span><span class="word">(</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">json</span><span class="rect">[</span><span class="key">this</span><span class="rect">[</span><span class="param">i</span><span class="rect">]]</span><span class="key"> = </span><span class="word">1;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="param">res</span><span class="word">;</span><br><span class="flower">}</span><br><br><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,1,3,4,1,2,5,5</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">unique_3</span><span class="word">();</span></pre>
            <p>
                除了这些方法以外，当然还有其他的方法。懒得写的话，这几种也差不多够了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


