<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                要做到限制输入其实很容易，下面直接上代码。
            </p>
            <p>
                <span class="tip">jQuery</span> 方法（原生的就是把这些 <span class="tip">jQuery</span> 方法改为 <span class="code">addEventListener</span>、<span class="tip">this.value</span> 等，就不写了，最主要的就是 <span class="code">this.value = this.value.replace(/[^\d]/ig,''/)</span>）：
            </p>
            <pre class="code">
                <span class="spe">$</span><span class="word">(</span><span class="str">'#element'</span><span class="word">).</span><span class="method">off</span><span class="word">(</span><span class="str">'input'</span><span class="word">).</span><span class="method">on</span><span class="word">(</span><span class="str">'input'</span><span class="word">,</span><span class="fun">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">val</span><span class="key"> = </span><span class="spe">$</span><span class="word">(</span><span class="key">this</span><span class="word">).</span><span class="method">val</span><span class="word">();</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="key">this</span><span class="word">).</span><span class="method">val</span><span class="word">(</span><span class="param">val</span><span class="word">.</span><span class="method">replace</span><span class="word">(</span><span class="spe">/[^\d]/ig</span><span class="word">,</span><span class="str">''</span><span class="word">));</span><br><span class="flower">}</span><span class="word">);</span>
            </pre>
            <p>
                有更好的方法，请指点，感谢。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


