<template>
    <div id="go-top"
         :style="{
                    opacity:$store.state.btnOpacity,
                    display:$store.state.btnOpacity<0.05?'none':'block',
                    cursor:$store.state.isScroll?'not-allowed':'pointer'
                }"
         @click="goTop">
        <span class="fa fa-caret-up"></span>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                timer:'',
                ratio:1/1000,
            }
        },
        methods:{
            goTop(){
                if(this.$store.state.isScroll) return false;
                let that =this;
                let basicSpeed = 80,
                    t = this.getPageScroll()[1],
                    all = t;
                this.timer = setInterval(()=>{
                    // 滚动时不可点击
                    if(!this.$store.state.isScroll) this.$store.state.isScroll = !this.$store.state.isScroll;
                    // 计算速度
                    let speed = basicSpeed * t / all;
                    speed = speed < 1 ? 1 : speed;
                    t -= speed;
                    // 滚动
                    document.body.scrollTop = t-speed;
                    //self.pageYOffset = t - speed;
                    document.documentElement.scrollTop = t - speed;

                    if( parseInt(t) <= 0 ){
                        this.$store.state.isScroll = false;
                        clearInterval(this.timer);
                    }
                },10);
            },
            scrollController(){
                let scrollTop = this.getPageScroll()[1];
                let [_op1,_op2] = [0,0];
                if(this.$store.state.isScroll){
                    [_op1,_op2] = [this.ratio * scrollTop,1 - this.ratio * scrollTop];
                    this.$store.state.menuOpacity = _op2 > 0.7 ? 1 : _op2;
                    this.$store.state.btnOpacity  = _op1 < 0.3 ? 0 : _op1;
                }else{
                    [_op1,_op2] = [1 - this.ratio * scrollTop,this.ratio * scrollTop];
                    this.$store.state.menuOpacity = _op1 < 0.3 ? 0.3 : _op1;
                    this.$store.state.btnOpacity  = _op2 > 0.3 ? 0.3 : _op2;
                }
            },
            getPageScroll() {
                let xScroll, yScroll;
                if (self.pageYOffset) {
                    yScroll = self.pageYOffset;
                    xScroll = self.pageXOffset;
                } else if (document.documentElement && document.documentElement.scrollTop) { // Explorer 6 Strict
                    yScroll = document.documentElement.scrollTop;
                    xScroll = document.documentElement.scrollLeft;
                } else if (document.body) {// all other Explorers
                    yScroll = document.body.scrollTop;
                    xScroll = document.body.scrollLeft;
                }
                return [xScroll,yScroll];
            }
        },
        mounted(){
            // 滚动按钮
            this.scrollController();
            window.onmousewheel = ()=>{
                if(this.$store.state.isScroll){
                    clearInterval(this.timer);
                    this.$store.state.isScroll = false;
                }
            };
            window.addEventListener('scroll',this.scrollController);
        }
    }
</script>
<style>
    #go-top{
        text-align:center;
        background:#99A9BF;
        opacity:0.2;
        position: fixed;
        bottom:100px;
        right:40px;
        width:50px;
        height:50px;
        -webkit-border-radius:50%;
        -moz-border-radius:50%;
        border-radius:50%;
    }
    #go-top .fa{
        line-height:44px;
        color:#EFF2F7;
        font-size:40px;
    }
</style>


