<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                如果表单中有 <span class="code">&lt;input&nbsp;type="password"/&gt;</span> 在一些主流浏览器上开启自动填充账号密码了。
            </p>
            <p>
                在刚才的场景中，输入子账号的密码时，当然不能显示出密码，而自动填充进来又没有用，还要删去。别说用户体验了，自己都难受。
            </p>
            <p>
                使用了 <span class="code">autocomplete="off"</span> 的方法，在 <span class="tip">Chrome</span> 下无效。进过搜索发现，这个方法存在兼容问题，对 <span class="tip">Chrome</span>、<span class="tip">FireFox</span> 是无效的。如果是 <span class="tip">IE</span> 不兼容，还可以“理直气壮”地说 <span class="tip">IE</span> 该淘汰了。主流的现代浏览器不行那肯定是不行的。
            </p>
            <p>
                后来找到了解决方法。还是 <span class="code">autocomplete</span> 属性，这个属性除了 <span class="code">on</span> 和 <span class="code">off</span> 之外，还有一个“隐藏属性”（之所以说隐藏属性，是因为 <span class="tip">W3School</span> 上也没有），这个属性值叫做 <span class="code">new-password</span>。
            </p>
            <p>
                顾名思义，说明了是新密码，那就没有可以自动填充的项了。
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">input</span><span class="attr"> auto-complete=</span><span class="str">"new-password"</span><span class="attr"> type=</span><span class="str">"password"</span><span class="arrow">/&gt;</span></pre>
            <p>
                <span class="code">autocomplete</span> 和 <span class="code">auto-complete</span> 有什么区别不太清楚。上面的代码亲测 <span class="tip">Chrome</span> 有效。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


