<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                本地直接双击打开文件后发送 <span class="tip">ajax</span> 会报出以下错误： 
            </p>
            <p class="error">
                XMLHttpRequest cannot load file:///D:/WWW/angularlx/ui-router-test/template/content.html. 
            Cross origin requests are only supported for protocol schemes: http, data, chrome, chrome-extension, https, chrome-extension-resource.
            </p>
            <p>
                意思是：<span class="tip">XMLHttpRequest</span> 不能加载那个 <span class="tip">html</span> 文件，在本地是禁止跨域请求的，不支持 <span class="tip">file://</span> 协议。
            </p>
            <p>
                下面介绍如何绕过该安全机制，以 <span class="tip">Chrome</span> 浏览器为例（其他浏览器不知道怎么设置）：
            </p>
            <p class="minititle">方法一：</p>
            <p>
                右键点击 <span class="tip">Chrome</span> 快捷方式，选择 <span class="tip">“属性”</span> =>    <span class="tip">“快捷方式”</span> => <span class="tip">“目标”</span> ，在后面加上 “<span class="code"> --allow-file-access-from-files</span>”（最前面有一个空格），重启 <span class="tip">Chrome</span> 即可。
            </p>
            <p class="minititle">方法二：</p>
            <p>
                （1）<span class="tip">cmd</span> 进入 <span class="tip">dos</span> 环境；
            </p>
            <p>
                （2）<span class="tip">dos</span> 下进入 <span class="tip">\Google\Chrome\Application</span>(<span class="tip">chrome.exe</span> 所在目录)
            </p>
            <p>
                （3）运行命令：
            </p>
            <pre class="cmd">chrome.exe -- allow-file-access-from-files </pre>
            <p>
                （4）启动 <span class="tip">Chrome</span> 浏览器，然后去打开文件即可。但是这种方式只对本次打开有效。
            </p>
            <p>
                如果经常使用直接打开本地文件的话，可以用方法一修改一下配置，难得使用的话，用第二种即可。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


