<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                <span class="code">vertical-align</span> 除了使用 <span class="code">middle</span> 也可以使用百分比数值。
            </p>
            <p>
                下例中，两个 <span class="code">div</span> 高度相同，<span class="code">vertical-align</span> 和 <span class="code">margin-bottom</span> 的数值也相等，前者将 <span class="code">div</span>（容器）增大了。
            </p>
            <div class="test">
                <div class="v">vertical-align</div>
            </div>
            <div class="test">
                <div class="m">margin-bottom</div>
            </div>
            <div style="clear:both"></div>
            <p>
                <span class="code">vertical-align</span> 属性使用的限制还是比较多的，并不是直接写上就有用的。一般情况下，使用 <span class="code">margin</span> 已经足够了。
            </p>
            <p>
                在需要使用到垂直居中的时候，会更多用到。父元素上使用 <span class="code">display:table</span>，然后在子元素上使用： <span class="code">display:table-cell;vertical-align:middle;</span>，就可以做到垂直居中。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>
<style>
    .test:first-of-type{
        display:table;
    }
    .test{
        float:left;
        width:120px;
        line-height:120px;
        height:120px;
        margin-left:40px;
        overflow: hidden;
        -webkit-box-shadow:0 0 10px 0 #ddd;
        -moz-box-shadow:0 0 10px 0 #ddd;
        box-shadow:0 0 10px 0 #ddd;
    }
    .test .v{
        display: inline-block;
        vertical-align:50px;
    }
    .test .m{
        display: inline-block;
        margin-bottom:50px;
    }
</style>


