<template>
    <main class="container overflow-hidden">
        <section class="main-left">
            <router-view></router-view>
        </section>
        <section class="main-right" >

        </section>
    </main>
</template>
<script>
    export default {
        name: 'app',
        data: function (){
            return {
                activeIndex:'home'
            }
        },
        methods: {
        },
        mounted(){
            if(location.hash ==='#/demo') document.title = '个人博客 by 龙 - demoList';
        }
    }
</script>
<style>
    main.container section.main-left{
        margin-right:2%;
    }
    main.container section.main-left,main.container section.main-right{
        -webkit-box-shadow:0 0 10px 0 #eee;
        -moz-box-shadow: 0 0 10px 0 #eee;
        box-shadow: 0 0 10px 0 #eee;
    }
    .demo{
        padding:10px;
    }
    .demo .title{
        border-left:8px solid #23aa23;
        padding-left:4px;
        color:#475669;
        font-size:1.4em;
    }
    .demo .time{
        padding-left:10px;
        color:#99A9BF;
        font-size:0.9em;
    }
    .demo .subtitle{
        border-left:8px solid #D3DCE6;
        padding-left:4px;
        font-size:1.1em;
        color:#8492A6;
        text-indent: 0;
    }
    .demo section p{
        font-size:0.9em;
        text-indent: 2em;
        color:#99A9BF;
    }
</style>


