<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                获取浏览器的版本很实用，也很容易。只需要短短一行代码。<span class="code">navigator.userAgent</span>
            </p>
            <p>
                下面举几个例子：
            </p>
            <p>
                <span class="tip">Chrome</span> 浏览器：<span class="code">Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/51.0.2704.63 Safari/537.36</span>
            </p>
            <p>
                <span class="tip">iPhone6</span>：<span class="code">Mozilla/5.0 (iPhone; CPU iPhone OS 9_1 like Mac OS X) AppleWebKit/601.1.46 (KHTML, like Gecko) Version/9.0 Mobile/13B143 Safari/601.1</span>
            </p>
            <p>
                <span class="tip">Galaxy</span>：<span class="code">Mozilla/5.0 (Linux; Android 5.0; SM-G900P Build/LRX21T) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/48.0.2564.23 Mobile Safari/537.36</span>
            </p>
            <p>
                下面有一个简单的示例，可以区分移动端和 PC 端，并进行跳转：
            </p>
            <pre class="code"><span class="key">function </span><span class="fun">checkDevice</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">let </span><span class="param">version </span><span class="key">= </span><span class="spe">navigator</span><span class="word">.</span><span class="spe">userAgent</span><span class="word">.</span><span class="method">toLowerCase</span><span class="word">();</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">window</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">href</span><span class="key"> = </span><span class="param">version</span><span class="word">.</span><span class="method">match</span><span class="word">(</span><span class="rect">/</span><span class="str">(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)</span><span class="rect">/i</span><span class="word">)))</span><span class="key"> ? </span><span class="str">'电脑端地址'</span><span class="key"> : </span><span class="str">'手机端地址'</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
                更多的判断戳这里：<a target="_blank" href='https://github.com/xll032/browser-version.github.io'>浏览器、设备版本判断</a>
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


