<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">遮罩层</p>
            <p>
                下面是一个简单遮罩层的样式
            </p>
            <pre class="code"><span class="key">.mask</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">width</span><span class="word">:</span><span class="str">100%</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">height</span><span class="word">:</span><span class="str">100%</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">background</span><span class="word">:</span><span class="str">rgba(0,0,0,0.8)</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">position</span><span class="word">:</span><span class="str">fixed</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">top</span><span class="word">:</span><span class="str">0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">left</span><span class="word">:</span><span class="str">0</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">z-index</span><span class="word">:</span><span class="str">9999</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">display</span><span class="word">:</span><span class="str">none</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p class="minititle">禁止屏幕滚动</p>
            <p>
                如果只是在点击的时候对 <span class="code">.mask</span> 做 <span class="tip">show</span> 操作的话，如果下面一层的内容比较多，仍然可以滚动。禁止滚动的方法也比较简单。见下面的代码：
            </p>
            <pre class="code"><span class="com">/* 需要做兼容处理 */</span><br><span class="spe">document</span><span class="word">.</span><span class="spe">body</span><span class="word">.</span><span class="spe">overflow</span><span class="key"> = </span><span class="str">'hidden'</span><span class="word">;</span><br><span class="spe">document</span><span class="word">.</span><span class="spe">documentElement</span><span class="word">.</span><span class="spe">style</span><span class="word">.</span><span class="spe">overflow</span><span class="key"> = </span><span class="str">'hidden'</span><span class="word">;</span></pre>
            上面的代码对于移动端是不能兼容的，兼容移动端的方案如下（阻止浏览器的默认行为）：
            <pre class="code"><span class="spe">document</span><span class="word">.</span><span class="method">addEventListener</span><span class="word">(</span><span class="str">'touchmove'</span><span class="word">,</span><span class="fun">function</span><span class="word">(event)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* flag: true => 遮罩层显示，false => 遮罩层隐藏 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param">flag</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">event.</span><span class="method">preventDefault</span><span class="word">();</span><br><span class="flower">}</span><span class="word">);</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


