<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                简单介绍一下，<span class="tip">require.js</span> 引入 <span class="tip">js</span> 的方法，下面是一个引入 <span class="tip">jQuery</span> 的例子
            </p>
            <pre class="code"><span class="spe">define</span><span class="word">(</span><span class="rect">[</span><span class="str">'jQuery'</span><span class="rect">]</span><span class="word">,</span><span class="key">function</span><span class="word">($)</span><span class="flower">{</span><br><span class="com">&nbsp;&nbsp;&nbsp;&nbsp;/*内部函数*/</span><br><span class="key">&nbsp;&nbsp;&nbsp;&nbsp;var </span><span class="param">myFun </span><span class="key">= </span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// content</span><br><span class="flower">&nbsp;&nbsp;&nbsp;&nbsp;}</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
                引入上面的 <span class="code">js</span> 要使用下面的方法（假设上述的文件是 <span class="code">demo.js</span>）：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="method">&nbsp;&nbsp;&nbsp;&nbsp;require</span><span class="word">(</span><span class="rect">[</span><span class="str">'demo.js'</span><span class="rect">]</span><span class="word">,</span><span class="key">function</span><span class="word">(myFun)</span><span class="flower">{</span><br><span class="fun">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;myFun</span><span class="word">();</span><br><span class="flower">&nbsp;&nbsp;&nbsp;&nbsp;}</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span></pre>
            <p>
                在 <span class="tip">js</span> 中使用 <span class="tip">define</span> 的方法可以实现 <span class="tip">js</span> 按需加载，而且只会在第一次未加载的时候去引用，第二次调用就不会再加载了。
            </p>
            <p>
                <span class="tip">require.js</span> 可以设置配置文件，将文件路径配置进去后，可以使用定义好的名字去引用，简化内容。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


