<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、typeof</p>
            <p>
                <span class="code">typeof</span> 会返回类型的名称（不能区分数组和对象）：
            </p>
            <pre class="code"><span class="method">typeof</span><span class="word">(123);</span><span class="com">// 'number'</span><br><span class="method">typeof</span><span class="word">(</span><span class="str">'123'</span><span class="word">);</span><span class="com">// 'string'</span><br><span class="method">typeof</span><span class="word">(</span><span class="rect">[</span><span class="word">1,2</span><span class="rect">]</span><span class="word">);</span><span class="com">// 'obect'</span><br><span class="method">typeof</span><span class="word">(</span><span class="flower">{</span><span class="spe">a</span><span class="word">:111</span><span class="flower">}</span><span class="word">);</span><span class="com">// 'obect'</span><br><span class="method">typeof</span><span class="word">(</span><span class="key">true</span><span class="word">);</span><span class="com">// 'boolean'</span><br><span class="method">typeof</span><span class="word">(</span><span class="param">a</span><span class="word">);</span><span class="com">// 'undefined'</span><br><span class="method">typeof</span><span class="word">(</span><span class="spe">/</span><span class="flower">\</span><span class="spe">s/g</span><span class="word">);</span><span class="com">// 'object'，有一些浏览器是 'function'</span><br></pre>
            <p class="minititle">二、instanceof</p>
            <p>
                 <span class="code">typeof</span> 会返回是否为该类型的布尔值（能区分数组和对象）：
            </p>
            <pre class="code"><span class="key">const</span><span class="param"> a</span><span class="key"> = </span><span class="flower">{</span><span class="spe">a</span><span class="word">:1</span><span class="flower">}</span><span class="word">;</span><br><span class="param">a</span><span class="method"> instanceof</span><span class="spe"> Object</span><span class="word">;</span><span class="com"> // true</span><br><span class="key">const</span><span class="param"> b</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,2</span><span class="rect">]</span><span class="word">;</span><br><span class="param">b</span><span class="method"> instanceof</span><span class="spe"> Array</span><span class="word">;</span><span class="com"> // true</span></pre>
            <p>
                这个方法不能直接用来检测基本类型的数据，全部会返回 <span class="code">false</span>。只能用来检测数据、对象、函数。
            </p>
            <p>
                上面的说法不绝对。比如：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">a</span><span class="key"> = </span><span class="key">new </span><span class="method">String</span><span class="word">(</span><span class="str">'abc'</span><span class="word">);</span><br><span class="param">a</span><span class="method"> instanceof</span><span class="spe"> String</span><span class="word">;</span><span class="com"> // true</span></pre>
            <p>
                判断类型还可以使用 <span class="code">prototype</span> 属性，这里不展开介绍。
            </p>
            <p>
                下面是一个判断是否为数组的方法（用到了 <span class="code">prototype</span>）：
            </p>
            <pre class="code"><span class="key">const</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,2,3,4</span><span class="rect">]</span><span class="word">;</span><br><span class="key">function</span><span class="fun"> isArr</span><span class="word">(obj)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="key">typeof</span><span class="spe"> Array</span><span class="word">.</span><span class="fun">isArr</span><span class="key"> === </span><span class="str">'function'</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="spe">Array</span><span class="word">.</span><span class="spe">isArray</span><span class="word">(obj);</span><span class="com"> // 浏览器支持 isArray 方法</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return</span><span class="spe"> Object</span><span class="word">.</span><span class="spe">prototype</span><span class="word">.</span><span class="spe">toString</span><span class="word">.</span><span class="method">call</span><span class="word">(obj)</span><span class="key"> === </span><span class="str">'[object Array]'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><br><span class="fun">isArr</span><span class="word">(</span><span class="param">arr</span><span class="word">);</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


