<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                获取键名的方法实际上很简单，正所谓会者不难。下面直接通过一个简单的示例来介绍。
            </p>
            <pre class="code"><span class="key">const </span><span class="param">json</span><span class="key"> = </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">"name"</span><span class="key">:</span><span class="str">"John"</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">"age"</span><span class="key">:</span><span class="str">18</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="str">"grade"</span><span class="key">:</span><span class="str">80</span><br><span class="flower">}</span><span class="word">;</span><br><span class="key">for</span><span class="word">(</span><span class="key">let </span><span class="param">i </span><span class="key">in </span><span class="param">json</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">json</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="word">);</span><span class="com">// 值 John、18、80</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">i</span><span class="word">);</span><span class="com">// 键名 name、age、grade</span><br><span class="flower">}</span></pre>
            <p>
                数组实际上是特殊的对象（但是它们的原型一个是 <span class="code">Array</span>、一个是 <span class="code">Object</span>，还是有一些区别的）。
            </p>
            <p>
                例如这样一个数组：<span class="code">arr = ['1号','2号','3号','4号']</span>，实际上是这样一个对象：<span class="code">arr = {"0":"1号","1":"2号","2":"3号","3":"4号"}</span>。
            </p>
            <p>
                而上例中的对象也可用 <span class="code">json['name']</span> 来取得其值： <span class="code">John</span>
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


