            <p>
                下面是写好的一个例子，以供参考，直接拿去用会有问题，需要改一些东西。原理不是很复杂。（注：1、下面方法要引入 <span class="tip">require.js</span> 和 <span class="tip">jQuery</span>，如不希望使用，请自行修改；2、这个是前端封装好的方法，后端的处理这里不做介绍。）
            </p>
signature
            <pre class="code"><span class="key">var </span><span class="param">requireNeed</span><span class="key"> = </span><span class="key">true</span><span class="word">;</span><span class="key">if</span><span class="word">(</span><span class="key">typeof</span><span class="param"> define</span><span class="key"> === </span><span class="str">'undefined'</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="str">'need require'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">requireNeed</span><span class="key"> = </span><span class="key">false</span><span class="word">;</span><br><span class="flower">}</span><br><span class="key">if</span><span class="word">(</span><span class="param">requireNeed</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">define</span><span class="word">(</span><span class="rect">[</span><span class="str">'https://res.wx.qq.com/open/js/jweixin-1.0.0.js'</span><span class="rect">]</span><span class="word">,</span><span class="key">function</span><span class="word">(wx)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">link </span><span class="key"> = </span><span class="spe">window</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">href</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">function</span><span class="fun"> wxShare</span><span class="word">(object)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 不是微信就返回</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( </span><span class="rect">/</span><span class="str">MicroMessenger</span><span class="rect">/i</span><span class="word">.</span><span class="method">test</span><span class="word">(</span><span class="spe">navigator</span><span class="word">.</span><span class="spe">userAgent</span><span class="word">)</span><span class="word"> )</span><span class="key"> return</span><span class="key"> false</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">//获取网页中的值（如果原来没有设置就不用去获取了）</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var </span><span class="param">obj</span><span class="key"> = </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">title</span><span class="word">:</span><span class="spe">$</span><span class="word">(</span><span class="str">'title'</span><span class="word">)</span><span class="word">.</span><span class="method">text</span><span class="word">(),</span><span class="com"> // 标题</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">desc</span><span class="word">:</span><span class="spe">$</span><span class="word">(</span><span class="str">'meta[name="description"]'</span><span class="word">)</span><span class="word">.</span><span class="method">attr</span><span class="word">(</span><span class="str">'content'</span><span class="word">),</span><span class="com"> // 描述</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">img</span><span class="word">:</span><span class="spe">$</span><span class="word">(</span><span class="str">'.titleImg'</span><span class="word">)</span><span class="word">.</span><span class="method">attr</span><span class="word">(</span><span class="str">'src'</span><span class="word">),</span><span class="com"> // 图片</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 接收传入的参数</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( </span><span class="method">typeof</span><span class="word"> object</span><span class="key"> === </span><span class="str">'object'</span><span class="word"> )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="param">obj</span><span class="word">,object);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 设置默认值（防止页面和传入参数都无可用的内容）</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">function </span><span class="fun">setDefault</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param"> obj</span><span class="word">.</span><span class="spe">title</span><span class="key"> === </span><span class="str">''</span><span class="key"> || </span><span class="key">typeof </span><span class="param">obj</span><span class="word">.</span><span class="spe">title</span><span class="key"> === </span><span class="str">'undefined'</span><span class="key"> || </span><span class="param">obj</span><span class="word">.</span><span class="spe">title</span><span class="word">.</span><span class="spe">length</span><span class="key"> === </span><span class="word">0</span><span class="word"> )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">obj</span><span class="word">.</span><span class="spe">title</span><span class="key"> = </span><span class="str">'默认标题'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param"> obj</span><span class="word">.</span><span class="spe">desc</span><span class="key"> === </span><span class="str">''</span><span class="key"> || </span><span class="key">typeof </span><span class="param">obj</span><span class="word">.</span><span class="spe">desc</span><span class="key"> === </span><span class="str">'undefined'</span><span class="key"> || </span><span class="param">obj</span><span class="word">.</span><span class="spe">desc</span><span class="word">.</span><span class="spe">length</span><span class="key"> === </span><span class="word">0</span><span class="word"> )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">obj</span><span class="word">.</span><span class="spe">desc</span><span class="key"> = </span><span class="str">'默认描述'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="param"> obj</span><span class="word">.</span><span class="spe">img</span><span class="key"> === </span><span class="str">''</span><span class="key"> || </span><span class="key">typeof </span><span class="param">obj</span><span class="word">.</span><span class="spe">img</span><span class="key"> === </span><span class="str">'undefined'</span><span class="key"> || </span><span class="param">obj</span><span class="word">.</span><span class="spe">img</span><span class="word">.</span><span class="spe">length</span><span class="key"> === </span><span class="word">0</span><span class="word"> )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">obj</span><span class="word">.</span><span class="spe">img</span><span class="key"> = </span><span class="str">'default.png'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">//成功分享计入数据</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">function </span><span class="fun">shareCount</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 分享次数统计，向自己的后台请求，并存下来分享的链接和类型</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">setDefault</span><span class="word">();</span><br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 同步发送请求获取微信的配置</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">.</span><span class="method">ajax</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">type</span><span class="word">:</span><span class="str">'get'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">url</span><span class="word">:</span><span class="str">'获取 sdk 的 url（这是后端写好的）'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="word">:</span><span class="flower">{</span><span class="spe">url</span><span class="word">:</span><span class="spe">window</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">href</span><span class="word">.</span><span class="method">split</span><span class="word">(</span><span class="str">'#'</span><span class="word">)</span><span class="rect">[</span><span class="word">0</span><span class="rect">]</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">dataType</span><span class="word">:</span><span class="str">'jsonp'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">async</span><span class="word">:</span><span class="key">false</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">success</span><span class="word">:</span><span class="key">function </span><span class="word">(data) </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">wx.</span><span class="method">config</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">debug</span><span class="word">:</span><span class="key">false</span><span class="word">,</span><span class="com">// 调试模式,开启后的所有 api 的返回值会在客户端 alert 出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出。</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">appId</span><span class="word">:</span><span class="word">data.</span><span class="spe">appId</span><span class="word">,</span><span class="com"> // 必填，公众号的唯一标识</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">timestamp</span><span class="word">:</span><span class="word">data.</span><span class="spe">timestamp</span><span class="word">,</span><span class="com"> // 必填，生成签名的时间戳</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">signature</span><span class="word">:</span><span class="word">data.</span><span class="spe">signature</span><span class="word">,</span><span class="com"> // 必填，签名</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">nonceStr</span><span class="word">:</span><span class="word">data.</span><span class="spe">nonceStr</span><span class="word">,</span><span class="com"> // 必填，生成签名的随机串</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">jsApiList</span><span class="word">:</span><span class="rect">[</span><span class="str">'onMenuShareTimeline'</span><span class="word">,</span><span class="str">'onMenuShareAppMessage'</span><span class="word">,</span><span class="str">'onMenuShareQQ'</span><span class="word">,</span><span class="str">'onMenuShareWeibo'</span><span class="word">,</span><span class="str">'onMenuShareQZone'</span><span class="word">,</span><span class="rect">]</span><span class="com"> // 必填，需要使用的JS接口列表</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 完成配置后即可调用自定义分享</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">wx.</span><span class="method">ready</span><span class="word">(</span><span class="key">function</span><span class="word"> () </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 分享到朋友圈</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">wx.</span><span class="method">onMenuShareTimeline</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">title</span><span class="word">:</span><span class="param">obj</span><span clas