<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、headers 增加传递参数 token</p>
            <p>
                直接用一个代码示例来说明吧：
            </p>
            <pre class="code"><span class="key">let </span><span class="param">token</span><span class="key"> = </span><span class="str">'获取到的 token 值'</span><span class="word">;</span><br><span class="spe">axios</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">method</span><span class="word">:</span><span class="str">'get'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">url</span><span class="word">:</span><span class="str">'url地址'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">headers</span><span class="word">:</span><span class="flower">{</span><span class="str">'token'</span><span class="word">:</span><span class="param">token</span><span class="flower">}</span><span class="com"> // 在 headers 中传入 token</span><br><span class="flower">}</span><span class="word">).</span><span class="method">then</span><span class="word">( (res)</span><span class="key"> => </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">//success 回调</span><br><span class="flower">}</span><span class="word">, (res)</span><span class="key"> => </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// error 回调</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                说句题外话：<span class="tip">axios</span> 的回调写法也不一定要这么写，可以参照官方分开写成两个，感觉那样结构更清晰一点。
            </p>
            <p class="minititle">二、全局配置 axios 参数</p>
             <p>
                如果每一次请求都要加一句 <span class="code">headers:{'token':token}</span> 那真的是太麻烦了，万一后端说名字不能用 <span class="code">token</span> 了，要换成别的名字。一个大项目很多请求，要么改傻掉，要么就跟后端打架了。作为文明人，当然是有改的好办法的。下面介绍一下全局设置这个 <span class="code">headers</span>。
            </p>
            <p class="minititle">baseURL 配置</p>
            <p>
                开发时候的内网路径和外网路径肯定也不相同，如果上线一次就改一波全部的路径那也要烦死了。<span class="tip">axios</span> 提供了设置 <span class="code">baseURL</span> 的方法。下面是一个简单示例：
            </p>
            <pre class="code"><span class="key">const</span><span class="param"> host</span><span class="key"> = </span><span class="spe">window</span><span class="word">.</span><span class="spe">location</span><span class="word">.</span><span class="spe">host</span><span class="word">;</span><br><span class="key">let</span><span class="param">__baseURL</span><span class="key"> = </span><span class="str">''</span><span class="word">;</span><br><span class="com">/* 假定：</span><br><span class="com">localhost：本地环境</span><br><span class="com">debug：内网环境</span><br><span class="com">debug.xxx.com：内网请求地址</span><br><span class="com">www.xxx.com：外网请求地址</span><br><span class="key">if</span><span class="word">(</span><span class="param"> host</span><span class="word">.</span><span class="method">includes</span><span class="word">(</span><span class="str">'localhost'</span><span class="word">)</span><span class="key"> || </span><span class="param">host</span><span class="word">.</span><span class="method">includes</span><span class="word">(</span><span class="str">'debug'</span><span class="word">)</span><span class="word"> )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">__baseURL</span><span class="key"> = </span><span class="str">'https://debug.xxx.com/'</span><span class="word">;</span><br><span class="flower">}</span><span class="key">else if</span><span class="word">( </span><span class="param">host</span><span class="word">.</span><span class="method">includes</span><span class="word">(</span><span class="str">'www.xxx.com'</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">__baseURL</span><span class="key"> = </span><span class="str">'https://wwww.xxx.com/'</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
                这样一来，就不需要修改请求的 <span class="tip">host</span> 地址了，一劳永逸。
            </p>
            <p>
                上面提到加入 <span class="tip">token</span> 值，也可以配置公共属性。配置好也不需要每一个都改，甚至有了默认值都可以不管了。代码也很简单：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">getToken</span><span class="key"> = </span><span class="word">()</span><span class="key"> => </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 获取 token 的方法 */</span><br><span class="flower">}</span><br><span class="spe">axios</span><span class="word">.</span><span class="spe">defaults</span><span class="word">.</span><span class="spe">headers</span><span class="key"> = </span><span class="flower">{</span><span class="spe">token</span><span class="word">:</span><span class="fun">getToken</span><span class="word">()</span><span class="flower">}</span><span class="word">;</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


