<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">使用感想（题外话）</p>
            <p>
                首先说说这段时间使用 <span class="tip">Vue</span> + <span class="tip">elementUI</span> 开发的感想（不感兴趣请跳过这部分）。
            </p>
            <p>
                总体来说，<span class="tip">elementUI</span> 的组件比较完善，外观也算是比较好看（美中不足的是，<span class="code">table</span> 居然没有合并单元格）。包括时间组件，上传组件，配色都还不错（配色也可以自己定制）。
            </p>
            <p>
                只是为此专门去学习了 <span class="tip">ES6</span>，实际上也没有用到很多 <span class="tip">ES6</span> 的东西，只是它既然用了这个标准（包括 <span class="tip">webpack</span> 里的 <span class="tip">Vue</span> 其实也在用 <span class="tip">ES6</span>），和 <span class="tip">ES5</span> 混用总是觉得别扭，更何况 <span class="tip">ES6</span> 本就该学。
            </p>
            <p>
                刚开始用的时候最难受的莫过于每用一个组件都要去 <span class="tip">API</span> 上找示例了。慢慢熟悉了就好多了，常用的记住了， 不怎么常用的属性再去看看有没有。其实和一开始学 <span class="tip">bootstrap</span> 一样，只不过一个是需要记忆常用的 <span class="code">class</span>，一个是记忆 <span class="code">tagName</span>。我觉得有一个好的 <span class="tip">API</span> 对于一个框架来说非常重要，哈哈。
            </p>
            <p>
                题外话太多了，打住。下面是整体。
            </p>
            <p class="minititle">报错：type check failed for prop "xxx"</p>
            <p>
                举例：验证码限制 <span class="code">input</span> 最多只能输入 6 位，<span class="tip">API</span> 上写着的属性是 <span class="code">maxlength</span>。于是就写了如下代码：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">el-input</span><span class="attr"> v-model=</span><span class="str">"code"</span><span class="attr"> maxlength=</span><span class="str">"6"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">el-input</span><span class="arrow">&gt;</span></pre>
            <p>
                运行后，打开控制台发现报错（但是限制 6 位确实生效了）：
            </p>
            <p class="error">
                [Vue warn]: Invalid prop: type check failed for prop "maxlength". Expected Number, got String.
            </p>
            <p>
                看到这个提示，当然是让它不报错了，不然看着难受得很。
            </p>
            <p>
                料想出了这个问题应该是 <span class="code">maxlength="6"</span> 里面的类型不对，换成了 <span class="code">maxlength=6</span> 依旧不行。没有引号只是 <span class="tip">html</span> 一个不规范的写法，不能改变 6 的类型。
            </p>
            <p>
                这时候就要借助 <span class="tip">Vue</span> 来解决这个问题了。直接上一个代码：
            </p>
             <pre class="code"><span class="arrow">&lt;</span><span class="tag">el-input</span><span class="attr"> v-model=</span><span class="str">"code"</span><span class="attr"> :maxlength=</span><span class="str">"6"</span><span class="arrow">&gt;</span><span class="arrow">&lt;/</span><span class="tag">el-input</span><span class="arrow">&gt;</span></pre>
             <p>
                 没错，只要加一个小小的冒号就可以了。这样不仅可以直接写 6 ，也可以通过 <span class="code">data</span> 传一个参数进来。
             </p>
             <p>
                 基本上，使用 <span class="tip">elementUI</span> 的时候，标签里面的属性有很多是需要用 <span class="tip">Vue</span> 去绑定的。而属性里面的 <span class="code">Number</span> 类型就都需要这样处理了。
             </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


