<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>

            </p>
            <pre class="code"><span class="com">// 定义路由 （routeConfig 事先已定义好）</span><br><span class="key">const </span><span class="param">router</span><span class="key"> = new </span><span class="spe">VueRouter</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">routes</span><span class="word">: </span><span class="param">routeConfig</span><br><span class="flower">}</span><span class="word">);</span><br><span class="com">// 跳转前做判断</span><br><span class="param">router</span><span class="word">.</span><span class="method">beforeEach</span><span class="word">((to, from, next) </span><span class="key">=> </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( to.</span><span class="spe">matched</span><span class="word">.</span><span class="spe">length</span><span class="key"> === </span><span class="word">0 )</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">from.</span><span class="spe">name</span><span class="key"> ? </span><span class="word">next(</span><span class="flower">{ </span><span class="spe">name</span><span class="word">:</span><span class="word">from.</span><span class="spe">name</span><span class="flower"> }</span><span class="word">)</span><span class="key"> : </span><span class="word">next(</span><span class="str">'/'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">} </span><span class="key">else </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="word">next();</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                上面的效果是：如果地址在路由中，未定义则返回上一个输入的地址，如果也没有，就返回首页。
            </p>
            <p>
                如果希望在无页面的情况下，跳转到 <span class="tip">404 页面</span>。只需要事先配置好 <span class="tip">404 页面</span> 的路由。然后在 <span class="code">to.matched.length === 0</span> 为 <span class="code">true</span> 时，跳转过去即可。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


