<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                当在浏览器中输入一段 <span class="tip">url</span> 后，流程大致如下：
            </p>
            <p class="minititle">1、加载</p>
            <p>
               根据请求的 <span class="tip">url</span> 进行域名解析，向服务器发起请求，接收文件。（<span class="tip">html</span>、<span class="tip">js</span>、<span class="tip">css</span>、<span class="tip">image</span> 等）
            </p>
            <p class="minititle">2、解析</p>
            <p>
                对加载到的资源（<span class="code">html</span>、<span class="tip">js</span>、<span class="tip">css</span> 等）进行语法解析，建立相应的内部数据结构。（比如 <span class="tip">html</span> 的 <span class="tip">DOM</span> 树；<span class="tip">js</span> 的（对象）属性表、<span class="tip">css</span> 的样式规则等）
            </p>
            <p class="minititle">3、渲染</p>
            <p>
               构建渲染树，对各个元素进行位置计算、样式计算等等，然后根据渲染树对页面进行渲染（可以理解为画元素）
            </p>
            <p>
                渲染流程基本是：<span class="code">layout</span> 定位坐标和大小，是否换行，各种 <span class="code">position</span>、<span class="code">overflow</span>，以及 <span class="code">z-index</span> 等属性，然后开画。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


