<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                由于这个原因，如果有这样的需求：在输入中要替换输入的字符。那么如果使用字符串拼接或者 <span class="code">replace()</span> 方法，都会出问题。
            </p>
            <p>
                使用字符串拼接会拼接三次，出现三个替换掉的内容；如果是在中间部分输入就更加复杂了（眼花）。
            </p>
            <p>
               使用 <span class="code">replace()</span> 虽然也会多次触发，但是后面的触发理论上讲虽然多余，却不会影响到结果。然而，实际上并非如此，它会使得输入的内容莫名变为重复多次输入的样子（不知道现在这个情况是否还存在，<span class="tip">2017-7-25</span> 更新过，仍然有效）。可使用下例测试（下例将输入的 <span class="tip">“a”</span> 转换为 <span class="tip">“”</span>，输入 <span class="tip">“a”</span> 后不会直接替换，输入了别的字符时，将清除所有的 <span class="tip">“a”</span>），切换到微软输入法 <span class="tip">“a”</span> 和其它任意字符随便输，马上就能看到效果：
            </p>
            <el-input placeholder="替换输入的a" @input="input_25($event)" v-model="param_25"></el-input>    
            <p>
                综上，最好尽量避免这种用户输入过程中去改变用户输入内容的需求，毕竟用户使用的输入法是不可控的。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


