<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                使用 <span class="code">focus()</span> 方法即可。
            </p>
            <pre class="code"><span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'element'</span><span class="word">).</span><span class="method">focus</span><span class="word">();</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


