<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先介绍一下 <span class="code">each()</span> 函数的两种基本用法（其中，<span class="code">i</span> 为当前元素的下标，<span class="code">e</span> 为对应的 <span class="tip">DOM</span> 元素）：
            </p>
            <pre class="code"><span class="spe">$</span><span class="word">.</span><span class="method">each</span><span class="word">(</span><span class="str">'element'</span><span class="word">,</span><span class="key">function</span><span class="word">(i,e)</span><span class="flower">{</span><span class="word"> ... </span><span class="flower">}</span><span class="word">);</span><br><span class="spe">$</span><span class="word">(</span><span class="str">'element'</span><span class="word">).</span><span class="method">each</span><span class="word">(</span><span class="key">function</span><span class="word">(i,e)</span><span class="flower">{</span><span class="word"> ... </span><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                现在这么一个需求：在找到符合条件的值之后，就结束查找，不再继续执行循环。这时，使用 <span class="code">break</span> 和 <span class="code">continue</span> 都只能跳出本次的循环，因为这个操作都是对于 <span class="code">for</span> 循环的，而<span class="code">each()</span> 是一个函数。
            </p>
            <p>
                <span class="code">each()</span> 函数中的使用方法如下：
            </p>
            <p>
                <span class="code">break</span> 等于 <span class="code">return false</span>，效果是 跳出所有循环；
            </p>
            <p>
                <span class="code">continue</span> 等于 <span class="code">return true</span>，效果是 跳出当前（本次）循环，进入下一循环。
            </p>
            <p>
                下面是一个使用的例子：
            </p>
            <pre class="code"><span class="key">function</span> <span class="fun">test</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">var</span> <span class="param">temp</span> <span class="key">=</span> <span class="str">'not found'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">$</span><span class="word">(</span><span class="str">'element'</span><span class="word">).</span><span class="method">each</span><span class="word">(</span><span class="key">function</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">(</span><span class="spe">$</span><span class="word">(e).</span><span class="method">text</span><span class="word">()</span><span class="key"></span><span class="key"> === </span><span class="str">'1'</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">temp</span> <span class="key">=</span> <span class="str">'found'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return</span> <span class="key">false</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return</span> <span class="param">temp</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
              上例中，一旦找到内容为 '1' 的元素就会停止后面的循环，减少循环的消耗。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


