<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                目前遇到使用 <span class="code">hashchange</span> 函数的场景并不是很多。使用它可以给 <span class="tip">url</span> 地址后面加上哈希值，如：<span class="tip">localhost:1234/index.html => localhost:1234/index.html#hash</span>。这样可以利用这个方法，调用浏览器自带的返回功能，实现同一个页面覆盖层的关闭。
            </p>
            <p>
                给 <span class="code">window</span> 对象绑定 <span class="code">hashchange</span> 事件后，即可在回调中自定义方法，添加 <span class="code">hash</span> ，回调函数中有 <span class="code">e</span> 参数，可以使用其中的属性：<span class="code">e.oldURL</span> 和 <span class="code">e.newURL</span>
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


