<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先，<span class="code">this</span>  的使用范围很广泛，指向的是调用的对象。与 <span class="code">event.target</span> 的对比主要是一些事件（如 <span class="code">click</span>、<span class="code">keyup</span>、<span class="code">hover</span> 等）。
            </p>
            <p>
               <span class="code">event.target</span> 指向的是接收事件的 <span class="tip">DOM</span> 元素本身，而 <span class="code">this</span> 是会冒泡的。使用  <span class="code">event.target</span> 还是 <span class="code">this</span> 要根据情况而定。如果明确要的是触发事件的 <span class="tip">DOM</span> 元素本身，那使用 <span class="code">event.target</span> 效果应该会更好，也更简便。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


